import {actionTypes} from '@servicenow/ui-core';
import { t } from 'sn-translate';
import {ACTIONS} from './constants';
import {fetchConditionRecord} from './dataSource';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const deleteCondition = ({ action, state, updateState, dispatch }) => {
  let tempCondition = [];
  for (var i=0;i<state.condition.length;i++){
    if (action.payload.id != state.condition[i].counter) {
      tempCondition.push(state.condition[i]);
    }
  }
  if (tempCondition.length==0){
    updateState({showEmptyCondition: true});
  }
  updateState({condition: tempCondition});
  dispatch("CONDITION_CHANGED", {"condition": tempCondition});
}

const updateConditionObject = ({ action, state, updateState, dispatch }) => {
  let k = action.payload.currentElement;
  let tempCond = state.condition;
  for (var i=0;i<tempCond.length;i++){
    if (k == tempCond[i].counter) {
      tempCond[i].value = action.payload.currentValue ? action.payload.currentValue : '';
      tempCond[i].displayValue = action.payload.currentDisplayValue ? action.payload.currentDisplayValue : '';
    }
  }
  updateState({condition: tempCond});
  dispatch("CONDITION_CHANGED", {"condition": tempCond});
}

const addMoreCondition = ({ action, state, updateState }) => {
  initializeConditionObject({state, updateState});
}

const initializeConditionObject = ({ state, updateState }) => {
  let tempCond = state.condition ? state.condition : [];
  tempCond.push({
    counter: state.count,
    value: '',
    displayValue: ''
  });
  updateState({condition: tempCond});
}

const fetchConditionsDisplayValue = ({ state, updateState, dispatch }) => {
  updateState({condition : [], count : 0});
  if (state.properties.conditions) {
    updateState({ tempCondDisplayValue : [], showEmptyCondition: false });
    let conditionString = state.properties.conditions.split(',');
    for (var i=0; i<conditionString.length; i++){
      if (conditionString[i])
        dispatch(ACTIONS.FETCH_CONDITION, {tableName: 'sn_sro_cnd', sysId: conditionString[i]});
    }
  }
  else {
    updateState({showEmptyCondition: true});
  }
}

export const arConditionHandlers = {
  [COMPONENT_PROPERTY_CHANGED]: ({action, dispatch, state, updateState, updateProperties}) => {
    if (Object.keys(action.payload.previousValue).length == 0 && action.payload.value.length > 0 && state.properties.isNewAlertRule == false) {
      fetchConditionsDisplayValue({state, updateState, dispatch});
    }
    if(action.payload.name == 'refresh' && action.payload.previousValue == false && action.payload.value == true) {
      fetchConditionsDisplayValue({state, updateState, dispatch});
      updateProperties({refresh: false});
      dispatch("SRO_AR_CONDITIONS#CONDITION_REFRESHED");
    }
  },
  [ACTIONS.DELETE_CLICKED]: deleteCondition,
  [ACTIONS.CONDITION_UPDATED]: updateConditionObject,
  [ACTIONS.ADD_CLICKED]: addMoreCondition,
  [ACTIONS.REFERENCE_INFO_CLICK]: ({action, dispatch}) => {
    dispatch("SRO_AR_CONDITIONS#REFERENCE_ICON_CLICKED", {"sysId": action.payload.referenceInfoClick.sys_id});
  },
  [ACTIONS.FETCH_CONDITION]: fetchConditionRecord,
  [ACTIONS.FETCH_CONDITION_SUCCESS]: ({action, state, updateState, dispatch}) => {
    let x = state.tempCondDisplayValue;
    x.push(action.payload.result.name);
    updateState({tempCondDisplayValue : x});
    let conditionString = state.properties.conditions.split(',');
    if (x.length == conditionString.length)
      dispatch(ACTIONS.UPDATE_EXISTING_CONDITIONS);
  },
  [ACTIONS.UPDATE_EXISTING_CONDITIONS]: ({action, state, updateState}) => {
    let conditionString = state.properties.conditions.split(',');
    let tempCond = state.condition ? state.condition : [];
    let displayValues = state.tempCondDisplayValue;
    for (var i=0; i<conditionString.length; i++){
      tempCond.push({
        counter: i,
        value: conditionString[i],
        displayValue: displayValues[i]
      });
    }
    updateState({condition: tempCond, count: i});
  }
}
