import { ACTIONS } from './constants';
import { actionTypes } from '@servicenow/ui-core';
import {updateTeamEffect, getTeamDetailsEffect, getTeamAccessEffect, recordWatcherForTeamUpdateRequest} from './dataSource';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;
const teamUpdateReqFilterQuery = 'status=updateComplete^team=';

export const actionHandlers = {
    [COMPONENT_BOOTSTRAPPED]: ({state, dispatch, updateState}) => {
        dispatch(ACTIONS.FETCH_UPDATE_TEAM_ACCESS_REQUESTED,{
            teamId: state.properties.sysId
        });
        dispatch(ACTIONS.FETCH_TEAM_DETAILS_REQUESTED, {
            teamId: state.properties.sysId,
            getAllMemberDetails: 'false'
        });
    },
    [ACTIONS.TEAM_TAB_SELECTED]: ({ action, updateState }) => {
        updateState({
            selectedTab: action.payload.value
        });
    },
    [ACTIONS.FETCH_UPDATE_TEAM_ACCESS_REQUESTED]: getTeamAccessEffect,
    [ACTIONS.TEAM_ACCESS_DETAILS_FETCHED]: ({action, updateState}) => {
        updateState({
            canEditTeam: action.payload.canEdit
        }); 
    },
    [ACTIONS.FETCH_TEAM_DETAILS_REQUESTED]: getTeamDetailsEffect,
    [ACTIONS.FETCH_TEAM_DETAILS_SUCCEEDED]: ({action, updateState, updateProperties}) => {
        var result = action.payload.result;
        updateState({
            'nameValue': result.name,
            'managerValue': result.manager.sys_id,
            'managerDisplayValue': result.manager.name,
            'parentValue': result.parent.sys_id,
            'parentDisplayValue': result.parent.name,
            'emailValue': result.email,
            'descValue': result.description,
            'loading': false
        });

        updateProperties({
            'teamName': result.name
        });
    },
    [ACTIONS.SAVE_CLICKED]: ({state, updateState, dispatch}) => {
        dispatch(ACTIONS.UPDATE_SUBMITTED, {
            data: {
            'sysId': state.properties.sysId,
            'name': (state.nameValue ? state.nameValue + '' : ''),
            'parent': (state.parentValue ? state.parentValue + '' : ''),
            'manager': (state.managerValue ? state.managerValue + '' : ''),
            'email': (state.emailValue ? state.emailValue + '' : ''),
            'description': (state.descValue ? state.descValue + '' : '')
        }
        });
        updateState({
            loading: true
        });
    },
    [ACTIONS.UPDATE_SUBMITTED]: updateTeamEffect,
    [ACTIONS.UPDATE_FINISHED]: ({state, dispatch, updateState, updateProperties}) => {  
        dispatch(ACTIONS.FETCH_UPDATE_TEAM_ACCESS_REQUESTED,{
            teamId: state.properties.sysId
        });      
        dispatch(ACTIONS.FETCH_TEAM_DETAILS_REQUESTED, {
            teamId: state.properties.sysId
        });
        updateState({
            loading: false
        });
    },
    [ACTIONS.UPDATE_FAILED]: ({dispatch, updateState, updateProperties}) => {
        updateState({
            loading: false
        });
    },
    ['SRO_EDIT_TEAM_RL#CANCEL_CLICKED']: ({
        updateState
    }) => {
        updateState({
            manageTeamRlOpen: false
        });
    },
    ['NOW_MODAL#OPENED_SET']: ({
        updateState
    }) => {
        updateState({
            manageTeamRlOpen: false
        });
    },
    ['SRO_EDIT_TEAM_RL#UPDATE_FINISHED']: ({
        state, action, updateState, dispatch
    }) => {
        if (action.payload.result.status == 'TeamUpdatedAsync') {
            var teamUpdateReqFilter = getEncodedFilterString(teamUpdateReqFilterQuery + state.properties.sysId);
            dispatch('TEAM_UPDATE_REQUEST_RW_SUBSCRIBED', {
                filter: teamUpdateReqFilter,
                subscribe: true,
            });
            updateState({
                updateMembersPrecessing: true,
                manageTeamRlOpen: false
            });         
        }
        else
            updateState({
                manageTeamRlOpen: false,
                refreshPayload: { timestamp: Date.now(), background: false, loaderDuration: 0 }
            });
    },
	['SRO_EDIT_TEAM_RL#UPDATE_FAILED']: ({
        updateState
    }) => {
        updateState({
            manageTeamRlOpen: false
        });
    },
    [ACTIONS.MANAGE_SERVICES_CLICKED]: ({
        action, updateState
    }) => {
        updateState({
            selectedItem: 'services',
            manageTeamRlOpen: true
        });
    },
    [ACTIONS.MANAGE_MEMBERS_CLICKED]: ({
        action, updateState
    }) => {
        updateState({
            selectedItem: 'members',
            manageTeamRlOpen: true
        });
    },
    'NOW_ALERT#ACTION_CLICKED': ({ action, updateState }) => {
        if (action.payload.action.type === 'dismiss') {
            updateState({
                updateMembersPrecessing: false
            })
        }
    },
    'TEAM_UPDATE_REQUEST_RW_SUBSCRIBED': recordWatcherForTeamUpdateRequest,
    [ACTIONS.UPDATE_TEAM_FINISHED]: ({
        state, updateState, dispatch
    }) => {
        updateState({
            refreshPayload: { timestamp: Date.now(), background: false, loaderDuration: 0 },
            updateMembersPrecessing: false
        });
        var teamUpdateReqFilter = getEncodedFilterString(teamUpdateReqFilterQuery + state.properties.sysId);
        dispatch('TEAM_UPDATE_REQUEST_RW_SUBSCRIBED', {
            filter: teamUpdateReqFilter,
            subscribe: false,
        });
    }
}

function getEncodedFilterString(filter) {
    if (typeof filter != 'string')
        return '';
    filter = filter.
        replace(/\^EQ/g, '').
        replace(/\^ORDERBY(?:DESC)?[^^]*/g, '').
        replace(/^GOTO/, '');
    return btoa(filter).replace(/=/g, '-');
}
