import {
	createHttpEffect
} from '@servicenow/ui-effect-http';
import {
	ACTIONS
} from "./constants";


export const updateTeamMembersEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/updateTeam", {
	method: 'POST',
	dataParam: 'data',
	startActionType: ACTIONS.UPDATE_STARTED,
	successActionType: ACTIONS.UPDATE_FINISHED,
	errorActionType: ACTIONS.UPDATE_FAILED
});

export const updateTeamServicesEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/update_services_for_team", {
	method: 'POST',
	dataParam: 'data',
	startActionType: ACTIONS.UPDATE_STARTED,
	successActionType: ACTIONS.UPDATE_FINISHED,
	errorActionType: ACTIONS.UPDATE_FAILED
});

export const getTeamDetailsEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/team_details/sys_user_group/:teamId", {
	method: 'GET',
	pathParams: ['teamId'],
	successActionType: ACTIONS.FETCH_TEAM_DETAILS_SUCCEEDED,
	errorActionType: ACTIONS.FETCH_TEAM_DETAILS_FAILED
});

export const getTeamServicesEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/services_for_team/:teamId", {
	method: 'GET',
	pathParams: ['teamId'],
	successActionType: ACTIONS.FETCH_TEAM_SERVICES_SUCCEEDED,
	errorActionType: ACTIONS.FETCH_TEAM_SERVICES_FAILED
});