import {
	actionTypes
} from '@servicenow/ui-core';
import {
	updateTeamMembersEffect,
	updateTeamServicesEffect,
	getTeamDetailsEffect,
	getTeamServicesEffect
} from './dataSource';
import {
	ACTIONS
} from "./constants";


const {
	COMPONENT_BOOTSTRAPPED
} = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({
		state,
		dispatch,
		updateState
	}) => {
		if (state.properties.relatedList === 'members')
			dispatch(ACTIONS.FETCH_TEAM_DETAILS_REQUESTED, {
				teamId: state.properties.teamId
			});
		else
			dispatch(ACTIONS.FETCH_TEAM_SERVICES_REQUESTED, {
				teamId: state.properties.teamId
			});
	},
	[ACTIONS.FETCH_TEAM_DETAILS_REQUESTED]: getTeamDetailsEffect,
	[ACTIONS.FETCH_TEAM_DETAILS_SUCCEEDED]: ({
		action,
		updateState
	}) => {
		var result = action.payload.result;

		let members = result.members;
		let memberNames = [];
		let memberIds = [];
		members.forEach(element => {
			memberNames.push(element.name);
			memberIds.push(element.sys_id);
		});

		updateState({
			'recordsValue': memberIds.join(','),
			'recordsDisplayValue': memberNames.join(','),
		});
	},
	[ACTIONS.FETCH_TEAM_SERVICES_REQUESTED]: getTeamServicesEffect,
	[ACTIONS.FETCH_TEAM_SERVICES_SUCCEEDED]: ({
		action,
		updateState
	}) => {
		var result = action.payload.result;

		let services = result.services;
		let serviceNames = [];
		let serviceIds = [];
		services.forEach(element => {
			serviceNames.push(element.name);
			serviceIds.push(element.sys_id);
		});

		updateState({
			'recordsValue': serviceIds.join(','),
			'recordsDisplayValue': serviceNames.join(','),
		});
	},
	[ACTIONS.CANCEL_CLICKED]: ({
		updateProperties
	}) => {
		updateProperties({
			opened: false
		})
	},
	[ACTIONS.SAVE_CLICKED]: ({
		state,
		dispatch
	}) => {
		if (state.properties.relatedList === 'members') {
			dispatch(ACTIONS.UPDATE_MEMBERS_SUBMITTED, {
				data: {
					'sysId': state.properties.teamId,
					'active': true,
					'members': (state.recordsValue ? state.recordsValue : ''),
					'updateMode': 'members',
				}
			});
		} else {
			dispatch(ACTIONS.UPDATE_SERVICES_SUBMITTED, {
				data: {
					'teamId': state.properties.teamId,
					'services': (state.recordsValue ? state.recordsValue : '')
				}
			});
		}
	},
	[ACTIONS.UPDATE_MEMBERS_SUBMITTED]: updateTeamMembersEffect,
	[ACTIONS.UPDATE_SERVICES_SUBMITTED]: updateTeamServicesEffect,
	[ACTIONS.UPDATE_STARTED]: ({
		updateState
	}) => {
		updateState({
			loading: true
		});
	},
	[ACTIONS.UPDATE_FINISHED]: ({
		state,
		dispatch,
		updateState,
		updateProperties
	}) => {
		dispatch(ACTIONS.TEAM_UPDATED, {
			name: state.nameValue
		});
		updateState({
			loading: false
		});
		updateProperties({
			opened: false
		});
	},
	[ACTIONS.UPDATE_FAILED]: ({
		dispatch,
		updateProperties
	}) => {
		updateProperties({
			opened: false
		});
		dispatch(ACTIONS.TEAM_UPDATE_FAILED);
	}
};

export default actionHandlers;