import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-card';
import '@servicenow/now-tooltip';
import '@servicenow/now-icon';
import '@servicenow/now-avatar';
import '@servicenow/now-heading';
import '@servicenow/now-highlighted-value';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import '@servicenow/now-modal';
import '@servicenow/now-button';
import { t } from 'sn-translate';
import styles from './styles.scss';

const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED} = actionTypes;

const view = (state, {dispatch, helpers}) =>  {
	let cardActions = [];
	if (state.canViewOC)
		cardActions.push({id: "view-oc-cal", label: t("View On-Call Calendar")});
	if (state.canCreateOCSchedule)
		cardActions.push({id: "create-oc-sch", label: t("Create On-Call Schedule")});
	if (state.canDeactivateTeam)
		cardActions.push({id: "deactivate-action", label: t("Deactivate")});

	let moreActionsObj = [
        {
            id: "dropdown-actions",
            items: cardActions,
            icon: "ellipsis-v-outline",
            label: t("More actions"),
            variant: "primary",
        },
	];

	return (
		<div className="main-container">
			<now-card size="lg" interaction={state.properties.table === 'sys_user_group' ? 'click' : 'none'} append-to-payload={{teamId: state.properties.teamId, type: 'team-card'}} config-aria={{'aria-label': t(state.properties.name)}}>
				<div className='card-header'>
					<now-card-header heading={{label: t(state.properties.name), size: 'sm', level: 5}} dropdowns= {(state.properties.table === 'sys_user_group' && cardActions.length > 0)? moreActionsObj : ''}>
						<div className="team-type" slot="metadata">
							{renderTeamTypes(state)}
						</div>
					</now-card-header>
				</div>
				<hr/>
				{renderTeamsCardContent(state, helpers)}
			</now-card>
			<div>
				<now-modal size="sm" opened={state.showDeactivateConfirmation} headerLabel={t('Deactivate Team?')}>
				 {renderDeactivateConfirmationModal(state, dispatch)}
				 </now-modal>
			</div>
		</div>
	);
};

const renderTeamTypes = function (state) {
	if (state.properties.table === 'sn_sro_team_request')
		return <now-highlighted-value label={t('Pending approval')} icon="" status="warning"></now-highlighted-value>;
	if (!state.type[0] || state.properties.mode === 'compact')
		return;

	return <now-highlighted-value label={state.type[0]} icon="" status="info"></now-highlighted-value>;
}

const renderTeamsCardContent = function(state, helpers) {
		return (
			<div className='team-card-container'>
				<div className="team-details">
					{displayManagerDetails(state)}
				</div>
				<div className="team-details-oc">
					{displayOnCallMemberDetails(state)}
				</div>
				<div className="team-members">
					{displayMembers(state)}
				</div>
			</div>
		);
}


const MAX_MEMBERS_TO_SHOW = 6;

function displayMembers(state) {
	let members = state.members;
	let memberCount = state.memberCount;
	let content;
    if(!members || members.length === 0)
        content = (
			<div className="user-profile">
				<div aria-labelledby={"memberLabel" + state.properties.teamId}>
					<span className="user-name">{t('This team has no members')}</span>
				</div>
			</div>
		);
	else {
		let groupMembers = [];
		for (let i=0; i < members.length; i++) {
			let memberAvatarUrl;
			if (members[i].avatar)
				 memberAvatarUrl = `/${members[i].avatar}.iix?t=small`;
			groupMembers.push(
				<span role="img" aria-label={members[i].name}>
					<now-avatar
						userName={members[i].name}
						imageSrc={memberAvatarUrl}
						size="sm"/>
				</span>
			);
		}
		if (memberCount > MAX_MEMBERS_TO_SHOW) {
			const balanceCount = memberCount - MAX_MEMBERS_TO_SHOW;
			const showMore = t(" + {0} more", balanceCount);
			groupMembers.push(
				<span className='show-more'>
					{showMore}
				</span>
			);
		}
		content = (
			<div className={state.properties.mode === 'compact' ? 'members-compact': 'members'} aria-labelledby={"memberLabel" + state.properties.teamId}>
				{groupMembers}
			</div>
		);
	}
	return (
			<div>
				<span className="team-detail-label" id={"memberLabel" + state.properties.teamId}>
					{t('Members')}
				</span>
				{content}
			</div>
	);
}

function displayManagerDetails(state) {
	let content;
	if (!state.manager || !state.manager.sys_id) {
		content = (
			<div className="user-profile">
				<div>
					<span className="user-name" aria-labelledby={"managerLabel" + state.properties.teamId}>{t('No Manager assigned')}</span>
				</div>
			</div>
		);
	}
	else {
		const managerAvatarUrl = (state.manager && state.manager.avatar) ? `/${state.manager.avatar}.iix?t=small` : '';
		content = (
			<div className="user-profile" aria-labelledby={"managerLabel" + state.properties.teamId}>
				<div className="user-avatar" role="img" aria-label={state.manager.name}>
					<now-avatar userName={state.manager.name} imageSrc={managerAvatarUrl} size="md"/>
				</div>
				<div className={state.properties.mode === 'compact'? 'user-info-compact': 'user-info'}>
					<span className='user-name'>{state.manager.name}</span>
					<span className="user-role">{state.manager.title}</span>
				</div>
			</div>
		);
	}
	return (
		<div>
			<span className="team-detail-label" id={"managerLabel" + state.properties.teamId}>
				{t('Manager')}
			</span>
			{content}
		</div>
	);
}

function displayOnCallMemberDetails(state) {
	if (!state.canViewOC || state.properties.table === 'sn_sro_team_request')
		return;
	var content;
	if (!state.onCallMembers || state.onCallMembers.length === 0) {
		content = (
			<div className="user-profile">
				<div>
					<span className="user-name">{t('No one is on-call now')}</span>
				</div>
			</div>
		);
	}
	else {
		let onCallData = [];
		for (let i = 0; i < state.onCallMembers.length; i++) {
			const onCallMemberAvatarUrl = state.onCallMembers[i].avatar ? `/${state.onCallMembers[i].avatar}.iix?t=small` : '';
			onCallData.push(
				<div className="user-profile">
					<div className="user-avatar" role="img" aria-label={state.onCallMembers[i].name}>
						<now-avatar userName={state.onCallMembers[i].name} imageSrc={onCallMemberAvatarUrl} size="md"/>
					</div>
					<div className={state.properties.mode === 'compact'? 'user-info-compact': 'user-info'}>
						<span className='user-name'>{state.onCallMembers[i].name}</span>
						<span className="user-role">{state.onCallMembers[i].order === 1 ? t("Primary") : t("Secondary")}</span>
					</div>
					<br/>
				</div>
			);
		}
		content = onCallData;
	}
	

	if (state.isOnCallActive == true)
		return (
			<div>
				<span className="team-detail-label" id={"on-call" + state.properties.teamId}>
					{t('On-call now')}
				</span>
				<div aria-labelledby={"on-call" + state.properties.teamId}>
					{content}
				</div>
			</div>
		);
}

const fetchTeamDetailsEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/team_details/:table/:team_id", {
	method: 'GET',
	pathParams: ['table', 'team_id'],
	queryParams: ['getAllMemberDetails'],
	successActionType: 'TEAM_DETAILS_FETCHED'
});

const deactivateTeamEffect =  createHttpEffect("api/sn_sro/sr_ops_workflow/updateTeam", {
	method: 'POST',
	dataParam: 'data',
	successActionType: 'DEACTIVATE_TEAM_SUCCESS',
	errorActionType: 'DEACTIVATE_TEAM_ERROR'
});

const fetchUpdateTeamAccessEffect =  createHttpEffect("api/sn_sro/sr_ops_workflow/getUpdateTeamAccess/:teamId", {
	method: 'GET',
	pathParams: ['teamId'],
	successActionType: 'FETCH_UPDATE_TEAM_ACCESS_SUCCESS',
	errorActionType: 'FETCH_UPDATE_TEAM_ACCESS_ERROR'
});

function renderDeactivateConfirmationModal(state, dispatch) {
	let label = t('Are you sure you want to deactivate {0}?',state.properties.name)
	return (
		<div className="modal-deactivate">
			<div class="modal-label">
				{label}
			</div>
			<div className="button-row">
				<div className="cancel-deactivate">
					<div className="cancel-deactivate-btn">
						<now-button label={t("Cancel")} variant="secondary" size="md" configAria={{}}
							on-click={() => dispatch('MODAL_CANCEL_BTN_CLICKED')}
						></now-button>
					</div>
					<div className="cancel-deactivate-btn">
						<now-button label={t("Deactivate")} variant="primary-negative" size="md" configAria={{}}
							on-click={() => dispatch('MODAL_DEACTIVATE_BTN_CLICKED')}
						></now-button>
					</div>
				</div>
			</div>
		</div>
	)
}

createCustomElement('sn-sr-ops-team-card', {
	renderer: {type: snabbdom},
	properties : {
		teamId: {},
		name: {schema: {type: 'string'}},
		table: {},
		mode: {
			default: 'compact'
		},
		refresh: {
			default: false
		}
    },
	view,
	styles,
	initialState: {
		'type': [],
		showDeactivateConfirmation: false
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({state, dispatch, updateProperties}) => {
			dispatch('FETCH_TEAM_DETAILS', {
				'team_id': state.properties.teamId + '',
				'table': state.properties.table + '',
				'getAllMemberDetails': 'false'
			});
			dispatch('FETCH_UPDATE_TEAM_ACCESS', {teamId: state.properties.teamId});
			updateProperties({
				'refresh': false
			});
		},
		[COMPONENT_PROPERTY_CHANGED]: ({state, updateProperties, dispatch, action}) => {
			if (action.payload.name === 'refresh' && action.payload.value) {
				dispatch('FETCH_TEAM_DETAILS', {
					'team_id': state.properties.teamId + '',
					'table': state.properties.table + ''
				});
				dispatch('FETCH_UPDATE_TEAM_ACCESS', {teamId: state.properties.teamId});
			}
		},
		'FETCH_TEAM_DETAILS': fetchTeamDetailsEffect,
		'TEAM_DETAILS_FETCHED': ({action, updateState, updateProperties}) => {
			var result = action.payload.result;
			updateState({
				'type': result.type,
				'manager': result.manager,
				'members': result.members,
				'memberCount': result.memberCount,
				'onCallMembers': result['on_call_users'],
				'isOnCallActive': result.is_on_call_active
			});
			updateProperties({
				'refresh': false
			});
		},
		'NOW_CARD#CLICKED': ({state, action, dispatch}) => {
			if (action.payload.type === "team-card") {
				dispatch('PREVIEW_RECORD', {
					table: "sys_user_group",
					sys_id: state.properties.teamId
				});
			}
		},
		'NOW_CARD_HEADER#ACTION_CLICKED': ({state, action, updateState}) => {
			const actionId = action.payload.action.id;
			if (actionId == 'deactivate-action') {
				updateState({
					showDeactivateConfirmation: true
				});
			}
			if (actionId === 'view-oc-cal') {
				let link = "$oc.do?sysparm_timeline_enabled=true&sysparm_current_view=weekly&sysparm_group_id=" + state.properties.teamId;
				window.open(link, '_blank');
			}
			if (actionId === 'create-oc-sch') {
				let link = "$oc_creation_wizard.do?sysparm_group_id=" + state.properties.teamId + "&sysparm_panel=define_schedule";
				window.open(link, '_blank');
			}
		},
		'MODAL_CANCEL_BTN_CLICKED': ({updateState}) => {
			updateState({
				showDeactivateConfirmation: false
			});
		},
		'MODAL_DEACTIVATE_BTN_CLICKED': ({state, dispatch, updateProperties}) => {
			let data = {
				sysId : state.properties.teamId,
				active: "false"
			};
			dispatch('DEACTIVATE_TEAM', {data: data});
			updateProperties({
				showDeleteConfirmation: false
			});
		},
		'DEACTIVATE_TEAM': deactivateTeamEffect,
		'DEACTIVATE_TEAM_SUCCESS': ({state, updateState, dispatch}) => {
			updateState({
				showDeactivateConfirmation: false
			});
			dispatch('TEAM_DEACTIVATED', {
				name: state.properties.name
			});
		},
		'DEACTIVATE_TEAM_ERROR': ({updateState}) => {
			updateState({
				showDeactivateConfirmation: false
			});
			dispatch('TEAM_DEACTIVATION_FAILED');
		},
		'FETCH_UPDATE_TEAM_ACCESS': fetchUpdateTeamAccessEffect,
		'FETCH_UPDATE_TEAM_ACCESS_SUCCESS': ({action, updateState}) => {
			updateState({
				canUpdateTeam: action.payload.canEdit,
				canDeactivateTeam: action.payload.canDeactivate,
				canViewOC: action.payload.canViewOC,
				canCreateOCSchedule: action.payload.canCreateOCSchedule
			});
		},
		'FETCH_UPDATE_TEAM_ACCESS_ERROR': () => {
			console.log("Failure fetching update team access for logged in user");
		},	
		'NOW_MODAL#OPENED_SET': ({updateState}) => {
			updateState({
				showDeactivateConfirmation: false
			});
		}
	}
});

export default 'sn-sr-ops-team-card';