import {createHttpEffect} from '@servicenow/ui-effect-http';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import { ACTIONS } from "./constants";


const userDetailsQuery = `{
	GlideDomain_Query {
	   user {
		  sys_id
		  fullName
		}
	}
}`;

export const fetchUserDetails = createGraphQLEffect(
	userDetailsQuery, {
	successActionType: ACTIONS.FETCH_USER_DETAILS_SUCCESS
});

export const createTeamEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/createNewTeam", {
	method: 'POST',
	dataParam: 'data',
	startActionType: ACTIONS.CREATION_STARTED,
	successActionType: ACTIONS.CREATION_FINISHED,
	errorActionType: ACTIONS.CREATION_FAILED
});