import {createCustomElement} from '@servicenow/ui-core';

import {actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { fetchProperties } from './dataSource';
import {ACTIONS} from './constants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;
const view = (state, {updateState, dispatch}) => {
	return <div>

	</div>
};

createCustomElement('sn-sr-ops-create-relationship', {
	renderer: {type: snabbdom},
	initialState: {

	},
	properties: {
		parentRecordSysId: {}
	},
	view,
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({state, dispatch, updateState}) => {
			dispatch(ACTIONS.FETCH_PROPERTIES);
		},
		[ACTIONS.FETCH_PROPERTIES]: fetchProperties,
		[ACTIONS.FETCH_PROPERTIES_SUCCESS]: ({state, dispatch, action}) => {
			let id = action.payload.data.GlideRecord_Query.sys_properties._results[0].value.value;
			let newRelationshipParams = {
				table: 'sc_cat_item',
				row: {
					sys_id: {
						value: id
					}
				},
				params: {
					sysparmService: state.properties.parentRecordSysId
				}
			};
			dispatch("ITEM_SELECTED", newRelationshipParams);
		}
	}
});

export default 'sn-sr-ops-create-relationship';
