import {actionTypes} from '@servicenow/ui-core';
import { ACTIONS } from './constants';
import {getAllActionsEffect} from './dataSource';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({state, dispatch, updateState}) => {
		dispatch(ACTIONS.FETCH_ALL_ACTIONS_REQUESTED);
	},
	[COMPONENT_PROPERTY_CHANGED]: ({dispatch, action, state, updateProperties}) => {
		
		const sysId = action.payload.value;
		const availableActions = state.availableActions;
		const actionDef = availableActions[state.properties.actionId];
		if (action.payload.name === 'actionId') {
			dispatch(ACTIONS.ACTION_UPDATED, {'actionId': sysId, 'actionConfigRequired': (actionDef && actionDef.config_required) ? true : false});
		} else if (action.payload.name === 'configId') {
			dispatch(ACTIONS.CONFIG_UPDATED, {'configId': sysId, 'actionConfigRequired': (actionDef && actionDef.config_required) ? true : false});
		}
	},
	[ACTIONS.FETCH_ALL_ACTIONS_REQUESTED]: getAllActionsEffect,
	[ACTIONS.FETCH_ALL_ACTIONS_REQUEST_SUCCEEDED]: ({action, updateState, updateProperties}) => {
		const data = action.payload.data.GlideRecord_Query.actions._results;
		let availableActions = {};
		for (let action of data) {
			let icon = 'configuration-item-outline';
			if(action.icon && action.icon.value && action.icon.value.length > 0) {
				icon = action.icon.value;
			}
			availableActions[action.sys_id.value] = {
				'sys_id': action.sys_id.value,
				'name': action.name.displayValue,
				'icon': icon,
				'config_required': action.cfg_req.value
			};
		}
		updateState({
			availableActions: availableActions
		});
	},
	[ACTIONS.MORE_ACTIONS_CLICKED]: ({updateState}) => {
		updateState({
			showMoreActionsModal: true
		});
	}, 
	[ACTIONS.NOW_BUTTON_ICONIC_CLICKED]: ({action, updateProperties}) => {
		const cardActionId = action.payload.actionId;
		if(cardActionId == 'clear') {
			updateProperties({
				actionId: '',
				configId: '',
				configDisplayValue: ''
			});
		}
	},
	[ACTIONS.NOW_DROPDOWN_ITEM_CLICKED]:  ({action, updateState, updateProperties}) => {
		const actionSysId = action.payload.item.id;
		updateProperties({
			actionId: actionSysId
		});
	},
	[ACTIONS.NOW_CARD_CLICKED]: ({updateProperties, action}) => {
		if (action.payload.type === "action-tile") {
			updateProperties( {
				actionId: action.payload.actionId
			});
		}
	},
	[ACTIONS.NOW_MODAL_OPENED_SET]: ({updateState}) => {
		updateState({
			showMoreActionsModal: false
		});
	},
	[ACTIONS.NOW_MODAL_FOOTER_ACTION_CLICKED]: ({action, updateState}) => {
		if (action.payload.action.id === 'more-actions-cancel') {
			updateState({
				showMoreActionsModal: false
			});
		}
	},
	[ACTIONS.NOW_RECORD_LIST_CONNECTED_ROW_CLICKED]: ({action, updateState, updateProperties}) => {
		action.preventDefault();
		action.stopPropagation();
		let actionId = action.payload.row.sys_id.value;
		if (actionId) {
			updateState({
				showMoreActionsModal: false
			});
			updateProperties({
				actionId: actionId
			});
		}
	},
	[ACTIONS.ITEM_SELECTED]: ({action}) => {
		action.preventDefault();
		action.stopPropagation();
	},
	[ACTIONS.LIST_ROW_CLICK_UPDATED]: ({action}) => {
		action.preventDefault();
		action.stopPropagation();
	},
};

export default actionHandlers;