import {createCustomElement} from '@servicenow/ui-core';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-sr-ops-maintenance-schedule.scss';
import {t} from 'sn-translate';
import '@servicenow/now-card';
import '@servicenow/now-icon';
import '@servicenow/now-text-link';
import '@servicenow/now-loader';
import '@servicenow/now-button';

function renderCard(state, dispatch) {
	if (state.isLoading)
		return <div className="loading-icon">
					<now-loader label={t("Loading...")} size="md"></now-loader>
				</div>

	return state.totalOutages > 0 ?
		<div>
			<div className="card-container">
				<div className="card-icon">
					<now-icon icon="wrench-outline" size="lg"></now-icon>
				</div>
				<div className="card-data">
				{state.schedules.map(item =>
					<div style={{"padding-bottom": "16px", "width": "100%"}}>
						<div className="card-body">
							<div className="card-content-padding">
								<now-text-link label={item.schedule_name} href={openOutageRecord(item.schedule_id)} opensWindow="true" underlined={false} variant="primary"/>
							</div>
							{/* <div className="card-content-padding">
								<span>{item.ci}</span>
							</div> */}
							<div>
								<span>{item.formattedTime}</span>
							</div>
						</div>
					</div>
				)} 
				</div>
				</div>
				<div className="view-all">
					<now-button-bare label={t("View maintenance calendar")} icon-end="open-link-outline" on-click={(e) => dispatch("VIEW_ALL_SCHEDULES_CLICKED", {targetLink: openOutageRecord(state.firstSchedule)})} size="sm" variant="secondary"/>
				</div>
			</div> :
			<div className="no-records-message">
				{t("No upcoming maintenance scheduled")}
			</div>
}

function openOutageRecord(outageId) {
	return "$show_schedule.do?sysparm_stack=no&sysparm_stack=no&sysparm_sys_id="+outageId;
}

const { COMPONENT_BOOTSTRAPPED } = actionTypes;
const view = (state, {updateState, dispatch}) => (
	<div className="card-content">
		<now-card size='md'>
			<div className="card-header">
				{/* <now-card-header heading={{"label":"Next on-call shift","size":"sm","lines":2}}></now-card-header> */}
				{t("Next maintenance schedule")}
			</div>
				{renderCard(state, dispatch)}
		</now-card>
	</div>
);

const fetchServiceOutageFailure = ({ action, updateState }) => {
	console.log("Failure fetching maintenance schedules");
};

const fetchServiceOutageSuccess = ({ action, updateState }) => {
	updateState({isLoading: false, schedules: action.payload.result, totalOutages: action.payload.result.length});
	if (action.payload.result.length > 0)
		updateState({firstSchedule: action.payload.result[0].schedule_id});
};

const fetchPlannedOutages = createHttpEffect('/api/sn_sro/serviceoncallhelper/getMaintenanceSchedule', {
	method: 'GET',
	successActionType: 'FETCH_SERVICE_OUTAGE_SUCCESS',
	errorActionType: 'FETCH_SERVICE_OUTAGE_FAILURE',
});

const viewAllSchedules = ({action}) => {
	window.open(action.payload.targetLink);
};

createCustomElement('sn-sr-ops-maintenance-schedule', {
	renderer: {type: snabbdom},
	initialState: {
		totalOutages: 0,
		isLoading: false,
		schedules: '',
		firstSchedule: '',
		outages: []
	},
	view,
	styles,
	actionHandlers: {
		"FETCH_SERVICE_OUTAGE": fetchPlannedOutages,
		"FETCH_SERVICE_OUTAGE_SUCCESS": fetchServiceOutageSuccess,
		"FETCH_SERVICE_OUTAGE_FAILURE": fetchServiceOutageFailure,
		"FETCH_USER_SPAN_FAILURE": fetchServiceOutageFailure,
		[COMPONENT_BOOTSTRAPPED]: ({state, dispatch, updateState}) => {
			console.log("Dispatching fetch outage");
			dispatch('FETCH_SERVICE_OUTAGE');
		},
		"VIEW_ALL_SCHEDULES_CLICKED": viewAllSchedules,
	}
});

export default 'sn-sr-ops-maintenance-schedule';
