import {createCustomElement} from '@servicenow/ui-core';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {actionTypes} from '@servicenow/ui-core';
import {t} from 'sn-translate';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-sr-ops-shift.scss';
import '@servicenow/now-card';
import '@servicenow/now-icon';
import '@servicenow/now-text-link';
import '@servicenow/now-loader';
import '@servicenow/now-button';

function renderCard(state, dispatch) {
	if (state.isLoading)
		return <div className="loading-icon">
					<now-loader label={t("Loading...")} size="md"></now-loader>
				</div>

	let link = "$oc.do?sysparm_timeline_enabled=true&sysparm_current_view=weekly" ;
	if (state.upcomingSpans > 0 && state.shifts && state.shifts.length > 0) {
		link = link + "&sysparm_group_id=" + state.shifts[0].span_group_id;
	}
	return state.upcomingSpans > 0 ?
		<div>
			<div className="card-container">
				<div className="card-icon">
					<now-icon icon="calendar-outline" size="lg"></now-icon>
				</div>
				<div className="card-data">
				{state.shifts.map(item =>
					<div style={{"padding-bottom": "16px", "width": "100%"}}>
						<div className="card-body">
							<div className="card-content-padding">
								<now-text-link label={item.group_name} href={getOnCallWorkbenchUrl(item.span_group_id, item.start_date)} opensWindow="true" underlined={false} variant="primary"/>
							</div>
							<div>
								<span>{item.formattedSpan}</span>
							</div>
							{/* <div className="divider">
							</div> */}
						</div>
					</div>
				)} 
				</div>
			</div> 
			<div className="view-all">
				<now-button-bare label={t("View on-call calendar")} icon-end="open-link-outline" on-click={(e) => dispatch("VIEW_ALL_SHIFTS_CLICKED", {targetLink: link})} size="sm" variant="secondary"/>
			</div>
		</div>:
		<div className="no-records-message">
			{t("No upcoming shifts")}
		</div>
}

function renderFooter(state, dispatch) {
	let userPrefLink = "$oc_user_preferences.do";
	return state.isNotifyActive ?
		<div className="card-footer">
			<div className="view-all">
				<now-text-link label={t("View notification preferences")} href={userPrefLink} opensWindow="true" underlined={false} variant="primary"/>
			</div>
		</div>
	: '';
}

function getOnCallWorkbenchUrl(groupId, startDate) {
	return "/$oc.do?sysparm_timeline_enabled=true&sysparm_current_view=weekly&sysparm_group_id="+groupId+"&sysparm_start_date="+startDate;
}

const { COMPONENT_BOOTSTRAPPED } = actionTypes;
const view = (state, {updateState, dispatch}) => {
	if(state.isOnCallActive == true)
		return renderShifts(state, dispatch);
};

function renderShifts(state, dispatch) {
	return <div className="card-content">
				<now-card size='md'>
					<div className="card-header">
						{t("Next on-call shift")}
					</div>
					{renderCard(state, dispatch)}
					{renderFooter(state, dispatch)}
				</now-card>
			</div>;
}

const userDetailsQuery = `{
	GlideDomain_Query {
	   user {
			sys_id
		  email
		  timeZone
		}
	}
}`;

const openTeamRecord = () => {
};

const fetchUserDetails = createGraphQLEffect(
	userDetailsQuery, {
	successActionType: "FETCH_USER_DETAILS_SUCCESS",
});

const fetchUserDetailsSuccess = ({action, updateState, dispatch}) => {
	const data = action.payload.data.GlideDomain_Query.user.sys_id;
	updateState({userId: data, isLoading: true});
	dispatch('FETCH_USER_SPAN', {user_id: data});
};

const fetchUserSpan = createHttpEffect('/api/sn_sro/serviceoncallhelper/getShiftSpan', {
	method: 'GET',
	queryParams: ['user_id'],
	successActionType: 'FETCH_USER_SPAN_SUCCESS',
	errorActionType: 'FETCH_USER_SPAN_FAILURE',
});

const fetchUserSpanSuccess = ({ action, updateState }) => {
	const spanCount = action.payload.length;
	updateState({isLoading: false});
	if (spanCount > 0) {
		const grpData = action.payload[0].group_name;
		const startData = action.payload[0].start;
		updateState({groupName: grpData, startDate: startData, upcomingSpans: spanCount, shifts: action.payload});		
	}
};

const fetchIsOnCallActive = createHttpEffect('/api/sn_sro/serviceoncallhelper/getIsOnCallActive', {
	method: 'GET',
	successActionType: 'FETCH_ON_CALL_ACTIVATION_SUCCESS',
	errorActionType: 'FETCH_ON_CALL_ACTIVATION_FAILURE',
});

const fetchIsOnCallActiveSuccess = ({action, updateState, dispatch}) => {
	const onCallActiveStatus = action.payload;
	updateState({isOnCallActive: onCallActiveStatus});
	if(onCallActiveStatus == true)
		dispatch('FETCH_USER_DETAILS');
};

const fetchIsOnCallActiveFailure = ({action, updateState, dispatch}) => {
	console.log("Failure to fetch on call activation status");
};

const fetchIsNotifyActive = createHttpEffect('/api/sn_sro/serviceoncallhelper/getIsNotifyActive', {
	method: 'GET',
	successActionType: 'FETCH_NOTIFY_ACTIVATION_SUCCESS',
	errorActionType: 'FETCH_NOTIFY_ACTIVATION_FAILURE',
});

const fetchIsNotifyActiveSuccess = ({action, updateState, dispatch}) => {
	const data = action.payload;
	updateState({isNotifyActive: data});
};

const fetchIsNotifyActiveFailure = ({action, updateState, dispatch}) => {
	console.log("Failure to fetch Notify activation status");
};

const viewAllOnCallShifts = ({action}) => {
	window.open(action.payload.targetLink);
};

createCustomElement('sn-sr-ops-shift', {
	renderer: {type: snabbdom},
	initialState: {
		groupName: '',
		startDate: '',
		userId: '',
		upcomingSpans: 0,
		isLoading: false,
		shifts: '',
	},
	view,
	styles,
	actionHandlers: {
		"FETCH_USER_DETAILS": fetchUserDetails,
		"FETCH_USER_DETAILS_SUCCESS": fetchUserDetailsSuccess,
		"FETCH_USER_SPAN": fetchUserSpan,
		"FETCH_USER_SPAN_SUCCESS": fetchUserSpanSuccess,
		"FETCH_ON_CALL_ACTIVATION": fetchIsOnCallActive,
		"FETCH_ON_CALL_ACTIVATION_SUCCESS": fetchIsOnCallActiveSuccess,
		"FETCH_ON_CALL_ACTIVATION_FAILURE": fetchIsOnCallActiveFailure,
		"FETCH_NOTIFY_ACTIVATION": fetchIsNotifyActive,
		"FETCH_NOTIFY_ACTIVATION_SUCCESS": fetchIsNotifyActiveSuccess,
		"FETCH_NOTIFY_ACTIVATION_FAILURE": fetchIsNotifyActiveFailure,
		[COMPONENT_BOOTSTRAPPED]: ({state, dispatch, updateState}) => {
			dispatch("FETCH_ON_CALL_ACTIVATION");
			dispatch("FETCH_NOTIFY_ACTIVATION");
		},
		"VIEW_ALL_SHIFTS_CLICKED": viewAllOnCallShifts,
	}
});

export default 'sn-sr-ops-shift';
