import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { ACTIONS } from './constants';

const fetchServiceQuery = `query($sys_id: String!) {
	GlideRecord_Query {
		cmdb_ci_service(sys_id: $sys_id) {
			_results {
				sys_id {
					value
				}
				sys_class_name {
					value
				}
			}
		}
	}
}`;

export const fetchService = createGraphQLEffect(fetchServiceQuery, {
	variableList: ['sys_id'],
	successActionType: ACTIONS.FETCH_SERVICE_SUCCESS,
	errorActionType: ACTIONS.FETCH_SERVICE_ERROR
});
