import '@devsnc/sn-glide-form-controls';
import '@devsnc/sn-declarative-actions';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-empty-state';
import {getReferencePickerConfig} from './utils';
import {t} from 'sn-translate';
import {ACTIONS} from './constants';

function renderSingleCondition(state, {updateState, dispatch}, i) {
    return  <div>
                <div id={i} className='condition-wrapper'>
                    <div className='delete-icon'>
                        {renderDeleteButton(state, updateState,i)}
                    </div>
                    <div className='record-picker'>
                        <sn-record-reference-connected
                            {...getReferencePickerConfig(state, i, state.condition)}
                            onValueChange={({ value, displayValue}) => {
                                updateState({
                                    condValue: value,
                                    condDisplayValue: displayValue,
                                });
                                dispatch(ACTIONS.CONDITION_UPDATED, {currentElement: i, currentValue: value, currentDisplayValue: displayValue});
                            }}/>
                    </div>
                </div>
                <br/>
            </div>;
}

function renderDeleteButton(state, updateState, i) {
    return (<now-button-iconic size='sm' configAria={{"aria-label":t("Remove condition")}} append-to-payload={{id: i}} icon="close-outline" disabled={state.properties.isLegacyRule ? true : false} variant="tertiary" bare></now-button-iconic>);
}

function renderAllConditions(state, {updateState, dispatch}) {
    let i = 0;
    let tempConditionTemplate = []; 
    if ( state.condition && state.condition.length > 0 ) {
        state.condition.forEach((condition) => {
            tempConditionTemplate.push(renderSingleCondition(state, {updateState, dispatch}, condition.counter));
            i++;
        });
        return <div>{tempConditionTemplate}</div>;
    }
    return null;
}

export function renderConditions(state, {updateState, dispatch}) {
    if ( state.showEmptyCondition )
        return (
            <div>
                <div className='no-conditions-container'>
                    <now-empty-state type="empty" variant="fit" bodyText={false} headerText={false}/>
                    <div className="text-primary">{t("No conditions set")}</div>
                    {state.properties.isLegacyRule ? '' :
                        <div>
                            <div className='text-tertiary'>{t("Add conditions for more accurate rules.")}</div>
                            <now-button 
                                label={t("Add condition")}
                                variant="primary"
                                size="sm"
                                icon="plus-outline" 
                                on-click={ (e) => { 
                                    let tempCount = state.count + 1;
                                    updateState({count: tempCount, showEmptyCondition: false});
                                    dispatch(ACTIONS.ADD_CLICKED); } 
                                }>
                            </now-button>
                        </div>
                    }
                </div>
            </div>
        );
    else
        return (
            <div>
                <div>{t('All of these conditions must be met.')}</div>
                <br/>
                {renderAllConditions(state, {updateState, dispatch})}
                {state.properties.isLegacyRule ? '' :
                    <div>
                        <now-button-bare label={t("Add Condition")} variant="secondary" size="md" icon-start="plus-outline" on-click={ (e) => { let tempCount = state.count + 1;
                            updateState({count: tempCount});
                            dispatch(ACTIONS.ADD_CLICKED); } }>
                        </now-button-bare>
                    </div>
                }
            </div>
        );
}