import { t } from 'sn-translate';
import '@devsnc/sn-record-input';
import '@servicenow/now-button';
import '@servicenow/now-modal';
import '@servicenow/now-loader';
import '@servicenow/now-alert';
import '@servicenow/now-record-field-lookup-connected';
import { ACTIONS } from "./constants";

const view = (state, {updateState, dispatch}) => (
	<div>
		<now-modal size='lg' opened={state.properties.opened} header-label={state.title}>
			{renderTeamModal(state, updateState, dispatch)}
		</now-modal>
	</div>
);

const renderTeamModal = (state, updateState, dispatch) => {
	if (!state.loading) {
		return (
			<div>
				<div className="row">
					  <div className="column">
					  <sn-record-input required={true} value={state.nameValue} type="text" name="team-name" label={t("Name")}
										onValueChange={({ value, displayValue, error }) => {
										updateState({
												nameValue: value,
												nameDisplayValue: displayValue
											});
										}}
				 	 />
					</div>
					<div className="column">
					  <sn-record-input fieldType="text" name="team-email" label={t("Email")} value={state.emailValue}
									  onValueChange={({ value, error}) => {
										updateState({
												emailValue: value
											});
										}}
					  />
					</div>
				</div>
				<div className="row">
					<div className="column">
					  <now-record-field-lookup-connected
						  label={t("Manager")}
						  tableName="sys_user"
						  fieldName="name"
						  value={state.managerValue}
						  displayValue={state.managerDisplayValue}
						  onValueChange={({ value, displayValue }) => {
							updateState({
									managerValue: value,
									managerDisplayValue: displayValue
								});
							}}
					  />
					</div>
					<div className="column">
						<now-record-field-lookup-connected
							label={t("Tags")}
							tableName="label"
							fieldName="name"
							value={state.tagsValue}
							displayValue={state.tagsDisplayValue}
							multiSelection={true}
							onValueChange={({ value, displayValue }) => {
								updateState({
									tagsValue: value,
						  			tagsDisplayValue: displayValue
					  			});
				  			}}
						/>
					</div>
				</div>
				<div className="row">
					<div className="column">
						<now-record-field-lookup-connected
							label={t("Members")}
							tableName="sys_user"
							fieldName="name"
							value={state.membersValue}
							displayValue={state.membersDisplayValue}
							multiSelection={true}
							onValueChange={({ value, displayValue }) => {
								updateState({
									membersValue: value,
						  			membersDisplayValue: displayValue
					  			});
				  			}}
						/>
					</div>
				</div>
				<div className="row">
					<div className="column">
						  <sn-record-input type="textarea" name="team-desc" label={t("Description")} value={state.descValue}
									  onValueChange={({ value }) => {
										updateState({
												descValue: value
											});
										}}/>
					</div>
				</div>
				<div className="button-row">
					<div className="cancel-save-div">
						<div className="cancel-save-button">
							<now-button label={t("Cancel")} variant="secondary" size="md" configAria={{}}
							on-click={() => dispatch(ACTIONS.CANCEL_CLICKED)}
							></now-button>
						</div>
						<div className="cancel-save-button">
							<now-button label={t("Save")} variant="primary" size="md" configAria={{}} disabled={!(state.nameValue)}
							on-click={() => {
                                if (state.nameValue)
                                    dispatch(ACTIONS.SAVE_CLICKED)
                            }}
							></now-button>
						</div>
					</div>
				</div>
			</div>
		);
	}
	return (
		<now-loader label={t("Creation in progress")} ></now-loader>
	);
};

export default view;
