import '@servicenow/now-heading';
import '@servicenow/now-button';
import '@servicenow/now-card';
import '@servicenow/now-modal';
import '@servicenow/now-icon';
import '@servicenow/now-dropdown';
import '@devsnc/sn-record-input';
import '@servicenow/now-record-field-lookup-connected';
import '@servicenow/now-record-list-connected';
import '@devsnc/sn-glide-form-controls';
import { t } from 'sn-translate';
import { ACTIONS,QUERIES } from './constants';

export default function view(state, {dispatch, updateState, updateProperties}) {
    return renderContent(state, dispatch, updateState, updateProperties);
}

function renderContent(state, dispatch, updateState, updateProperties) {
	if (!state.properties.actionId)
		return renderAvailableActions(state, dispatch, updateState, updateProperties);
	return renderActionsCard(state, updateProperties);
}

function renderActionsCard(state, updateProperties) {
	return (
		<div>
			<div className='action-tile-border'>
				<div className='clear-icon'>
					<now-button-iconic configAria={{"aria-label":t("Remove action")}} disabled={state.properties.isLegacyRule} append-to-payload={{actionId: 'clear'}} size='sm' icon="close-outline" variant="tertiary" bare></now-button-iconic>
				</div>
				{renderCardContent(state, updateProperties)}
			</div>
		</div>
	);
}

function renderAvailableActions(state, dispatch, updateState, updateProperties) {
	let actionsJSX = [];
	let moreActions = [];
	const dropdownItems = [];
	if (state.availableActions) {
		let availableActions = state.availableActions;
		for (let ai in availableActions) {
			const action = availableActions[ai];
			const actionSysId = action.sys_id;
			const actionIcon = action.icon ? action.icon : 'configuration-item-outline';
			let oobActions = ['4907a55de7661010e10f6584c2f6a977', '6137a55de7661010e10f6584c2f6a901', 'ef47e95de7661010e10f6584c2f6a915', '86c2ecd2c3f520103111070e9140dde5', 'f51a0d2ec3fe20103111070e9140dd8a'];
			if(oobActions.includes(actionSysId)) {
				actionsJSX.push(
					<now-card size="md" interaction="click" append-to-payload={{actionId: actionSysId, type: 'action-tile'}} config-aria={{'aria-label': t(action.name)}}>
						<div className="action-tile-content">
							<div className="action-tile-icon">
								<now-icon icon={actionIcon} size="xl"></now-icon>
							</div>
							<div className="action-tile-name">
								{t(action.name)}
							</div>
						</div>
					</now-card>
				);
			} else {
				dropdownItems.push({
					id: action.sys_id, 
					label: t(action.name)
				});
			}
		}
		if(dropdownItems.length > 0) {
			moreActions.push(
				<now-button tooltipContent={t('More actions')} label={t('More actions')} variant="secondary" 
				on-click={() => dispatch(ACTIONS.MORE_ACTIONS_CLICKED)}
				size="md"></now-button>
			);
		}
	}
	return (
		<div>
			<now-heading label={t("Add an action")} level="5" variant="title-tertiary" />
			<hr/>
			<div className="actions-container">
				{actionsJSX}
			</div>
			<div className="more-actions">
				{moreActions}
			</div>
			<now-modal
    			opened={state.showMoreActionsModal}
    			size='lg'
    			header-label={t('More actions')}
   				footer-actions='[{
					"id": "more-actions-cancel",	
           		 	"variant": "secondary",
            		"label": "Cancel"
        		}]'
			>
				{renderMoreActionsModal(state)}
			</now-modal>
		</div>
	)
}

const renderMoreActionsModal = function(state) {
	return <now-record-list-connected table='sn_sro_act' hideDeclarativeActions={true} query={QUERIES.FILTER_OOB_ACTIONS}
	columns='name,short_description' limit='10' hideHeader={true} hideRowSelector={true} hideColumnFiltering={true} hideColumnGrouping={true} hideCellFilter={true} hideMenuButton={true} hideQuickEdit={true} hideSelectAll={true} />
}

function renderCardContent(state, updateProperties) {
	let content = [];
	let dropdownItems = [];
	let selectedItems = [];
	let availableActions = state.availableActions
	for (let action in availableActions) {
		dropdownItems.push({
			'value': availableActions[action].sys_id,
			'displayValue': availableActions[action].name,
			'icon': availableActions[action].icon
		});
	}

	if (state.properties.actionId)
		selectedItems.push(state.properties.actionId);

	content.push(
		<div className="row">
		<div className="column">
			<sn-record-choice
				label={t('Action')}
				choices={dropdownItems}
				value={state.properties.actionId}
				display-value={availableActions[state.properties.actionId].name}
				onValueChange={({value}) => {
					updateProperties({
							actionId: value,
							configId: '',
							configDisplayValue: ''
						});
					}}
				ariaLabel={t('Action')}
				readonly={state.properties.isLegacyRule}
			/>
		</div>
		</div>
	);
	let action = null;
	if(state.properties.actionId && state.availableActions[state.properties.actionId]) {
		action = state.availableActions[state.properties.actionId];
	}
	if (action && true == action.config_required) {
		content.push(
			<div className="row">
			<div className="column">
			  <now-record-field-lookup-connected
				  required={true}
				  label={t("Configuration")}
				  additionalQueryString={"att_doc=" + state.properties.actionId}
				  tableName="sn_sro_cfg"
				  fieldName="name"
				  value={state.properties.configId}
				  displayValue={state.properties.configDisplayValue}
				  onValueChange={({ value, displayValue }) => {
					updateProperties({
							configId: value,
							configDisplayValue: displayValue
						});
					}}
			  />
			</div>
			</div>
		);
		if (state.properties.configId) {
			content.push(
				<div className="button-row">
					<now-button-bare variant="primary" iconStart="open-link-fill" size="sm" label={t("Edit configuration")}
						on-click={() => {
							let link = "sn_sro_cfg.do?sys_id=" + state.properties.configId;
							window.open(link, "_blank");
						}}
					/>
				</div>
			);
		}
	}

	return content;
}
