import {actionTypes} from '@servicenow/ui-core';
import {createUpdateStateEffect} from '@servicenow/ui-effect-update-state';

import {ACTIONS, SORT_QUERY, CHANGE_QUERY} from './constants';
import {fetchChanges} from './dataSource';

const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

const createQuery = serviceSysId =>
	serviceSysId ?
		`cmdb_ci.sys_id=${serviceSysId}^cmdb_ci.sys_class_name=cmdb_ci_service^ORcmdb_ci.sys_class_name=cmdb_ci_service_discovered^ORcmdb_ci.sys_class_name=cmdb_ci_query_based_service^active=true^NQbusiness_service.sys_id=${serviceSysId}^business_service.sys_class_name=cmdb_ci_service^ORbusiness_service.sys_class_name=cmdb_ci_service_discovered^ORbusiness_service.sys_class_name=cmdb_ci_query_based_service`
		: CHANGE_QUERY;

const triggerDataFetch = ({state, dispatch}) => {
	const {
		properties: {service},
		sortCriteria
	} = state;

	const QUERY = createQuery(service);

	dispatch(ACTIONS.FETCH_CHANGE, {
		queryConditions: `${SORT_QUERY[sortCriteria]}^${QUERY}`
	});
};

export default {
	[ACTIONS.INTERNAL_STATE_SET]: ({action, updateState}) => {
		if (action.payload)
			updateState({...action.payload});
	},
	[COMPONENT_BOOTSTRAPPED]: triggerDataFetch,
	[COMPONENT_PROPERTY_CHANGED]({action, state, dispatch}) {
		if (action.payload.name === 'refreshChange' && action.payload.value !== action.payload.previousValue)
			triggerDataFetch({state, dispatch});
	},
	[ACTIONS.FETCH_CHANGE]: fetchChanges,
	[ACTIONS.FETCH_CHANGE_SUCCESS]({action, updateState}) {
		updateState({
			changes: action.payload.data.GlideRecord_Query.change_request._results,
			totalChanges: action.payload.data.GlideAggregateRecord_Query.aggregates[0].count, 
		});
	},
	[ACTIONS.FETCH_CHANGE_ERROR]() {
		//TODO: Show error message
	},
	[ACTIONS.NOW_DROPDOWN_ITEM_CLICKED]({state, action, dispatch, updateState}) {
		updateState({sortCriteria: action.payload.item.id});
		triggerDataFetch({
			state: {...state, sortCriteria: action.payload.item.id},
			dispatch
		});
	},
	[ACTIONS.CARD_CLICKED]({action, dispatch}) {
		if (action.payload.type === 'change-card') {
			dispatch(ACTIONS.ITEM_SELECTED, {
				table: 'change_request',
				sys_id: action.payload.change.sys_id.value
			});
		}
	},
	[ACTIONS.NOW_BUTTON_ICONIC_CLICKED]({state, action, dispatch}) {
		if (action.payload.action === 'refresh-changes')
			triggerDataFetch({state, dispatch});
	}
};
