import {t} from 'sn-translate';

import '@servicenow/now-dropdown';
import '@servicenow/now-card';
import '@servicenow/now-highlighted-value';

import {RISK_MAP} from './constants';

const dropdownItems = [
	{id: 'change_risk', label: t('Change risk')}, 
	{id: 'change_updated_desc', label: t('Change updated time (desc.)')},
	{id: 'change_updated_asc', label: t('Change updated time (asc.)')}
];

const renderChange = change => {
	if (!change)
		return '';

	const {
		number: {value: number},
		risk: {
			value: riskValue,
			displayValue: riskDisplayValue
		},
		short_description: {displayValue: shortDescription},
		cmdb_ci: {displayValue: configurationItem},
		assignment_group: {displayValue: assignmentGroup},
		sys_updated_on: {displayValue: updatedOn}
	} = change;

	return <div className="change">
		<now-card
			size="lg"
			append-to-payload={{change, type: 'change-card'}}
			interaction="click"
			className="change-card"
			configAria={{'aria-label': t('Change: {0}, {1}, Risk: {2}, Last updated: {3}', change.number.value, change.short_description.displayValue , change.risk.displayValue, change.sys_updated_on.displayValue)}}
		>
			<now-card-header tagline={{label: number, variant: 'secondary'}}>
				<div slot="metadata">
					<now-highlighted-value
						label={riskDisplayValue}
						status={RISK_MAP[riskValue]}
					/>
				</div>
			</now-card-header>
			<h3 className="change-short-description">
				{shortDescription}
			</h3>
			<div className="change-row">
				<div className="change-row-column">
					<div className="change-row-column-label">{t('Configuration item')}</div>
					<div>{configurationItem}</div>
				</div>
				<div className="change-row-column">
					<div className="change-row-column-label">{t('Assignment group')}</div>
					<div>{assignmentGroup}</div>
				</div>
			</div>
			<div className="change-last-event-time">
				{t('Last updated: {0}', updatedOn)}
			</div>
		</now-card>
	</div>;
};

const renderChanges = changes => {
	if(!changes || changes.length <= 0)
		return '';

	return <div className="changes-list-flex">
		{changes.map(
			change => renderChange(change)
		)}
		<div className="change change--filler" />
		<div className="change change--filler" />
		<div className="change change--filler" />
		<div className="change change--filler" />
	</div>;
};

export default state => {
	const {
		properties: {service},
		changes,
		sortCriteria,
		totalChanges
	} = state;

	return <div class={{
		changes: true,
		'changes--context-menu': service
	}}>
		<div className="changes-header">
			<h2 className="changes-header-heading">{t('Changes')}</h2>
			<now-button-iconic
				icon="sync-outline"
				variant="tertiary"
				bare
				hidePadding
				size="md"
				configAria={{
					button: {'aria-label': t('Refresh Changes')}}
				}
				tooltipContent={t('Refresh Changes')}
				append-to-payload={{action: 'refresh-changes'}}
			/>
		</div>
		<div className="changes-sort">
			<div className="changes-sort-label">{t('Sort by')}</div>
			<now-dropdown 
				items={dropdownItems}
				className="changes-sort-dropdown"
				size="sm"
				variant="tertiary"
				manage-selected-items
				selected-items={[sortCriteria]}
			/>
		</div>
		<div className="changes-list">
			{renderChanges(changes)}
		</div>
		<div className="changes-total-count">
			{t(
				'Showing {0} - {1} out of {2}',
				totalChanges > 0 ? 1 : 0,
				totalChanges <= 20 ? totalChanges : 20,
				totalChanges
			)}
		</div>
	</div>;
};
