
import {t} from 'sn-translate';

export function getReferencePickerConfig(state, i, condition2) {
    let tempCondition = [];
    for (var j=0; j<state.condition.length; j++){
        if (i == state.condition[j].counter) {
            tempCondition = state.condition[j];
        }
    }
    //console.log("Condition in getReferencePickerConfig - "+JSON.stringify(tempCondition));
    if (state.condition && tempCondition) {
        const name = tempCondition.displayValue;
        const sys_id = tempCondition.value;
    }
	return {
        fieldName: 'cnd_set',
        tableName: 'sn_sro_int_act',
        recordSysId: "-1",
        name: "cnd_set",
        label: t('Condition'),
        fieldType: "reference",
        value: state.condition && tempCondition ? tempCondition.value : state.value,
        displayValue: state.condition && tempCondition ? tempCondition.displayValue : state.displayValue,
        readonly: state.properties.isLegacyRule,

		declarativeActionsProps: {
			declarativeUiActions: [
				{
					name: 'open_record',
					icon: 'info-circle',
					label: t('Open record'),
					requiresValue: true,
					actionType: "dispatch_action",
					actionDispatch: "REFERENCE_INFO_CLICK",
					actionPayload: { 
						"referenceInfoClick": { 
                            name: state.condition && tempCondition ? tempCondition.displayValue : state.displayValue,
                            sys_id: state.condition && tempCondition ? tempCondition.value : state.value,
                            referencetable: 'sn_sro_cnd',
                            table: 'sn_sro_int_act',
                            field: 'cnd_set'
						}
					},
					order: 10
				},
				{
                    name: 'reference_search',
                    icon: 'search',
                    label: t('Search for record'),
                    requiresValue: false,
                    requiresWritable: true,
                    order: 100,
                    actionType: 'action_component',
                    actionComponent: "sn-declarative-reference-search"
				}
			],
            label: t("Conditions"),
            name: "cnd_set",
            reference: "sn_sro_cnd",
            referringRecordId: '-1',
            referringTable: 'sn_sro_int_act',
            tableName: 'sn_sro_int_act',
            fieldName: "cnd_set",
            referenceTable: "sn_sro_cnd",
		}
	}
}