import { t } from 'sn-translate';
import '@servicenow/now-button';
import '@servicenow/now-dropdown';

export function renderZoomControls(state) {
	const zoom = state.zoom;

	const scalePercentage = Math.floor(zoom * 100);

	return (
		<div className="zoom-controls">
			<now-button
				icon-name="minus-fill"
				size="sm"
				component-name="zoom-out"
				variant="secondary"
				tooltip-content={t('Zoom Out')}
				configAria={{
					'aria-label': t('Zoom Out')
				}}
			/>

			<div className="current-zoom">
				<span className="a11y-message">{t('Current Zoom')}</span>
				<span>{`${scalePercentage}%`}</span>
			</div>

			<now-button
				icon-name="plus-fill"
				size="sm"
				component-name="zoom-in"
				variant="secondary"
				tooltip-content={t('Zoom In')}
				configAria={{
					'aria-label': t('Zoom In')
				}}
			/>
		</div>
	);
};

export function renderZoomToFit(state) {
	return (
		<div className="zoom-to-fit">
			<now-button
				size="sm"
				component-name="zoom-to-fit"
				variant="secondary"
				tooltip-content={t('Zoom To Fit')}
			>{t("Fit")}</now-button>
		</div>
	);
}

export function renderTreeView(state) {
	const treeDropdownItems = [{id: "horizontal", label: t("Horizontal")}, {id: "vertical", label: t("Vertical")}];

	let selectedItem = state.layout ? [state.layout]: ["vertical"];
	let selectedItemLabel = treeDropdownItems.filter(({id}) => id === selectedItem[0])[0].label;

	return (
		<div className="tree-view">
			<now-dropdown 
				items={treeDropdownItems} 
				tooltipContent={t('Tree view')}
				size="sm" 
				selectedItems={selectedItem}
				variant="secondary" 
				select="single"
				configAria={{
					'aria-label': t('Orientation: {0}', selectedItemLabel)
				}}
			/>
		</div>
	);
}