import { ACTIONS } from './constants';
import { actionTypes } from '@servicenow/ui-core';
import { postIntAction, putIntAction, postCndAction, putCndAction, fetchIntAction, fetchIntActionDetails, fetchRecord, deleteIntAction } from "./dataSource";
import { t } from 'sn-translate';
const { COMPONENT_BOOTSTRAPPED,COMPONENT_DISCONNECTED } = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({
		action,          // The dispatched action
		dispatch,        // Dispatch actions from the component handling the action
		state,           // Current state of the component handling the action
		properties,      // Properties of the component handling the action
		updateState,     // Function used to perform declarative state updates
		updateProperties // Function used to update component properties
	}) => {
		if (state.properties.sysId != "-1" && state.properties.sysId != "undefined") {
			// Existing record
			updateProperties({
				isNewRecord : false
			});
			dispatch(ACTIONS.FETCH_INT_ACTION, {tableName: "sn_sro_int_act", sysId: state.properties.sysId, sysparm_display_value: 'all'});
			dispatch(ACTIONS.FETCH_INT_ACTION_DETAILS, {sysId: state.properties.sysId});
		} else {
			// New record
			updateProperties({
				isNewRecord : true
			});
			let model = {...state.model};
			model.order = "100";
			if (state.properties.sourceTable) {
				model.type = state.properties.sourceTable == 'sn_sro_em_integration' ? 'sn_sro_em_integration' : 'cmdb_ci_service';
				model.typeDisplayValue = state.properties.sourceTable == 'sn_sro_em_integration' ? t('Integration') : t('Service');
			}
			if (state.properties.sourceSysId) {
				if (model.type == 'cmdb_ci_service')
					dispatch(ACTIONS.FETCH_RECORD, {tableName : 'cmdb_ci_service', sysId : state.properties.sourceSysId});
				if (model.type == 'sn_sro_em_integration')
					dispatch(ACTIONS.FETCH_RECORD, {tableName : 'sn_sro_em_integration', sysId : state.properties.sourceSysId});
			}
			updateState({model : model});
		}
	},
	[COMPONENT_DISCONNECTED]: ({action, dispatch, state, properties, updateState, updateProperties}) => {
	},
	[ACTIONS.CONDITION_BUILDER_OPEN_MODAL]: ({action, dispatch, state, properties, updateState, updateProperties}) => {
		updateState({ conditionSysId: '', openConditionBuilderModal: true });
	},
	[ACTIONS.CONDITION_BUILDER_CRITERIA_CHANGED]: ({action, dispatch, state, properties, updateState, updateProperties}) => {
        const { payload } = action;
		const { value } = payload;
		let conditionBuilder = {...state.condition_builder};
		conditionBuilder.criteria = value;
        updateState({
			condition_builder: conditionBuilder
		});
	},
	[ACTIONS.POST_CND_ACTION]: postCndAction,
	[ACTIONS.PUT_CND_ACTION]: putCndAction,
	[ACTIONS.POST_CND_ACTION_SUCCESS]: ({action, dispatch, state, properties, updateState, updateProperties}) => {
		const conditionBuilder = {...state.condition_builder}
		let msg = {};
		const payload = action.payload;
		if (payload && payload.result && payload.result.sys_id != '') {
			const sysId = payload.result.sys_id
			const msgList = [];
			if (conditionBuilder.sysId == payload.result.sys_id)
				msg = {"id":"alert1","status":"positive","icon":"circle-check-outline","header":"", "content": t('Your condition has been updated successfully!'), "action":{"type":"dismiss"}};
			else
				msg = {"id":"alert1","status":"positive","icon":"circle-check-outline","header":"", "content": t('Your condition has been created successfully!'), "action":{"type":"dismiss"}};
			msgList.push(msg);
			updateState({
				messageList: msgList,
				openConditionBuilderModal: false,
				conditionSysId: ''
			});
			//Refresh condition builder modal
			var builder = {
				sysId: '',
				name: '',
				short_description: '',
				type: 'criteria',
				criteria: '',
				script: '(function( /*ExecutionContext*/ executionContext, gr) { \n\t// Add your code here and return a boolean \n\treturn false;\n})(executionContext, gr);'
			}
			updateState({condition_builder: builder});
		}
	},
	[ACTIONS.POST_CND_ACTION_ERROR]: ({action, dispatch, state, properties, updateState, updateProperties}) => {
		const payload = action.payload;
		if (payload && payload.data && payload.data.error) {
			const error = payload.data.error;
			if (error.message || error.detail) {
				const msgList = [];
				if (error.message) {
					const msg = {"id":"alert1","status":"critical","icon":"info-circle-outline","header":"", "content": error.message, "action":{"type":"dismiss"}};
					msgList.push(msg);
				}
				if (error.detail) {
					const msg = {"id":"alert1","status":"critical","icon":"info-circle-outline","header":"", "content": error.detail, "action":{"type":"dismiss"}};
					msgList.push(msg);
				}
				updateState({
					messageList: msgList,
					openConditionBuilderModal: false,
					conditionSysId: ''
				});
			}
		}
	},
	[ACTIONS.TRIGGER_TYPE_UPDATED]: ({action, dispatch, state, properties, updateState, updateProperties}) => {
		if (action.payload.name == 'trigger-type-dropdown') {
			let model = {...state.model};
			model.type = action.payload.item.value;
			model.typeDisplayValue = action.payload.item.displayValue;
			updateState({model : model});
		}
	},
	[ACTIONS.SVC_TRIGGER_TYPE_UPDATED]: ({action, dispatch, state, properties, updateState, updateProperties}) => {
		if (action.payload.name == 'svc-trigger-type-dropdown') {
			let model = {...state.model};
			model.svc_trg_type = action.payload.item.value;
			model.svcTriggerTypeDisplayValue = action.payload.item.displayValue;
			updateState({model : model});
		}
	},
	[ACTIONS.SAVE_BTN_CLICKED]: ({action, dispatch, state, properties, updateState, updateProperties}) => {
		let model = state.model;
		// Start validation
		const triggerType = model.type;
		const actionSysId = model.action;
		const actCfgRequired = model.actionConfigRequired;
		const cfgSysId = model.configuration;
		const order = model.order;
		const msgList = [];
		// Check trigger type, action, order
		// Sequence specified based on section: 1 - Details, 2 - Conditions, 3 - Actions
		let requiredModelFields = {
			type: {
				sequence: 1,
				prefix: 'a',
				display_name: 'trigger type'
			},
			action: {
				sequence: 3,
				prefix: 'an',
				display_name: 'action'
			},
			order: {
				sequence: 1,
				prefix: 'an',
				display_name: 'order'
			}
		};
		// If action requires configuration check one is present
		if(actCfgRequired) {
			requiredModelFields.configuration = {
				sequence: 3,
				prefix: 'a',
				display_name: 'configuration'
			};
		}
		// Conditionaly check ref fields based on triger type
		if ('cmdb_ci_service' == triggerType) {
			requiredModelFields.service = {
				sequence: 1,
				prefix: 'a',
				display_name: 'service'
			};
		} else if ('sn_sro_em_integration' == triggerType) {
			requiredModelFields.integration = {
				sequence: 1,
				prefix: 'an',
				display_name: 'integration'
			};
		}
		
		let missingFields = [];
		for(const fieldName in requiredModelFields) {
			const value = model[fieldName];
			if(null == value || '' == value || 'undefined' == value) {
				missingFields.push({
					sequence: requiredModelFields[fieldName].sequence,
					prefix: requiredModelFields[fieldName].prefix,
					display_name: requiredModelFields[fieldName].display_name
				});
			}
		}

		// Sort based on sequence
		missingFields.sort((a, b) => {
			let comparison = 0;
			let aSeq = parseInt(a.sequence);
			let bSeq = parseInt(b.sequence);
			if (aSeq > bSeq)
				comparison = 1;
			else if (aSeq < bSeq)
				comparison = -1;
			return comparison;
		});
		const valid = 0 == missingFields.length;
		if(false == valid) {
			var displayMsg = 'All required fields must be complete.';
			if (missingFields.length == 1)
				displayMsg = t('{0} Please add {1}.', displayMsg, missingFields[0].prefix + ' ' + missingFields[0].display_name);
			else {
				var fieldMsg = '';
				for (var i = 0; i < (missingFields.length - 1); i++) {
					fieldMsg = missingFields[i].display_name + ', ';
				}
				// Remove comma and space
				fieldMsg = fieldMsg.slice(0, -2);
				var lastField = missingFields[missingFields.length - 1];
				fieldMsg = fieldMsg + ' and ' + lastField.prefix + ' ' + lastField.display_name;
				displayMsg = t('{0} Please add {1}.', displayMsg, fieldMsg);
			}
			const msg = {"id":"saveActionValidationAlert" ,"status":"critical","icon":"info-circle-outline","header":"", "content": displayMsg, "action":{"type":"dismiss"}};
			msgList.push(msg);
			updateState({
				messageList: msgList
			});
		} else {
			let data = {};
			data.trg_type = model.type;
			if (state.model.type == 'cmdb_ci_service')
				data.cmdb_ci_service = model.service;
			else if (state.model.type == 'sn_sro_em_integration')
				data.em_integration = model.integration;
			data.order = model.order;
			data.act = model.action;
			data.cfg = model.configuration;
			data.cnd_set = model.condition;
			data.trg_on_upd = model.triggerOnUpdate;
			data.svc_trg_type = model.svc_trg_type;
			let methodAction = ACTIONS.POST_INT_ACTION;
			const payload = {tableName: 'sn_sro_int_act', data: data};
			const props = state.properties;
			if(null != props && null != props.sysId && '' != props.sysId && 'undefined' != props.sysId && '-1' != props.sysId) {
				payload.sysId = props.sysId;
				methodAction = ACTIONS.PUT_INT_ACTION;
			}
			// Make Table API call to create integration action
			dispatch(methodAction, payload);
		}
		updateState({refreshConditions: true});
	},
	[ACTIONS.POST_INT_ACTION]: postIntAction,
	[ACTIONS.PUT_INT_ACTION]: putIntAction,
	[ACTIONS.POST_INT_ACTION_SUCCESS]: ({action, dispatch, state, properties, updateState, updateProperties}) => {
		const payload = action.payload;
		if (payload && payload.result && payload.result.sys_id != '') {
			const sysId = payload.result.sys_id;
			const msgList = [];
			let msg, recordStatus;
			if (properties.isNewRecord) {
				msg = {"id":"alert1","status":"positive","icon":"circle-check-outline","header":"", "content": t('Your alert rule has been created successfully!'), "action":{"type":"dismiss"}};
				recordStatus = 'inserted';
			}
			else {
				msg = {"id":"alert1","status":"positive","icon":"circle-check-outline","header":"", "content": t('Your alert rule has been updated successfully!'), "action":{"type":"dismiss"}};
				recordStatus = 'updated';
			}
			msgList.push(msg);
			updateState({
				messageList: msgList
			});
			
			dispatch(ACTIONS.FETCH_INT_ACTION, {tableName: "sn_sro_int_act", sysId: sysId, sysparm_display_value: 'all'});
			updateProperties({
				isNewRecord : false,
				sysId: sysId
			});
			dispatch(ACTIONS.RECORD_STATUS_CHANGED, {
				table: 'sn_sro_int_act',
				sys_id: sysId,
				status: recordStatus,
			});
		}
	},
	[ACTIONS.POST_INT_ACTION_ERROR]: ({action, dispatch, state, properties, updateState, updateProperties}) => {
		const payload = action.payload;
		if (payload && payload.data && payload.data.error) {
			const error = payload.data.error;
			if (error.message || error.detail) {
				const msgList = [];
				if (error.message) {
					const msg = {"id":"alert1","status":"critical","icon":"info-circle-outline","header":"", "content": error.message, "action":{"type":"dismiss"}};
					msgList.push(msg);
				}
				if (error.detail) {
					const msg = {"id":"alert1","status":"critical","icon":"info-circle-outline","header":"", "content": error.detail, "action":{"type":"dismiss"}};
					msgList.push(msg);
				}
				updateState({messageList: msgList});
			}
		}
	},
	"CONDITION_CHANGED": ({action, state, updateState}) => {
        let i, cndSet = '';
		let cond = action.payload.condition;
        if (cond) {
            for (i=0; i<cond.length; i++) {
				if (cond[i].value)
                	cndSet = cndSet + cond[i].value + ',';
            }
		}
		cndSet = cndSet.slice(0, -1);
		const model = state.model;
		let currModel = model ? { ...model } : {};
		currModel.condition = cndSet;
		updateState({model: currModel});
	},
	"SRO_AR_CONDITIONS#REFERENCE_ICON_CLICKED" : ({action, state, dispatch, updateState}) => {
		if (action.payload.sysId) {
			dispatch(ACTIONS.FETCH_RECORD, {tableName : 'sn_sro_cnd', sysId : action.payload.sysId});	
		}
	},
	"SRO_AR_CONDITIONS#CONDITION_REFRESHED": ({updateState}) => {
		updateState({refreshConditions: false});
	},
	"SRO_AR_ACTIONS#ACTION_UPDATED": ({action, updateState}) => {
		updateState({
			path: 'model',
			value: {
				'action': action.payload.actionId,
				'actionConfigRequired': action.payload.actionConfigRequired
			},
			operation: 'merge'
		});
	},
	"SRO_AR_ACTIONS#CONFIG_UPDATED": ({action, updateState}) => {
		updateState({
			path: 'model',
			value: {
				'configuration': action.payload.configId,
				'actionConfigRequired': action.payload.actionConfigRequired
			},
			operation: 'merge'
		});
	},
	[ACTIONS.FETCH_INT_ACTION]: fetchIntAction,
	[ACTIONS.FETCH_INT_ACTION_SUCCESS]: ({action, state, updateState, dispatch}) => {
		let data = action.payload.result;
		let model = {...state.model};
		model.type = data.trg_type.value;
		model.svc_trg_type = data.svc_trg_type.value;
		const svcTriggerTypeLookup = {
			'service': t('Service'),
			'ci': t('CI'),
			'both': t('Service + CI'),
		}
		if (data.trg_type.value == 'cmdb_ci_service') {
			model.typeDisplayValue = t('Service');
			const svcTrgDispVal = svcTriggerTypeLookup[model.svc_trg_type];
			model.svcTriggerTypeDisplayValue = svcTrgDispVal;
			model.service = data.cmdb_ci_service.value;
			model.serviceDisplayValue = data.cmdb_ci_service.display_value;
		} else if (data.trg_type.value == 'sn_sro_em_integration') {
			model.typeDisplayValue = t('Integration');
			model.integration = data.em_integration.value;
			model.integrationDisplayValue = data.em_integration.display_value;
		}
		model.order = data.order.value;
		model.action = data.act.value;
		model.configuration = data.cfg.value;
		if (data.cfg.value)
			model.actionConfigRequired = true;
		model.configurationDisplayValue = data.cfg.display_value;
		model.condition = data.cnd_set.value;
		updateState({model : model});
		dispatch(ACTIONS.FORM_UPDATE_HEADER, {
			recordDisplayValue: 'Alert Rule',
			sysId: data.sys_id.value
		});
	},
	[ACTIONS.FETCH_INT_ACTION_ERROR]: ({action, state, updateState, dispatch}) => {
		const payload = action.payload;
		if (payload && payload.data && payload.data.error) {
			const error = payload.data.error;
			if (error.message || error.detail) {
				const msgList = [];
				if (error.message) {
					const msg = {"id":"alert1","status":"critical","icon":"info-circle-outline","header":"", "content": error.message, "action":{"type":"dismiss"}};
					msgList.push(msg);
				}
				if (error.detail) {
					const msg = {"id":"alert1","status":"critical","icon":"info-circle-outline","header":"", "content": error.detail, "action":{"type":"dismiss"}};
					msgList.push(msg);
				}
				updateState({
					messageList: msgList,
					openConditionBuilderModal: false,
					conditionSysId: ''
				});
			}
		}
		updateProperties({
			isNewRecord : false
		});
	},
	[ACTIONS.FETCH_INT_ACTION_DETAILS]: fetchIntActionDetails,
	[ACTIONS.FETCH_INT_ACTION_DETAILS_SUCCESS]: ({action, state, updateState, dispatch}) => {
		const payload = action.payload;
		if (payload && payload.result && payload.result.is_legacy_rule != null) {
			const msgList = [];
			let msg;
			if (payload.result.is_legacy_rule) {
				msg = {"id":"alert1","status":"info","icon":"info-circle-outline","header":"", "content": t('This alert rule was created from the integration page and can only be deleted by deselecting the checkbox on the integration page.'), "action":{"type":"dismiss"}};
			}
			msgList.push(msg);
			updateState({
				messageList: msgList,
				isLegacyRule: payload.result.is_legacy_rule
			});
		}
	},
	[ACTIONS.FETCH_INT_ACTION_DETAILS_ERROR]: ({action, state, updateState, dispatch}) => {
	},
	[ACTIONS.FETCH_RECORD]: fetchRecord,
	[ACTIONS.FETCH_RECORD_SUCCESS]: ({action, state, updateState, dispatch}) => {

		if (action.payload.result.sys_class_name != 'sn_sro_cnd'){
			let model = {...state.model};
			const payload = action.payload;
			const name = payload.result.name;
			if (state.properties.sourceTable === "sn_sro_em_integration") {
				model.integrationDisplayValue = name;
				model.integration = state.properties.sourceSysId;
			} else {
				model.serviceDisplayValue = name;
				model.service = state.properties.sourceSysId;
			}
			
			updateState({model : model});
		}
		else if (action.payload.result.sys_class_name == 'sn_sro_cnd'){
			let record = action.payload.result;
			let tempCondBuilder = {
				sysId: action.payload.result.sys_id,
				name: record.name,
				short_description: record.short_description,
				type: record.type,
				criteria: record.criteria,
				script: record.script
			}
			updateState({condition_builder: tempCondBuilder});
			updateState({openConditionBuilderModal: true, conditionSysId: action.payload.result.sys_id});
		}
	},
	[ACTIONS.NOW_CHECKBOX_CHECKED_SET]: ({action, state, updateState}) => {
		const { name, value } = action.payload;
		if (name == 'trg_on_upd') {
			let model = {...state.model};
			model.triggerOnUpdate = value;
			updateState({model : model});
		}
	},
	[ACTIONS.NOW_DROPDOWN_ITEM_CLICKED]: ({action, state, updateState}) => {
		// If Delete option clicked
		let actionId = action.payload.item.id;
		if (actionId === 'delete') {
			updateState({
				showDeleteConfirmation: true
			});
		}
		if (actionId === "create-action")
			window.open("sn_sro_act.do?sys_id=-1", "_blank");
		if (actionId === "create-config")
			window.open("sn_sro_cfg.do?sys_id=-1", "_blank");
	},
	[ACTIONS.NOW_MODAL_FOOTER_ACTION_CLICKED]: ({action, dispatch, state, updateState}) => {
		let payload = action.payload;
		if (payload.action.id == 'delete') {
			dispatch(ACTIONS.DELETE_INT_ACTION, {tableName: "sn_sro_int_act", sysId: state.properties.sysId});
		} else if (payload.action.id == 'cancel') {
			updateState({
				showDeleteConfirmation: false
			});
		} else if(payload.action.id == 'condition_builder_modal_save') {
			const conditionBuilder = {...state.condition_builder};
			let data = { ...conditionBuilder };
			const cndPayload = {tableName: 'sn_sro_cnd', data: data};
			if (state.condition_builder.sysId) {
				cndPayload.sysId = state.condition_builder.sysId;
				dispatch(ACTIONS.PUT_CND_ACTION, cndPayload);
			}
			else {
				dispatch(ACTIONS.POST_CND_ACTION, cndPayload);
			}
		} else if(payload.action.id == 'condition_builder_modal_cancel') {
			updateState({ openConditionBuilderModal: false, conditionSysId: '' });
			//Refresh condition builder modal
			var builder = {
				sysId: '',
				name: '',
				short_description: '',
				type: 'criteria',
				criteria: '',
				script: '(function( /*ExecutionContext*/ executionContext, gr) { \n\t// Add your code here and return a boolean \n\treturn false;\n})(executionContext, gr);'
			}
			updateState({condition_builder: builder});
		}
	},
	[ACTIONS.NOW_MODAL_OPENED_SET]: ({action, state, updateState}) => {
		updateState({ openConditionBuilderModal: false });
		updateState({ showDeleteConfirmation: false });
		//Refresh condition builder modal
		var builder = {
			sysId: '',
			name: '',
			short_description: '',
			type: 'criteria',
			criteria: '',
			script: '(function( /*ExecutionContext*/ executionContext, gr) { \n\t// Add your code here and return a boolean \n\treturn false;\n})(executionContext, gr);'
		}
		updateState({condition_builder: builder});
	},
	[ACTIONS.DELETE_INT_ACTION]: deleteIntAction,
	[ACTIONS.DELETE_INT_ACTION_SUCCESS]: ({state, action, dispatch, updateState}) => {
		updateState({
			showDeleteConfirmation: false
		});
		dispatch(ACTIONS.RECORD_STATUS_CHANGED, {
			table: 'sn_sro_int_act',
			sys_id: state.properties.sysId,
			status: 'closed'
		});
	},
	[ACTIONS.DELETE_INT_ACTION_ERROR]: ({action, state, updateState}) => {
	},
	[ACTIONS.NOW_RADIO_BUTTONS_SELECTED]: ({state, action, dispatch, updateState}) => {
		if (action.payload.value == 'criteria') {
			let conditionBuilder = {...state.condition_builder};
			conditionBuilder.type = 'criteria';
			updateState({
				condition_builder: conditionBuilder
			});
		}
		else if (action.payload.value == 'script') {
			let conditionBuilder = {...state.condition_builder};
			conditionBuilder.type = 'script';
			updateState({
				condition_builder: conditionBuilder
			});
		}
	},
	[ACTIONS.NOW_TEXTAREA_INPUT_CHANGED]: ({state, action, updateState}) => {
		const {name, fieldValue} = action.payload;
		if(name == 'condition_builder_script_field'){
			let conditionBuilder = {...state.condition_builder};
			conditionBuilder.script = fieldValue;
			updateState({
				condition_builder: conditionBuilder
			});
		}
	}
};

export default actionHandlers;