import $ from './make-graph-object';
import {
	GraphLinksModel,
	TreeLayout
} from 'gojs';

export function setDiagramModel(diagram, model) {
	diagram.model = new GraphLinksModel(model.nodes, model.edges);
};

export function setDiagramScale(diagram, scale) {
	diagram.scale = scale;
};

export function setZoomToFit(diagram) {
	diagram.zoomToRect(diagram.documentBounds);
}

export function setDiagramLayout(diagram, view) {
	if (view == "horizontal")
		diagram.layout = $(TreeLayout, {angle: 0});
	else
		diagram.layout = $(TreeLayout, {angle: 90, arrangement: TreeLayout.ArrangementHorizontal});
}

export function updateDiagramNode(diagram, key, property, value) {
	var node = diagram.findNodeForKey(key);
	console.log(node);
	
	// Model.commit executes the given function within a transaction
	diagram.model.commit(function(m) {  // m == the Model
  
	  // This is the safe way to change model data.
	  // GoJS will be notified that the data has changed
	  // so that it can update the node in the Diagram
	  // and record the change in the UndoManager.
	  m.set(node.data, property, value);
  
	}, "change data");
}