import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import '@servicenow/now-record-field-lookup-connected';
import '@devsnc/sn-record-input';
import '@servicenow/now-checkbox';
import '@devsnc/sn-glide-form-controls';
import { ACTIONS } from './constants';
import { t } from 'sn-translate';
import '@servicenow/now-condition-builder';
import '@servicenow/now-alert';
import '@servicenow/now-radio-buttons';
import '../sn-sr-ops-ar-conditions';
import '../sn-sr-ops-ar-actions';

function renderMessage(state, dispatch)  {
	return (
		<div className="message-container">
			<now-alert-list configAriaLive="polite" items={state.messageList}></now-alert-list>
		</div>
	)
}

function renderDeleteConfirmationModal(state, updateState, dispatch) {
	return (
		<now-modal size='sm' 
			opened={state.showDeleteConfirmation} 
			header-label={t('Are you sure you want to delete?')}
			content={t('This will erase your alert rule.')}
			manage-opened
			footer-actions={[
				{
					"id": "delete",
					"label": "Delete",
					"variant": "primary-negative"
				},
				{
					"id": "cancel",
					"label": "Cancel",
					"variant": "secondary"
				}
			]}>
		</now-modal>
	)
}

function renderHeader(state, updateState, dispatch) {
	let isNewRecord = state.properties.isNewRecord;
	let headerTitle = isNewRecord ? t('New Alert Rule') : t('Alert Rule');
	let moreUIActions = [
		{
			"id": "create-action",
			"label": t("Create Action")
		},
		{
			"id": "create-config",
			"label": t("Create Configuration")
		}
	];
	if (!state.properties.isNewRecord && !state.isLegacyRule)
		moreUIActions.push({
			"id": "delete",
			"label": t("Delete")
		});

	return (
		<div className="header-row">
			<div className="header-title">
				<h1 className="title">{headerTitle}</h1>
			</div>
			<div className="header-actions">
				{renderDeleteConfirmationModal(state, updateState, dispatch)}
				<div className="header-actions-container">
					{state.isLegacyRule ? '' : 
						<div className="header-actions-fixed">
							<now-button
								label={t('Save')}
								variant="secondary"
								size="md" on-click={ () => {
									dispatch(ACTIONS.SAVE_BTN_CLICKED);
								}}></now-button>
						</div>
					}
					<div className="header-actions-overflow">
						<now-dropdown 
							items={moreUIActions} 
							selectedItems={[]} 
							select="none" 
							placeholder="" 
							icon="ellipsis-h-outline" 
							variant="secondary" 
							size="md" 
							bare={false} 
							hideCaret={true} 
							tooltipContent={t('More UI Actions')} 
							panelFitProps={{}} 
							configAria={{
								trigger: {'aria-label': t('More UI Actions')}
							}}
							manage-selected-items>
						</now-dropdown>
					</div>
				</div>
			</div>
		</div>
	)
}

function renderDetails(state, updateState, dispatch, properties) {
	let dropdownItems = [
		{value: 'cmdb_ci_service', displayValue: t('Service')},
		{value: 'sn_sro_em_integration', displayValue: t('Integration')}
	];

	let svcDropdownItems = [
		{value: 'service', displayValue: t('Service')},
		{value: 'ci', displayValue: t('CI')},
		{value: 'both', displayValue: t('Service + CI')}
	];

	return (
		<div className="details-container">
			<div className="header">
				<span>{t('Details')}</span>
			</div>
			<div className="body">
				<div className="field">
					{state.model.type == 'cmdb_ci_service' ?
						<sn-record-choice
							append-to-payload={{name: 'svc-trigger-type-dropdown'}}
							label={t('Service Trigger Type')}
							choices={svcDropdownItems}
							size='sm'
							variant='inherit'
							manage-selected-items
							bare={true}
							display-value={state.model.svcTriggerTypeDisplayValue}
							value={state.model.svc_trg_type}
							config-aria={{'aria-label': t('Service Trigger Type')}}
							onValueChange={e => {
								dispatch(
									ACTIONS.SVC_TRIGGER_TYPE_UPDATED, {"item":{"value":e.value, "displayValue":e.displayValue},"name":"svc-trigger-type-dropdown"}
								);
							}}
						/> :
<sn-record-choice
							append-to-payload={{name: 'trigger-type-dropdown'}}
							label={t('Trigger Type')}
							choices={dropdownItems}
							size='sm'
							variant='inherit'
							manage-selected-items
							bare={true}
							display-value={state.model.typeDisplayValue}
							value={state.model.type}
							config-aria={{'aria-label': t('Trigger Type')}}
							readonly={true}
							onValueChange={e => {
								dispatch(
									ACTIONS.TRIGGER_TYPE_UPDATED, {"item":{"value":e.value, "displayValue":e.displayValue},"name":"trigger-type-dropdown"}
								);
							}}
						/>
					}
				</div>
				{state.model.type == 'cmdb_ci_service' ?
					<div className="field">
						<now-record-field-lookup-connected
							label={t('Service')}
							table-name="cmdb_ci_service"
							field-name="name"
							value={state.model.service}
							display-value={state.model.serviceDisplayValue}
							required={true}
							readonly={true}
							onValueChange={({ value, displayValue }) => {
								let model = {...state.model};
								model.service = value;
								model.serviceDisplayValue = displayValue;
								updateState({
									model: model
	                            });
							}}
						/>
					</div>
				:
					<div className="field">
						<now-record-field-lookup-connected
							label={t('Integration')}
							tableName="sn_sro_em_integration"
							fieldName="name"
							value={state.model.integration}
							displayValue={state.model.integrationDisplayValue}
							required={true}
							readonly={true}
							onValueChange={({ value, displayValue }) => {
								let model = {...state.model};
								model.integration = value;
								model.integrationDisplayValue = displayValue;
								updateState({
									model: model
	                            });
							}}
						/>
					</div>
				}
				<now-checkbox label="Trigger on Alert Update"
								  checked={state.model.triggerOnUpdate}
								  configAria={{}}
								  manage-checked={true}
								  value="false"
								  name="trg_on_upd"
								  hidden
								  aria-hidden="true"
				></now-checkbox>
				{state.model.triggerOnUpdate == false ? 
					<div className="field">
						<sn-record-input required={!state.model.triggerOnUpdate} value={state.model.order} type="number" step="100" name="order" label={t("Order")}
							readonly={state.isLegacyRule}
							onValueChange={({ value, displayValue, error }) => {
								let model = {...state.model};
								model.order = value;
								updateState({
									model: model
	                            });
	                        }}
					 	 />
					</div>
				: 
					''
				}
			</div>
		</div>
	)
}

function renderConditions(state, updateState, dispatch) {
	return (
		<div>
			{renderConditionBuilderModal(state, updateState, dispatch)}
			<div className="conditions-container">
				<div className="header">
					<span>{t('Condition(s)')}</span>
					{state.isLegacyRule ? '' :
						<div className="new-condition-button">
							<now-button label={t("Create custom condition")} variant="secondary" size="md" 
								on-click={(e) => dispatch(ACTIONS.CONDITION_BUILDER_OPEN_MODAL) } >
							</now-button>
						</div>
					}
				</div>
				<div className="body">
					<sn-sr-ops-ar-conditions conditions={state.model.condition} isLegacyRule={state.isLegacyRule} refresh={state.refreshConditions} isNewAlertRule={state.properties.isNewRecord}/>
				</div>
			</div>
		</div>
	)
}

function renderConditionBuilderModal(state, updateState, dispatch) {
	if (state.openConditionBuilderModal) {
		const openModal = true == state.openConditionBuilderModal;
		const saveButton = {
			"id": "condition_builder_modal_save",
			"variant": "primary",
			"label": t("Save")
		};
		saveButton.disabled = (false == (state.condition_builder.name && state.condition_builder.name.length > 0)) || (state.isLegacyRule == true);
		const modalActions = [saveButton,
			{
				"id": "condition_builder_modal_cancel",
				"variant": "secondary",
				"label": t("Cancel")
			}];
		return (
			<now-modal size='lg' opened={openModal} header-label={state.conditionSysId ? t('Condition') : t('New custom condition')} footer-actions={modalActions}>
				<div className="field">
					<sn-record-input required={true} value={state.condition_builder.name} type="text" name="name" label={t("Name")}
						readonly={state.isLegacyRule}
						on-blur={(e) => {
							const cmp = e.target;
							const cmpValue = cmp.value;
							cmp.invalid = false;
							if(!cmpValue || null == cmp.value || '' == cmp.value) {
								cmp.invalid = true;
							}
						}}
						onValueChange={({ value, displayValue, error }) => {
							let conditionBuilder = {...state.condition_builder};
							conditionBuilder.name = value;
							updateState({
								condition_builder: conditionBuilder
							});
						}}
					/>
				</div>
				<div className="field">
					<sn-record-input required={false} value={state.condition_builder.short_description} type="text" name="short_description" label={t("Short description")}
						readonly={state.isLegacyRule}
						onValueChange={({ value, displayValue, error }) => {
							let conditionBuilder = {...state.condition_builder};
							conditionBuilder.short_description = value;
							updateState({
								condition_builder: conditionBuilder
							});
						}}
					/>
				</div>
				<div className="field">
						<div className="condition-type">
							<now-radio-buttons
								label={t('Mode')}
								required={true}
								options={[{id: 'criteria', label: t('Criteria'), checked: state.condition_builder.type == 'criteria' ? true : false},
								{id: 'script', label: t('Script'), checked: state.condition_builder.type == 'script' ? true : false}]}
								layout="horizontal"
								readonly={state.isLegacyRule} />
						</div>
				</div>
				<div className="builder">
					{renderConditionBuilderTab(state, updateState, dispatch)}
				</div>
			</now-modal>
		);
	}
}

function renderConditionBuilderTab(state, updateState, dispatch) {
	const conditionBuilder = {...state.condition_builder};
	const cndType = conditionBuilder.type ?  conditionBuilder.type : 'criteria';
  if ("criteria" == cndType) {
		 return (<now-condition-builder
							tableName="em_alert"
							encodedQuery={state.condition_builder.criteria}
							hideRelatedListQueryConditions={true}
							readonly={state.isLegacyRule} />);
	} else  if ("script" == cndType) {
		return (<now-textarea
					autoresize={true}
					rows="6" 
					name="condition_builder_script_field"
					showBorders={true}
					value={state.condition_builder.script}
					readonly={state.isLegacyRule}
				/>);
	}   
}

function renderActions(state, updateState, dispatch) {
	return (
		<div className="actions-container">
			<div className="header">
				<span>{t('Action')}</span>
			</div>
			<div className="body">
				<sn-sr-ops-ar-actions actionId={state.model.action} configId={state.model.configuration} 
					configDisplayValue={state.model.configurationDisplayValue}
					isLegacyRule={state.isLegacyRule}>
				</sn-sr-ops-ar-actions>
			</div>
		</div>
	)
}

export default function view(state, {updateState, dispatch}) {
	return (
		<div className="create-int-action">
			<div className="header-container">
				{renderHeader(state, updateState, dispatch)}
				<div>
					{state.messageList && state.messageList.length > 0 ? renderMessage(state, dispatch): null}
				</div>
			</div>
			<div className="body-container">
				<div className="body-details-container">
					{renderDetails(state, updateState, dispatch)}
				</div>
				<div className="body-conditions-container">
					{renderConditions(state, updateState, dispatch)}
				</div>
				<div className="body-actions-container">
					{renderActions(state, updateState, dispatch)}
				</div>
			</div>
		</div>
	)
}
