import {createCustomElement} from '@servicenow/ui-core';
import {snabbdom} from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-sr-ops-services.scss';
import {renderPage, renderNoServiceMessage, renderServiceMap} from './view';
import {overviewHandlers} from './actionHandlers';

const view = (state, coeffects) => {
	const {servicesCount, unassignedServicesCount, showServiceMap, hasLoaded} = state;
	let totalCount = unassignedServicesCount + servicesCount;
	if (showServiceMap) 
		return renderServiceMap(state, coeffects);
	
	if (hasLoaded) {
		if (totalCount > 0)
			return renderPage(state, coeffects);
		else
			return renderNoServiceMessage(state, coeffects);
	}
	return null;
};

createCustomElement('sn-sr-ops-services', {
	renderer: {type: snabbdom},
	view,
	initialState : {
		userId: '',
		services: [],
		serviceName: "",
		alerts: 0,
		incidents: 0,
		changes: 0,
		servicesCount: 0,
		hideNewAction: true,
		refreshPayload: {},
		refreshAlertReport: Date.now(),
		refreshIncidentReport: Date.now(),
		refreshChangeReport: Date.now(),
		selectedTab: 'myServices',
		alertDetails: [{
			table: "em_alert",
			listId: "c1c9e1e6c7b15010d721fff1c7c260ac"
		}],
		incidentDetails: [{
			table: "incident",
			listId: "1d6ae52ac7b15010d721fff1c7c260d6"
		}],
		changeDetails: [{
			table: "change_request",
			listId: "31d7e13d5b331010ca4e551a5d81c748"
		}]
	},
	styles,
	actionHandlers : overviewHandlers
});


export default 'sn-sr-ops-services';