import '@servicenow/now-card';
import '@servicenow/now-icon';
import '@servicenow/now-dropdown';
import '@servicenow/now-highlighted-value';
import { t } from 'sn-translate';
import { PRIORITY_MAP } from './constants';

function renderIncident(incident, state, dispatch) {
	return (
		<div className="incident">
			<now-card size="lg" 
			configAria={{
				'aria-label': t('Incident: {0}, {1}, Priority: {2}, Last updated: {3}', incident.number.value , incident.short_description.displayValue, incident.priority.displayValue, incident.sys_updated_on.displayValue)
			}} 
			append-to-payload={{incident: incident, type: 'incident-card'}} 
			interaction="click">
				<now-card-header
					tagline={{label: incident.number.value, variant: "secondary"}}
					>
					<div slot="metadata">
						<now-highlighted-value label={incident.priority.displayValue} icon="" status={PRIORITY_MAP[incident.priority.value]}></now-highlighted-value>
					</div>
				</now-card-header>
				<div className="card-body">
					<h3 className="short-description">{incident.short_description.displayValue}</h3>
					<div className="row">
						<div className="column">
							<div className="label">
								{t('Configuration item')}
							</div>
							<div>
								{incident.cmdb_ci.displayValue}
							</div>
						</div>
						<div className="column">
							<div className="label">
								{t('Assigned to')}
							</div>
							<div>
								{incident.assigned_to.displayValue}
							</div>
						</div>
					</div>
					<div className="last-event-time">
						<span>{t('Last updated: ')}</span>
						<span>{incident.sys_updated_on.displayValue}</span>
					</div>
				</div>
			</now-card>
		</div>
	);
}
function renderIncidents(state, dispatch) {
	const incidentsTemplate = [];
	state.incidents && state.incidents.forEach((incident, index) => {
		incidentsTemplate.push(renderIncident(incident, state, dispatch));
	});
	return (
		<div className="incidents-wrapper">
			<div className="incidents-flex">
				{incidentsTemplate}
				<div className="incident incident--filler" />
				<div className="incident incident--filler" />
				<div className="incident incident--filler" />
				<div className="incident incident--filler" />
			</div>
		</div>
	)
}

export default function view(state, {updateState, dispatch}) {
	let dropdownItems = [
		{id: 'incident_priority', label: t('Incident priority')}, 
		{id: 'incident_updated_desc', label: t('Incident updated time (desc.)')},
		{id: 'incident_updated_asc', label: t('Incident updated time (asc.)')}
	];
	return (
		<div class={{
			incidents: true,
			'incidents--context-menu': state.properties.service
		}}>
			<div className="incidents__header">
				<h2 className="now-heading -header -secondary has-no-margin">{t('Incidents')}</h2>
				<now-button-iconic
					icon="sync-outline"
					variant="tertiary"
					bare
					hidePadding
					size="md"
					configAria={{
						button: {'aria-label': t('Refresh Incidents')}}
					}
					tooltipContent={t('Refresh Incidents')}
					append-to-payload={{action: 'refresh-incidents'}}
				/>
			</div>
			<div className="sort-wrapper">
				<div className="sort-by">{t('Sort by')}</div>
				<now-dropdown 
					items={dropdownItems}
					className='dropdown-sort'
					size='sm'
					variant='tertiary'
					manage-selected-items
					selected-items={[state.sortCriteria]}
				></now-dropdown>
			</div>
			{renderIncidents(state, dispatch)}
			<div className="total-count">
				{t(
					'Showing {0} - {1} out of {2}',
					state.totalIncidents > 0 ? 1 : 0,
					state.totalIncidents <= 20 ? state.totalIncidents : 20,
					state.totalIncidents
				)}
			</div>
		</div>
	);
}
