import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { ACTIONS } from './constants';

const fetchIncidentQuery = `query($queryConditions: String!) {
	GlideRecord_Query {
	incident(queryConditions: $queryConditions) {
		_results {
			sys_id {
				value
			}
			number {
				value
			}
			short_description {
				displayValue
			}
			cmdb_ci {
				value
				displayValue
			}
			assigned_to {
				value
				displayValue
			}
			sys_updated_on {
				displayValue
			}
			priority {
				value
				displayValue
			}
		}
	}
	}
	GlideAggregateRecord_Query(tableName: "incident", queryConditions: $queryConditions) {
		aggregates{
		  count
		}
	}
}`;

export const fetchIncidents = createGraphQLEffect(fetchIncidentQuery, {
	variableList: ['queryConditions'],
	successActionType: ACTIONS.FETCH_INCIDENT_SUCCESS,
	errorActionType: ACTIONS.FETCH_INCIDENT_ERROR
});
