import '@servicenow/now-button';
import '@servicenow/now-card';
import '@servicenow/now-icon';
import '@servicenow/now-dropdown';
import '@servicenow/now-highlighted-value';
import {t} from 'sn-translate';
import {SEVERITY_MAP} from './constants';

function renderAlert(alert, state, dispatch) {
	let severity = (alert.severity && alert.severity.displayValue) ? alert.severity.displayValue : '';
	let last_event_time = (alert.last_event_time && alert.last_event_time.displayValue) ? alert.last_event_time.displayValue : '';
	let description = (alert.description && alert.description.displayValue) ? alert.description.displayValue : '';
	return (
		<div className="alert">
			<now-card 
			size="lg" 
			configAria={{
				'aria-label': t('Alert: {0}, {1}, Severity: {2}, Last event: {3}', alert.number.value ,description, severity, last_event_time)
			}} 
			append-to-payload={{alert, type: 'alert-card'}} 
			className="alert-card"
			interaction="click">
				<now-card-header
					tagline={{label: alert.number.value, variant: 'secondary'}}
				>
					<div slot="metadata"> {alert.severity && alert.severity.displayValue ? 
						<now-highlighted-value label={alert.severity.displayValue} icon="" status={SEVERITY_MAP[alert.severity.displayValue]}></now-highlighted-value>
						: ''}
					</div>
				</now-card-header>
				<div className="card-body">
					<h3 className="short-description">{alert.description.displayValue}</h3>
					<div className="row">
						<div className="column">
							<div className="label">
								{t('Configuration item')}
							</div>
							<div>
								{alert.cmdb_ci.displayValue}
							</div>
						</div>
						<div className="column">
							<div className="label">
								{t('Assigned to')}
							</div>
							<div>
								{alert.assigned_to.displayValue}
							</div>
						</div>
					</div>
					<div className="last-event-time">
						<span>{t('Last event: ')}</span>
						<span>{alert.last_event_time.displayValue}</span>
					</div>
				</div>
			</now-card>
		</div>
	);
}
function renderAlerts(state, dispatch) {
	const alertsTemplate = [];
	state.alerts && state.alerts.forEach((alert, index) => {
		alertsTemplate.push(renderAlert(alert, state, dispatch));
	});
	return (
		<div className="alerts-wrapper">
			<div className="alerts-flex">
				{alertsTemplate}
				<div className="alert alert--filler" />
				<div className="alert alert--filler" />
				<div className="alert alert--filler" />
				<div className="alert alert--filler" />
			</div>
		</div>
	);
}

export default function view(state, {dispatch}) {
	const dropdownItems = [
		{id: 'alert_priority', label: t('Alert priority (urgent first)')}, 
		{id: 'severity', label: t('Severity (critical first)')}, 
		{id: 'alert_updated_desc', label: t('Alert updated time (desc.)')},
		{id: 'alert_updated_asc', label: t('Alert updated time (asc.)')}
	];
	return (
		<div class={{
			alerts: true,
			'alerts--context-menu': state.properties.service
		}}>
			<div className="alerts__header">
				<h2 className="now-heading -header -secondary has-no-margin">{t('Alerts')}</h2>
				<now-button-iconic
					icon="sync-outline"
					variant="tertiary"
					bare
					hidePadding
					size="md"
					configAria={{
						button: {'aria-label': t('Refresh Alerts')}}
					}
					tooltipContent={t('Refresh Alerts')}
					append-to-payload={{action: 'refresh-alerts'}}
				/>
			</div>
			<div className="sort-wrapper">
				<div className="sort-by">{t('Sort by')}</div>
				<now-dropdown 
					items={dropdownItems}
					className='dropdown-sort'
					size='sm'
					variant='tertiary'
					manage-selected-items
					selected-items={[state.sortCriteria]}

				></now-dropdown>
			</div>
			{renderAlerts(state, dispatch)}
			<div className="total-count">
				{t(
					'Showing {0} - {1} out of {2}',
					state.totalAlerts > 0 ? 1 : 0,
					state.totalAlerts <= 20 ? state.totalAlerts : 20,
					state.totalAlerts
				)}
			</div>
		</div>
	);
}
