import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import '@servicenow/now-icon';
import '@servicenow/now-alert';
import '@servicenow/now-toggle';
import '@servicenow/now-modal';
import '@servicenow/now-text-link';
import '@devsnc/sn-glide-form-controls';
import '@devsnc/sn-declarative-actions';

import { t } from 'sn-translate';

import '@servicenow/now-record-field-lookup-connected';
import { ACTIONS, SERVICE_TYPES } from './constants';
import '../sn-sr-ops-node-map';

function renderPopover(state, dispatch) {
	let cls = ["settings-popover"];
	return (
		<div className={cls.join(" ")} on-keydown={(event) => {
			dispatch(ACTIONS.POPOVER_KEYDOWN, {event});
		}}>
			<div className="section-title">{t('Show in map')}</div>
			<div className="section-container">
				<span className="text">{t('Service offering')} </span>
				<span className="action"><now-toggle size="md" checked={state.showServiceOffering} append-to-payload={{name: 'service-offering'}} configAria={{'aria-label': t('Service offering')}}></now-toggle></span>
			</div>
		</div>
	)
}

function renderNewService(state, dispatch, updateState) {

	let dropdownItems = [
		{value: 'cmdb_ci_service_discovered', displayValue: t('Application Service')},
		{value: 'cmdb_ci_service', displayValue: t('Business Service')}
	];
	let label;
	if (state.class && state.class == 'cmdb_ci_service') {
		label = t('Business Service');
	} else {
		label = t('Application Service');
	}
	let cls = ["trigger-content"];
	if (state.sys_id != "" || state.showBusinessService == false) {
		cls.push("is-disabled");
	}
	let readOnlyCls = [];
	if (state.sys_id != "")
		readOnlyCls.push("input-read-only");
	return (
		<div className="new-service">
			<div className="label">{t("Service type")}</div>
			<sn-record-choice
				append-to-payload={{name: 'class-dropdown'}}
				choices={dropdownItems}
				readonly={state.sys_id != "" || state.showBusinessService == false ? true: false}
				className='dropdown-service'
				size='sm'
				variant='inherit'
				manage-selected-items
				bare={true}
				displayValue={[state.class == 'cmdb_ci_service_discovered'? 'Application Service': 'Business Service']}
				value={[state.class ? state.class: 'cmdb_ci_service_discovered']}
				configAria={{'aria-label': t('Service type: {0}', label)}}
				onValueChange={e => {
					dispatch(
						ACTIONS.SERVICE_TYPE_UPDATED,
						{"item":{"value":e.value,"displayValue":e.displayValue},"name":"class-dropdown"}
					);
				}}>
				<span slot="trigger-content" className={cls.join(' ')} >
				<span className="label">{label}</span>
				</span>
			</sn-record-choice>
			<label className="label" htmlFor="name">{t("Name")} <now-icon icon="asterisk-outline" size="sm"></now-icon></label>
			<input className={readOnlyCls.join(' ')} type="text" id="name" value={state.name} required readonly={state.sys_id !="" ? true: false} on-change={ (event) => {
				dispatch(ACTIONS.INTERNAL_STATE_SET, {name: event.target.value});
			}}/>
			<label className="label" htmlFor="description">{t("Description")}</label>
			<input className={readOnlyCls.join(' ')} type="text" id="description" value={state.description} readonly={state.sys_id !="" ? true: false} on-change={ (event) => {
				dispatch(ACTIONS.INTERNAL_STATE_SET, {description: event.target.value});
			}}/>
			<div className="label">{t("Owned By")}</div>
			<now-record-field-lookup-connected
				tableName='sys_user'
				fieldName={'name'}
				value={state.ownedBy}
				displayValue={state.ownedByDisplayValue}
				readonly={state.sys_id !="" ? true: false}
				onValueChange={({ value, displayValue }) => {
					updateState({
						ownedBy: value,
						ownedByDisplayValue: displayValue
					});
				}}
			/>
			<div className="label">{t("Support Team")}</div>
			<now-record-field-lookup-connected
				tableName='sys_user_group'
				fieldName={'name'}
				value={state.supportGroup}
				displayValue={state.supportGroupDisplayValue}
				readonly={state.sys_id !="" ? true: false}
				onValueChange={({ value, displayValue }) => {
					updateState({
						supportGroup: value,
						supportGroupDisplayValue: displayValue
					});
				}}
			/>
		</div>
	)
}

function renderExistingService(state, dispatch) {
	let dropdownItems = [
		{value: 'cmdb_ci_service_discovered', displayValue: t('Application Service')},
		{value: 'cmdb_ci_service', displayValue: t('Business Service')}
	];
	let table = state.class ? state.class: 'cmdb_ci_service_discovered';
	let label;
	if (state.class && state.class == 'cmdb_ci_service') {
		label = t('Business Service');
	} else {
		label = t('Application Service');
	}
	let cls = ["trigger-content"];
	if (state.sys_id != "" || state.showBusinessService == false) {
		cls.push("is-disabled");
	}
	let readOnlyCls = [];
	if (state.sys_id != "")
		readOnlyCls.push("input-read-only");
	return (
		<div className="existing-service">
			<div className="label">{t("Service type")}</div>
				<sn-record-choice
					append-to-payload={{name: 'class-dropdown'}}
					choices={dropdownItems}
					readonly={state.sys_id != "" || state.showBusinessService == false ? true: false}
					className='dropdown-service'
					size='sm'
					variant='inherit'
					manage-selected-items
					bare={true}
					displayValue={[state.class == 'cmdb_ci_service_discovered'? 'Application Service': 'Business Service']}
					value={[state.class ? state.class: 'cmdb_ci_service_discovered']}
					configAria={{'aria-label': t('Service type: {0}', label)}}
					onValueChange={e => {
						dispatch(
							ACTIONS.SERVICE_TYPE_UPDATED,
							{"item":{"value":e.value,"displayValue":e.displayValue},"name":"class-dropdown"}
						);
					}}>
					<span slot="trigger-content" className={cls.join(' ')} >
					<span className="label">{label}</span>
					</span>
				</sn-record-choice>
			<div>
				<div className="label">{t("Service")} <now-icon icon="asterisk-outline" size="sm"></now-icon></div>
				{state.class == 'cmdb_ci_service' ?
						<sn-record-reference-connected
							fieldName="business_service"
							tableName="sn_sro_service_map"
							recordSysId="-1"
							name="sn_sro_service_map"
							value={state.serviceLookup ? state.serviceLookup.value: null}
							displayValue={state.serviceLookup ? state.serviceLookup.displayValue: null}
							fieldType="reference"
						/>
					:<div>
						<sn-record-reference-connected
							fieldName="application_service"
							tableName="sn_sro_service_map"
							recordSysId="-1"
							name="sn_sro_service_map"
							value={state.serviceLookup ? state.serviceLookup.value: null}
							displayValue={state.serviceLookup ? state.serviceLookup.displayValue: null}
							fieldType="reference"
						/>
					</div>
				}
			</div>
		</div>
	)
}
function renderMessage(state, dispatch)  {
		return (
			<div className="message-container">
				<now-alert-list configAriaLive="polite" items={state.messageList}></now-alert-list>
			</div>
		)
}

function renderServiceSelection(state, dispatch) {

	return (
		<div>
			<div className="label label-no-margin">
				<span>{t('Start with')}</span>{' '}
				<now-icon icon="asterisk-outline" size="sm" />
			</div>

			<div className="radio-with-label">
				<input className="radio-style" type="radio" id="existing_service" name="service_type" value="existing_service" disabled={state.sys_id !="" ? true: false} checked={state.serviceType == "existing_service" ? true: false} on-click={ () => {
					dispatch(ACTIONS.INTERNAL_STATE_SET, {serviceType: 'existing_service'});
				}}/>
				<label className="radio-label" htmlFor="existing_service">{t('Existing Service')}</label>
			</div>

			<div className="radio-with-label">
				<input className="radio-style-new-service" type="radio" id="new_service" name="service_type" value="new_service" disabled={state.sys_id !== "" ? true: false} checked={state.serviceType == "new_service" ? true: false} on-click={ () => {
					dispatch(ACTIONS.INTERNAL_STATE_SET, {serviceType: 'new_service'});
				}}/>
				<label className="radio-label" htmlFor="new_service">{t('New Service')}</label>
			</div>
		</div>
	)
}

function renderRightPane(state, dispatch, updateState) {
	return (
		<div className="right-pane-container">
			{
				state.selectedNode ?
					<h2 className="right-pane-heading">{state.selectedNode.name}</h2>
					: ''
			}
			<div className="right-pane-container-scroll">
				{renderServiceSelection(state, dispatch)}
				{ state.serviceType === "new_service" ? renderNewService(state, dispatch, updateState) : null }
				{ state.serviceType === "existing_service" ? renderExistingService(state, dispatch) : null }
			</div>
			{	state.serviceType === "new_service" || state.serviceType === "existing_service" ?
				<div className="button-wrapper">
					<now-button
					label={state.serviceType === "new_service" ? t("Update service") : t("Add")}
					variant="primary"
					size="md" on-click={ () => {
						const actionName =
							state.serviceType === "existing_service" ? ACTIONS.ADD_EXISTING_SERVICE : ACTIONS.ADD_SERVICE; 
						
						dispatch(actionName);
					}}></now-button>
				</div>
				: ''
			}
		</div>
	);
}

function renderLeftPane(state, dispatch) {
	if (state.serviceLoading == false)
		return renderLeftPaneContent(state, dispatch)
	else
		return null;
}

function renderLeftPaneContent(state, dispatch) {
	let model = {...state.model};
	let isEdit = state.properties.isEdit;
	let sysId = state.properties.serviceId ? state.properties.serviceId : state.properties.sysId;
	const showRefresh = null != sysId && "" != sysId && "-1" != sysId && "undefined" != sysId;
	let menu = [...state.menu];
	if (isEdit != "true" && isEdit != true) {
		menu = menu.filter((menuItem) => {return menuItem.showOnReadOnlyView != undefined && menuItem.showOnReadOnlyView == true;});
  	}
	let cls = ["node-map-wrapper"];
	if (state.moreServices) {
		cls.push("view-more-visible");
	}
	return (
		<div className="left-pane-container">
			{model.nodes && model.nodes.length > 0 ?
			<div className="node-map-container">
				<div className={cls.join(" ")}>
					<sn-sr-ops-node-map model={model} is-edit={state.properties.isEdit} menu={menu} shouldRefocusInDiagram={state.shouldRefocusInDiagram}>
						<div className="left-pane-control" slot="top-left-slot">
							<span className="settings-popover-button">
								<now-button-iconic
									icon="sliders-fill"
									tooltipContent={t("Settings")}
									size="sm"
									configAria={{"aria-label":"Settings"}}
									on-click={ () => {
										dispatch(ACTIONS.SHOW_SETTINGS_POPOVER);
									}}>
								</now-button-iconic>
							</span>
							{state.isSettingsPopoverVisible ? renderPopover(state, dispatch): null}
							{showRefresh ?
								<span className="refresh-popover-button">
									<now-button-iconic
										icon="sync-outline"
										tooltipContent={t("Refesh")}
										size="sm"
										configAria={{"aria-label":"Refresh"}}
										on-click={ () => {
											dispatch(ACTIONS.REFRESH);
										}}>
									</now-button-iconic>
								</span> : null }
						</div>
						<div slot="top-right-slot">
							{isEdit ?
								<div className="add-service-button">
									<now-button-bare
										label={t('Add service')}
										tooltipContent={t("Add service")}
										configAria={{"aria-label":"Add service"}}
										on-click={ () => {
											dispatch(ACTIONS.CREATE_SERVICE_NODE);
										}}>
									</now-button-bare>
								</div>
								:
								<div className="add-service-button">
									<a className="link-text" href="javascript:void(0);" on-click={(e) => dispatch(ACTIONS.EDIT_RELATIONSHIP) }>
										<span className="edit-relationship">{t('Edit relationships')} <now-icon icon="open-link-outline" size="md"></now-icon></span>
									</a>
								</div> }
						</div>
					</sn-sr-ops-node-map>
				</div>

				{state.moreServices ?
					<div className="view-more">
						<a className="view-more-link" href="javascript:void(0);" on-click={(e) => dispatch(ACTIONS.VIEW_MORE) }>
							{t('Show next level of relationships')}
						</a>
					</div>
				: null }
			</div>
			: null }

			{isEdit && (!model.nodes || model.nodes.length == 0) ?
			<div className="no-service">
				<div className="no-service-container">

					<div className="sn-empty-state-image"></div>
					<div className="text1">{t('Start with a parent service.')}</div>
					<div className="text2">{t('You can connect existing services or create new ones, map dependencies, and complete setting up added services later.')}</div>
					<div className="add-service-button">
						<now-button label={t('Add service')} on-click={(e) => dispatch(ACTIONS.CREATE_SERVICE_NODE) } bare="true"></now-button>
					</div>
				</div>
			</div>
			: null}
		</div>
	)
}

function renderDeleteConfirmationModal(state, updateState, dispatch) {
	let deleteMessage;
	if (state.showDeleteConfirmation) {
		deleteMessage = t('All associated integrations and relationships will be permanently deleted.You will not receive any alert notifications. Are you sure you want to delete the service?');
	} else if (state.showDeleteOfferingConfirmation) {
		deleteMessage = t('Deleting a service offering removes the service offering and its relationships permanently. Are you sure you want to delete the service offering?');
	}
	return (
		<div className="modal-delete">
			<div>
				<p>{deleteMessage}</p>
			</div>
			<br/>
			<div className="button-row">
				<div className="cancel-delete">
					<div className="cancel-delete-btn">
						<now-button label={t("Cancel")} variant="secondary" size="md" configAria={{}}
							on-click={() => dispatch(ACTIONS.MODAL_CANCEL_BTN_CLICKED)}
						></now-button>
					</div>
					<div className="cancel-delete-btn">
						<now-button label={t("Delete")} variant="primary-negative" size="md" configAria={{}}
							on-click={() => dispatch(ACTIONS.MODAL_DELETE_BTN_CLICKED)}
						></now-button>
					</div>
				</div>
			</div>
		</div>
	)
}

function renderManageRelationshipModal(state, updateState, dispatch) {
	let parentLabel, childLabel;
	if (state.selectedParentNodes.length > 1) {
		parentLabel = t('{0} items selected', state.selectedParentNodes.length);
	} else if (state.selectedParentNodes.length == 1){
		parentLabel = state.parentNodes.filter((n) => {return n.id == state.selectedParentNodes[0]})[0].label;
	} else {
		parentLabel = t('Select Parent Relationships');
	}
	if (state.selectedChildNodes.length > 1) {
		childLabel = t('{0} items selected', state.selectedChildNodes.length);
	} else if (state.selectedChildNodes.length == 1){
		childLabel = state.childNodes.filter((n) => {return n.id == state.selectedChildNodes[0]})[0].label;
	} else {
		childLabel = t('Select Child Relationships');
	}

	let parentRelationshipsLabel = t('Select Parent Relationships');
	if (state.selectedParentNodes.length === 1)
		parentRelationshipsLabel = state.parentNodes.filter(({id}) => id === state.selectedParentNodes[0])[0].label;
	else if (state.selectedParentNodes.length > 1)
		parentRelationshipsLabel = t('{0} items selected', state.selectedParentNodes.length);

	let childRelationshipsLabel = t('Select Child Relationships');
	if (state.selectedChildNodes.length === 1)
		childRelationshipsLabel = state.childNodes.filter(({id}) => id === state.selectedChildNodes[0])[0].label;
	else if (state.selectedChildNodes.length > 1)
		childRelationshipsLabel = t('{0} items selected', state.selectedChildNodes.length);

	return (
		<div className="modal-relationships">
			<div>
				<div className="label">{t("Parent Relationships")}</div>
				<now-dropdown
					append-to-payload={{name: 'parent-relationship-dropdown'}}
					label={t('Parent relatinships')}
					items={state.parentNodes}
					className='dropdown-service'
					size='md'
					variant='inherit'
					manage-selected-items
					bare={true}
					select="multi"
					selected-items={state.selectedParentNodes}
					configAria={{'aria-label': t('Parent Relationships: {0}', parentRelationshipsLabel)}}
				>
					<span slot="trigger-content" className='trigger-content' >
						<span className="label">{parentLabel}</span>
						<now-icon className='icon' icon='caret-down-fill' />
					</span>
				</now-dropdown>
			</div>
			<div className="child-relationships">
				<div className="label">{t("Child Relationships")}</div>
				<now-dropdown
					append-to-payload={{name: 'child-relationship-dropdown'}}
					label={t('Child relatinships')}
					items={state.childNodes}
					className='dropdown-service'
					size='md'
					variant='inherit'
					manage-selected-items
					bare={true}
					select="multi"
					selected-items={state.selectedChildNodes}
					configAria={{'aria-label': t('Child Relationships: {0}', childRelationshipsLabel)}}
				>
					<span slot="trigger-content" className='trigger-content' >
						<span className="label">{childLabel}</span>
						<now-icon className='icon' icon='caret-down-fill' />
					</span>
				</now-dropdown>
			</div>
			<br/>
			<div className="button-row">
				<div className="cancel-delete">
					<div className="cancel-delete-btn">
						<now-button label={t("Cancel")} variant="secondary" size="md" configAria={{}}
							on-click={() => dispatch(ACTIONS.CANCEL_RELATIONSHIPS_MODAL)}
						></now-button>
					</div>
					<div className="cancel-delete-btn">
						<now-button label={t("Save")} variant="primary" size="md" configAria={{}}
							on-click={() => dispatch(ACTIONS.UPDATE_RELATIONSHIPS_MODAL)}
						></now-button>
					</div>
				</div>
			</div>
		</div>
	)
}

export default function view(state, {updateState, dispatch}) {
	let model = {...state.model};
	let cls = ["service-map"];
	let isEdit = state.properties.isEdit;
	if (isEdit) {
		cls.push("edit");
	}
	return (
		<div className={cls.join(" ")}
			hook-insert={(vnode) => {
				dispatch(ACTIONS.SAVE_ELEMENT, {
					element: vnode,
				});
			}}
			hook-destroy={() => {
				dispatch(ACTIONS.SAVE_ELEMENT, {
					element: null,
				});
			}}>
			{isEdit ?
			<div className="header">
				<div className="text">
					<h1 className="title">{t('Service Dependencies')}</h1>
					<div className="description">{t('Create dependency relationships between new and existing services. Complete setting up added services later.')}</div>
				</div>
				<div className="action-button">
					<now-button
					label={t('Save Relationships')}
					variant="secondary"
					size="md" on-click={ () => {
						dispatch(ACTIONS.SAVE_RELATIONSHIP);
					}}></now-button>
				</div>
			</div>
			: null }
			<div className="main-content">
				<div className="left-pane">
					{renderLeftPane(state, dispatch)}
				</div>
				{(isEdit && model.nodes.length > 0) ?
					<div className="right-pane" tabIndex="-1">
						{renderRightPane(state, dispatch, updateState)}
					</div>
				: null}
			</div>
			<div>
				{state.messageList && state.messageList.length > 0 ? renderMessage(state, dispatch): null}
			</div>
			<div>
				{state.showDeleteConfirmation ?
				<now-modal size='md' opened={state.showDeleteConfirmation} header-label={t('Delete Service?')}>
					{renderDeleteConfirmationModal(state, updateState, dispatch)}
				</now-modal>
				: null }
			</div>
			<div>
				{state.showDeleteOfferingConfirmation ? 
				<now-modal size='md' opened={state.showDeleteOfferingConfirmation} header-label={t('Delete Service Offering?')}>
					{renderDeleteConfirmationModal(state, updateState, dispatch)}
				</now-modal>
				: null }
			</div>
			<div> {state.showManageRelationshipModal ?
				<now-modal size='md' opened={state.showManageRelationshipModal} header-label={state.headerLabel}>
					{renderManageRelationshipModal(state, updateState, dispatch)}
				</now-modal>
				: null}
			</div>
		</div>
	)
}
