import { createGraphQLEffect } from "@servicenow/ui-effect-graphql";
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import { ACTIONS } from './constants';

export const postService = createHttpEffect('/api/sn_sro/service_registration/builder', {
		method: 'POST',
		dataParam: 'data',
		successActionType: ACTIONS.POST_RELATIONSHIP_SUCCESS,
		errorActionType: ACTIONS.POST_RELATIONSHIP_ERROR
	}
);

export const fetchService = createHttpEffect('/api/sn_sro/service_registration/graph/:serviceId', {
		method: 'GET',
		pathParams: ['serviceId'],
		queryParams: ['levels'],
		successActionType: ACTIONS.FETCH_SERVICE_SUCCESS,
		errorActionType: ACTIONS.FETCH_SERVICE_ERROR
	}
);

export const fetchPluginDetail = createHttpEffect('/api/sn_sro/sr_ops_workflow/getPluginDetail/:pluginId', {
	method: 'GET',
	pathParams: ['pluginId'],
	successActionType: ACTIONS.FETCH_PLUGIN_DETAIL_SUCCESS,
	errorActionType: ACTIONS.FETCH_PLUGIN_DETAIL_ERROR
}
);

export const deleteService = createHttpEffect("api/now/table/:tableName/:sys_id", {
	method: 'DELETE',
	pathParams: ['tableName','sys_id'],
	successActionType: ACTIONS.DELETE_SERVICE_SUCCESS,
	errorActionType: ACTIONS.DELETE_SERVICE_ERROR
});

const userDetailsQuery = `{
	GlideDomain_Query {
	 user {
			sys_id
			fullName
		}
	}
}`;

export const fetchCurrentUserDetails = createGraphQLEffect(
	userDetailsQuery, {
	successActionType: ACTIONS.FETCH_CURRENT_USER_SUCCESS,
});

const tableDescriptionQuery = `query($table: String!) {
	GlideDomain_Query {
		tableDescription(tableName: $table) {
			name
			canCreate
			canDelete
			canRead
			canWrite
		}
	}
}`;

export const fetchTableDescription = createGraphQLEffect(
	tableDescriptionQuery, {
	variableList: ['table'],
	successActionType: ACTIONS.FETCH_TABLE_DESCRIPTION_SUCCESS,
	errorActionType: ACTIONS.FETCH_TABLE_DESCRIPTION_ERROR
});

export const serviceMapWatcher = createAmbSubscriptionEffect('/rw/default/sn_sro_rp/:filter', {
	subscribeStartedActionType: ACTIONS.SUBSCRIPTION_STARTED,
	unsubscribeSucceededActionType: ACTIONS.SUBSCRIPTION_UNSUBSCRIBED,
	messageReceivedActionType: ACTIONS.SERVICE_MAP_RECORD_WATCHER_MESSAGE_RECEIVED
});

export const fetchServiceRecord = createHttpEffect('api/now/table/:tableName/:sysId', {
	method: 'GET',
	dataParam: 'data',
	pathParams: ['tableName', 'sysId'],
	successActionType: ACTIONS.FETCH_SERVICE_RECORD_SUCCESS,
	errorActionType: ACTIONS.FETCH_SERVICE_RECORD_ERROR
});