import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { ACTIONS } from './constants';

const fetchAlertsQuery = `query($queryConditions: String!) {
	GlideRecord_Query {
	em_alert(queryConditions: $queryConditions) {
		_results {
			sys_id {
				value
			}
			number {
				value
			}
			description {
				displayValue
			}
			cmdb_ci {
				value
				displayValue
			}
			assigned_to {
				value
				displayValue
			}
			last_event_time {
				displayValue
			}
			severity {
				value
				displayValue
			}
		}
	}
	}
	GlideAggregateRecord_Query(tableName: "em_alert", queryConditions: $queryConditions) {
		aggregates{
		  count
		}
	}
}`;

export const fetchAlerts = createGraphQLEffect(fetchAlertsQuery, {
	variableList: ['queryConditions'],
	successActionType: ACTIONS.FETCH_ALERT_SUCCESS,
	errorActionType: ACTIONS.FETCH_ALERT_ERROR
});
