import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

import {ACTIONS} from './constants';

const fetchChangesQuery = `query($queryConditions: String!) {
	GlideRecord_Query {
	change_request(queryConditions: $queryConditions) {
		_results {
			sys_id {
				value
			}
			number {
				value
			}
			short_description {
				displayValue
			}
			cmdb_ci {
				value
				displayValue
			}
			assignment_group {
				value
				displayValue
			}
			sys_updated_on {
				displayValue
			}
			priority {
				value
				displayValue
      }
      risk {
				value
				displayValue
			}
		}
	}
	}
	GlideAggregateRecord_Query(tableName: "change_request", queryConditions: $queryConditions) {
		aggregates{
		  count
		}
	}
}`;

export const fetchChanges = createGraphQLEffect(fetchChangesQuery, {
	variableList: ['queryConditions'],
	successActionType: ACTIONS.FETCH_CHANGE_SUCCESS,
	errorActionType: ACTIONS.FETCH_CHANGE_ERROR
});
