import { ACTIONS } from './constants';

export function onDiagramNodeClick(dispatch)  {
	return (data = {}) => {
		if (data) {
			dispatch(ACTIONS.NODE_SELECTED, {
				data
            });
        }
	};
};

export function onDiagramLinkClick(dispatch)  {
	return (data = {}) => {
		if (data) {
			dispatch(ACTIONS.LINK_CLICKED, {
				data
            });
        }
	};
};

