export function onDiagramLinkDrawn(dispatch) {
	return (data = {}) => {
		if (data) {
			dispatch(ACTIONS.LINK_DRAWN, {
				data
            });
        }
	};
}

export function onDiagramLinkRelinked(dispatch) {
	return (data = {}, oldLink = {}) => {
		if (data, oldLink) {
			dispatch(ACTIONS.LINK_RELINKED, {
				data,
				oldLink
            });
        }
	};
}

export function onDiagramViewportBoundsChanged(dispatch) {
	return (event = {}) => {
		if (event.subject.scale !== event.diagram.scale) {
			dispatch(ACTIONS.INTERNAL_STATE_SET, {zoom: event.diagram.scale});
		}
	};
}

export function onDiagramToggleContextMenu(dispatch) {
	return (data = {}, cordinates) => {
		if (data) {
			dispatch(ACTIONS.TOGGLE_CONTEXT_MENU, {data: data, cordinates: cordinates});
		}
	};
}

