import {
	t
} from 'sn-translate';
import '@devsnc/sn-record-input';
import '@servicenow/now-button';
import '@servicenow/now-modal';
import '@servicenow/now-loader';
import '@servicenow/now-record-field-lookup-connected';
import {
	ACTIONS
} from "./constants";

const view = (state, {
	updateState,
	dispatch
}) => {
	let relatedListHeader = state.properties.relatedList === 'members' ? 'Manage Members' : 'Manage Services';
	return (
		<div>
		<now-modal size='lg' opened={state.properties.opened} header-label={t(relatedListHeader)}>
			{renderEditTeamModal(state, updateState, dispatch)}
		</now-modal>
	</div>
	);
};

const renderEditTeamModal = (state, updateState, dispatch) => {
	let fieldName = state.properties.relatedList === 'members' ? 'Members' : 'Services';
	let tableName = state.properties.relatedList === 'members' ? 'sys_user' : 'cmdb_ci_service';
	let filter = state.properties.relatedList === 'members' ? '' : 'sys_class_name=cmdb_ci_service_discovered^ORsys_class_name=cmdb_ci_service';
	if (!state.loading) {
		return (
			<div>
				<div className="row">
					<div className="column">
						<now-record-field-lookup-connected
							label={t(fieldName)}
							tableName={tableName}
							fieldName={'name'}
							value={state.recordsValue}
							displayValue={state.recordsDisplayValue}
							multiSelection={true}
							onValueChange={({ value, displayValue }) => {
								updateState({
									recordsValue: value,
						  			recordsDisplayValue: displayValue
					  			});
							}}
							additionalQueryString={filter}
						/>
					</div>
				</div>
				<br/>
				<div className="button-row">
					<div className="cancel-save-div">
						<div className="cancel-save-button">
							<now-button label={t("Cancel")} variant="secondary" size="md" configAria={{}}
							on-click={() => dispatch(ACTIONS.CANCEL_CLICKED)}
							></now-button>
						</div>
						<div className="cancel-save-button">
							<now-button label={t("Save")} variant="primary" size="md" configAria={{}}
							on-click={() => {
                                dispatch(ACTIONS.SAVE_CLICKED)
                            }}
							></now-button>
						</div>
					</div>
				</div>
			</div>
		);
	}
	return (
		<now-loader label={t("Update in progress")} ></now-loader>
	);
};

export default view;