let servicesQuery = 'sys_class_name=cmdb_ci_service^ORsys_class_name=cmdb_ci_service_discovered^support_group=javascript:gs.getUser().getMyGroups()^ORsupport_group.manager=javascript:gs.getUserID()^NQsys_class_name=cmdb_ci_service^ORsys_class_name=cmdb_ci_service_discovered^sys_created_by=javascript:gs.getUserName()^support_groupISEMPTY'; //All services

export const fetchServicesQuery = `query {
	GlideAggregateRecord_Query(tableName: "cmdb_ci_service", queryConditions: "${servicesQuery}") {
		aggregates{
			count
		}
	}
}`;

let teamsQuery = 'managerDYNAMIC90d1921e5f510100a9ad2572f2b477fe^ORsys_idINjavascript:gs.getUser().getMyGroups();';

export const fetchTeamsQuery = `query {
	GlideRecord_Query {
		sys_user_group(queryConditions: "${teamsQuery}") {
		_results {
			sys_id {
				value
			} 
			name {
				displayValue
			}
		}
	}
}
}`;