import { actionTypes } from '@servicenow/ui-core';
const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;
import { ACTIONS, SORT_QUERY, ALERT_QUERY } from "./constants";
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import { fetchAlerts } from './dataSource';

const createQuery = serviceSysId =>
	serviceSysId ?
		`state!=Closed^cmdb_ci=${serviceSysId}^cmdb_ci.sys_class_name=cmdb_ci_service^ORcmdb_ci.sys_class_name=cmdb_ci_service_discovered^ORcmdb_ci.sys_class_name=cmdb_ci_query_based_service`
		: ALERT_QUERY;

const triggerDataFetch = ({state, dispatch}) => {
	const QUERY = createQuery(state.properties.service);

	dispatch(ACTIONS.FETCH_ALERT, {
		queryConditions: `${SORT_QUERY[state.sortCriteria]}^${QUERY}`
	});
};

const actionHandlers = {
	[ACTIONS.INTERNAL_STATE_SET]: ({action, updateState}) => {
		if (action.payload)
			updateState({...action.payload});
	},
	[COMPONENT_BOOTSTRAPPED]: triggerDataFetch,

	[COMPONENT_PROPERTY_CHANGED]: ({action, state, dispatch}) => {
		if (action.payload.name == 'refreshAlert' && action.payload.value != action.payload.previousValue){
			dispatch(ACTIONS.FETCH_ALERT, {queryConditions: SORT_QUERY[state.sortCriteria] + "^" + ALERT_QUERY});
		}
	},

	[ACTIONS.FETCH_ALERT]: fetchAlerts,
	[ACTIONS.FETCH_ALERT_SUCCESS]: ({state, action, dispatch}) => {
		dispatch(ACTIONS.INTERNAL_STATE_SET, {
			alerts: action.payload.data.GlideRecord_Query.em_alert._results, 
			totalAlerts: action.payload.data.GlideAggregateRecord_Query.aggregates[0].count, 
		});
	},
	[ACTIONS.FETCH_ALERT_ERROR]: ({state, action, dispatch}) => {
		//TODO: Show error message
	},
	[ACTIONS.NOW_DROPDOWN_ITEM_CLICKED]: ({state, action, dispatch, updateState}) => {
		updateState({sortCriteria: action.payload.item.id});
		triggerDataFetch({
			state: {...state, sortCriteria: action.payload.item.id},
			dispatch
		});
	},
	[ACTIONS.CARD_CLICKED]: ({state, action, dispatch}) => {
		if (action.payload.type == "alert-card") {
			dispatch(ACTIONS.ITEM_SELECTED, {
				table: 'em_alert',
				sys_id: action.payload.alert.sys_id.value
			});
		}
	},
	'NOW_BUTTON_ICONIC#CLICKED': ({ state, action, dispatch }) => {
		if (action.payload.action === 'refresh-alerts')
			triggerDataFetch({state, dispatch});
	}
}

export default actionHandlers;