	[ACTIONS.SAVE_RELATIONSHIP]: ({action, dispatch, state}) => {
		let model = state.model;
		const nodes = model.nodes;
		const edges = model.edges;
		let delNodes = state.deletedNodes;
		const validationResponse = validateModel(model);
		if(200 != validationResponse.statusCode) {
			let messageList = [];
			const issues = validationResponse.errors;
			for(const i in issues) {
				const issue = issues[i];
				const msg = {"id":"alert1","status":"critical","icon":"info-circle-outline","header":"", "content": issue.displayMessage, "action":{"type":"dismiss"}};
				messageList.push(msg);
			}
			dispatch(ACTIONS.INTERNAL_STATE_SET, {messageList: messageList});
			return;
		}

		let data = {
			services: [],
			relationships: []
		};

		let filteredModel;
		if (state.showServiceOffering != true) {
			filteredModel = filterServiceOffering(nodes, edges);
		} else {
			filteredModel = {nodes: nodes, edges: edges}
		}

		let nodesLength = filteredModel.nodes.length;
		for (let i = 0; i < nodesLength; i++) {
			const node = filteredModel.nodes[i];
			const postNode = {
				name: node.name,
				description: node.description,
				fields: {
					owned_by: node.ownedBy,
					support_group: node.supportGroup,
				},
				$class: node.class,
				$operation: node.sys_id ? 'update' : 'create'
			};
			data.services.push(postNode);
		}

		let deletedLinksLength = state.deletedLinks.length;
		for (let i = 0; i < deletedLinksLength; i++) {
			let fromServiceNodes = filteredModel.nodes.filter((node) => {return node.key == state.deletedLinks[i].from;});
			let isRemoveNode = state.isRemoveNode ? state.isRemoveNode : false;
			let fromServiceName;
			if (fromServiceNodes.length > 0) {
				fromServiceName = fromServiceNodes[0].name;
			} else if (isRemoveNode && state.removedNodes && state.removedNodes.length > 0) {
				let removedNodes = state.removedNodes;
				for (let j=0; j<removedNodes.length; j++) {
					if ((state.deletedLinks[i].from == removedNodes[j].key) &&
						(delNodes.length == 0 || (delNodes.length > 0 && delNodes.indexOf(removedNodes[j].key) == -1)))
						fromServiceName = removedNodes[j].name;
				}
			}

			let toServiceNodes = filteredModel.nodes.filter((node) => {return node.key == state.deletedLinks[i].to;});
			let toServiceName;
			if (toServiceNodes.length > 0) {
				toServiceName = toServiceNodes[0].name;
			} else if (isRemoveNode && state.removedNodes && state.removedNodes.length > 0) {
				let removedNodes = state.removedNodes;
				for (let j=0; j<removedNodes.length; j++) {
					if ((state.deletedLinks[i].to == removedNodes[j].key) && 
						(delNodes.length == 0 || (delNodes.length > 0 && delNodes.indexOf(removedNodes[j].key) == -1)))
						toServiceName = removedNodes[j].name;
				}
			}
			if (fromServiceName && fromServiceName != '' && toServiceName && toServiceName != '') {
				data.relationships.push({
					parent: fromServiceName,
					child: toServiceName,
					"$operation": "delete"
				});
			}
		}

		let edgesLength = filteredModel.edges.length;
		for (let i = 0; i < edgesLength; i++) {
			let fromServiceName = filteredModel.nodes.filter((node) => {return node.key == filteredModel.edges[i].from;})[0].name;
			let toServiceName = filteredModel.nodes.filter((node) => {return node.key == filteredModel.edges[i].to;})[0].name;
			data.relationships.push({
				parent: fromServiceName,
				child: toServiceName
			});
		}
		if (state.properties.serviceId != "-1" && state.properties.isEdit == true) {
			let rootNodeSysId = state.properties.sysId;
			if (rootNodeSysId.length > 32) //extra "1" is apended in edit mode
				data.rootNode = rootNodeSysId.substring(0, 32);
			else
				data.rootNode = rootNodeSysId;
		}
		console.debug(data);
		dispatch(ACTIONS.POST_RELATIONSHIP, {data: data});
	},
	[ACTIONS.POST_RELATIONSHIP]: postService,
	[ACTIONS.POST_RELATIONSHIP_SUCCESS]: ({action, dispatch, state, updateState}) => {

		const payload = action.payload;
		const serviceResult = payload.result.services.result;
		const relationshipResult = payload.result.relationships.result;
		console.debug(payload);
		const notificationList = [];
		parseUserNotifications(serviceResult, notificationList);
		parseUserNotifications(relationshipResult, notificationList);
		if(notificationList.length > 0) {
			dispatch(ACTIONS.INTERNAL_STATE_SET, {messageList: notificationList});
		}
		let model = state.model;
		let nodes = model.nodes;
		let edges = model.edges;

		for (let i = 0; i < nodes.length; i++) {
			var service = action.payload.result.services.result.filter((service) => {return service.service.name == nodes[i].name;});
			if (service && service.length > 0) {
				if (service[0].service.sys_id)
					nodes[i].sys_id = service[0].service.sys_id;
				else
					nodes[i].sys_id = '';
				nodes[i].ownedBy = service[0].service.fields.owned_by;
				nodes[i].supportGroup = service[0].service.fields.support_group;
				nodes[i].isSaved = true;
				if (state.selectedNode && nodes[i].key == state.selectedNode.key) {
					if (service[0].service.sys_id)
						updateState({sys_id: service[0].service.sys_id});
					else
						updateState({sys_id: ''});
				}
			}
		}

		for (let i = 0; i < edges.length; i++) {
			edges[i].isSaved = true;
		}
		let content = {
			type: 'jsx',
			value: (
				<span>
					<span><b>{t("Changes saved.")}</b></span>{" "}{t("You'll want to")} <now-text-link label={t("complete setup")} href="javascript:void(0);" on-click={(e) => dispatch(ACTIONS.COMPLETE_SETUP)} variant="primary" configAria={{}}></now-text-link> {t("for added services.")}
				</span>
			)
		};
		let messageList = [
			{"id":"alert2","status":"positive","icon":"check-circle-outline","header":"", "content": content, "action":{"type":"dismiss"}}
		];

		updateState({
			model: {nodes: nodes, edges: edges},
			deletedLinks: [],
			removedNodes: [],
			messageList: messageList,
			isRemoveNode: false,
			deletedNodes: []
		});
		if (state.properties.serviceId == "-1") {
			let firstNode = getFirstNode(state);
			if (firstNode) {
				dispatch(ACTIONS.FETCH_SERVICE, {serviceId: firstNode.sys_id});
			}
		} else if (state.properties.serviceId) {
			dispatch(ACTIONS.FETCH_SERVICE, {serviceId: state.properties.serviceId});
		}
	},
