import {createGraphQLEffect} from "@servicenow/ui-effect-graphql";
import {ACTIONS} from './constants';

export const fetchPropertiesQuery = `query{
    GlideRecord_Query{
      sys_properties(queryConditions: "name=sn_sro.relationship_rp_sysid"){
        _results{
            value {
            value
          }
        }
      }
    }
  }`;

export const fetchProperties = createGraphQLEffect(fetchPropertiesQuery, {
	variableList: [],
	successActionType: ACTIONS.FETCH_PROPERTIES_SUCCESS,
	errorActionType: ACTIONS.FETCH_PROPERTIES_ERROR
});