import {createHttpEffect} from '@servicenow/ui-effect-http';
import { ACTIONS } from "./constants";

export const postIntAction = createHttpEffect('api/now/table/:tableName', {
	method: 'POST',
	dataParam: 'data',
	pathParams: ['tableName'],
	successActionType: ACTIONS.POST_INT_ACTION_SUCCESS,
	errorActionType: ACTIONS.POST_INT_ACTION_ERROR
});

export const fetchIntAction = createHttpEffect('api/now/table/:tableName/:sysId', {
	method: 'GET',
	dataParam: 'data',
	pathParams: ['tableName', 'sysId'],
	queryParams: ['sysparm_display_value'],
	successActionType: ACTIONS.FETCH_INT_ACTION_SUCCESS,
	errorActionType: ACTIONS.FETCH_INT_ACTION_ERROR
});

export const fetchIntActionDetails = createHttpEffect('/api/sn_sro/sr_ops_workflow/rule_details/:sysId', {
	method: 'GET',
	dataParam: 'data',
	pathParams: ['sysId'],
	successActionType: ACTIONS.FETCH_INT_ACTION_DETAILS_SUCCESS,
	errorActionType: ACTIONS.FETCH_INT_ACTION_DETAILS_ERROR
});

export const deleteIntAction = createHttpEffect('api/now/table/:tableName/:sysId', {
	method: 'DELETE',
	pathParams: ['tableName','sysId'],
	successActionType: ACTIONS.DELETE_INT_ACTION_SUCCESS,
	errorActionType: ACTIONS.DELETE_INT_ACTION_ERROR
});

export const fetchRecord = createHttpEffect('api/now/table/:tableName/:sysId', {
	method: 'GET',
	dataParam: 'data',
	pathParams: ['tableName', 'sysId'],
	successActionType: ACTIONS.FETCH_RECORD_SUCCESS,
	errorActionType: ACTIONS.FETCH_RECORD_ERROR
});

export const putIntAction = createHttpEffect('api/now/table/:tableName/:sysId', {
	method: 'PUT',
	dataParam: 'data',
	pathParams: ['tableName', 'sysId'],
	successActionType: ACTIONS.POST_INT_ACTION_SUCCESS,
	errorActionType: ACTIONS.POST_INT_ACTION_ERROR
});

