import { ACTIONS, KEYS, DIAGRAM_A11Y_INSTRUCTIONS } from './constants';
import { renderZoomControls, renderZoomToFit, renderTreeView } from './zoom';
import { t } from 'sn-translate';
import { onKeyDown, onFocus } from './keyHandlers';
function renderMenu(state, dispatch, updateState) {
	let menuOptions = [];
	let menuStyle = {};
	
	if (state.isMenuVisible == true) {
		let top = (state.menuY + 70 * state.zoom);
		let containerHeight = state.element.elm.querySelector('.gojs').getBoundingClientRect().height;
		let menuHeight = 16; //padding
		state.properties.menu.forEach((menu) => {
			let isVisible = menu.class.includes(state.selectedMenu.class);
			if (isVisible && menu.requiredField) {
				isVisible = state.selectedMenu[menu.requiredField] != "" && state.selectedMenu[menu.requiredField] != undefined;
			}
			if (isVisible && menu.noConnectedAppEdge) {
				let edges = state.properties.model.edges;
				let isAppEdge = edges.some((edge)=> {return edge.from == state.selectedMenu.key;});
				isVisible = !isAppEdge;
			}
			if (isVisible) {
				menuHeight = menuHeight + 27; //Each menu item height
			}
		});
		if (containerHeight < top + menuHeight) {
			top = (state.menuY) + 50 - menuHeight;
		}
		menuStyle = {
			left: (state.menuX + 58 + (215 * (state.zoom - 1))) + 'px',
			top: top + 'px'
		}
	}
	state.properties.menu.forEach((menu) => {
		let isVisible = menu.class.includes(state.selectedMenu.class);
		if (isVisible && menu.requiredField) {
			isVisible = state.selectedMenu[menu.requiredField] != "" && state.selectedMenu[menu.requiredField] != undefined;
		}
		if (isVisible && menu.noConnectedAppEdge) {
			let edges = state.properties.model.edges;
			let isAppEdge = edges.some((edge)=> {return edge.from == state.selectedMenu.key;});
			isVisible = !isAppEdge;
		}
		if (isVisible) {
			menuOptions.push(
				<li className="menu-item" role="menuitem" tabIndex="-1" on-click={ () => {
						dispatch(menu.action, {data: state.selectedMenu});
					}} on-keydown={(event) => {
						if (event.which == KEYS.ENTER) {
							setTimeout(() => {
								let element = state.element.elm;

								if (!menu.isOpeningModal) {
									element.querySelector('.gojs').focus();
									onFocus(state, dispatch, updateState, false);
								}

								updateState({isMenuVisible: false});
								dispatch(menu.action, {data: state.selectedMenu});
							}, 10);
							event.stopPropagation();
						}
					}}>
					{menu.text}
				</li>
			)
		}
	});
	return (
		<div>
			<div className="backdrop"></div>
			<ul className="context-menu" style={menuStyle} role="menu" on-keydown={(event) => {
				dispatch(ACTIONS.MENU_KEYDOWN, {event: event, target: event.target, data: state.selectedMenu});
			}}>
				{menuOptions}
			</ul>
		</div>
	)
}

export default function view(state, {updateState, dispatch}) {
	const style = {
		width: '100%',
		height: 'calc(100% - 40px)'
	}

	return (
		<div className="node-map"
			hook-insert={(vnode) => {
				dispatch(ACTIONS.SAVE_ELEMENT, {
					element: vnode,
				});
			}}
			hook-destroy={() => {
				dispatch(ACTIONS.SAVE_ELEMENT, {
					element: null,
				});
			}}>

			{renderZoomControls(state)}
			{renderZoomToFit(state)}
			{renderTreeView(state)}
			<div className="top-left-slot">
				<slot name="top-left-slot" />
			</div>
			<div className="top-right-slot">
				<slot name="top-right-slot" />
			</div>
			<div className="gojs" style={style} tabIndex="0" role="application" aria-label={DIAGRAM_A11Y_INSTRUCTIONS} on-keyup={(event) => {
				dispatch(ACTIONS.KEYUP, {event});
			}} on-keydown={(event) => {
				onKeyDown(state, dispatch, updateState, event);
			}} on-focus={(event) => {
				if (event.relatedTarget && event.relatedTarget.className != "menu-item") { // Added menu item check for DEF0201962
					onFocus(state, dispatch, updateState, true);
				}
			}}
			></div>
			<div role="region" aria-live="assertive" className="a11y-message">{state.a11yMessage}</div>
			{state.isMenuVisible == true ? 
				renderMenu(state, dispatch, updateState)
			: null}
		</div>
	)
}
