import $ from './make-graph-object';
import { VALIDATION_API } from '../sn-sr-ops-service-map/constants';
import {
    GraphLinksModel,
	Diagram,
	Spot,
	TreeLayout,
	ToolManager,
} from 'gojs';
import { setDiagramModel} from './update-diagram';
import { SVG_PATH, MIN_ZOOM, MAX_ZOOM } from './constants';
