import { t } from 'sn-translate';


function noTargetProperties(tempnode, tempport, toend) {
    go.LinkingTool.prototype.setNoTargetPortProperties.call(this, tempnode, tempport, toend);
    if (!toend) return; // only bother to show message when linking to a new node
    var node = this.diagram.findPartAt(this.diagram.lastInput.documentPoint);
    if (node instanceof go.Node) {
        // create and show tooltip about why linking fails for this node,
        // but don't re-create the same tooltip Adornment all the time
        if (this.diagram.toolManager.currentToolTip === null || this.diagram.toolManager.currentToolTip.adornedPart !== node) {
			// create a GoJS tooltip, an Adornment
			if(node.data.validationFailure) {
				var tt = $(go.Adornment, "Auto",
                $(go.Shape, {
                    fill: "#000000"
                }),
                $(go.TextBlock, {
                        margin: 0,
                        stroke: '#FFFFFF',
                        background: '#000000'
                    },
                    node.data.validationFailure));
            	this.diagram.toolManager.showToolTip(tt, node);
			}
        }
    } else {
        // assume don't want to show tooltip when mouse is not over a node
        this.diagram.toolManager.hideToolTip();
    }
}
function linkValidation(fromnode, fromport, tonode, toport) {
	const links = fromnode.diagram.model.linkDataArray;

    const fromNodeKey = fromnode.data.key;
	const fromNodeName = fromnode.data.name;
	const fromNodeClass = fromnode.data.class;
	const fromNodeClassName = fromnode.data.className;

	const toNodeKey = tonode.data.key;
	const toNodeName = tonode.data.name;
	const toNodeClass = tonode.data.class;
	const toNodeClassName = tonode.data.className;

    // Node not allowed to link to itself
    if (fromNodeKey === toNodeKey) {
		tonode.data.validationFailure = t('Cannot link service to itself');
        return false;
    }
    if (fromNodeName === toNodeName) {
		tonode.data.validationFailure = t('Cannot link service to one with same name');
        return false;
	}

	const allowed = VALIDATION_API.canLinkToClass(fromNodeClass, toNodeClass);
	if (false === allowed) {
		tonode.data.validationFailure = t('Cannot link {0} to {1}', [fromNodeClassName, toNodeClassName]);
        return false;
    }
    // Don't allow multiple links between same nodes
    let linkAlreadyExists = false;
    for (const i in links) {
        const link = links[i];
        const linkFromKey = link.from;
        const linkToKey = link.to;
        linkAlreadyExists = linkFromKey == fromNodeKey && linkToKey == toNodeKey;
        if (linkAlreadyExists) {
			tonode.data.validationFailure = t('These services are already linked');
            return false;
        }
    }
    return true;
};

export default (vnode, model, options = {}) => {

	// License go, to remove the watermark
	licenseGo();

	// Create the diagram
	const diagram = $(Diagram, vnode.elm.querySelector('.gojs'), {
		hoverDelay: 1,
		maxSelectionCount: 1,
		contentAlignment: Spot.Center,
		layout: $(TreeLayout, {angle: 90, arrangement: TreeLayout.ArrangementHorizontal}),
		'draggingTool.isEnabled': false,
		'animationManager.isEnabled': false,
		minScale: MIN_ZOOM,
		maxScale: MAX_ZOOM,
		scale: options.zoom,
		isReadOnly: options.readOnly,
		padding: new go.Margin(0, 0, 15, 0)
	});

	const tempToNode = $(go.Node, {
            layerName: "Tool"
        },
        $(go.Shape, "Circle", {
            fill: "#01596b",
            portId: "",
            width: 9,
            height: 9
        })
    );

	diagram.toolManager.linkingTool.linkValidation = linkValidation;
	diagram.toolManager.relinkingTool.linkValidation = linkValidation;
	diagram.toolManager.linkingTool.setNoTargetPortProperties = noTargetProperties;
	diagram.toolManager.linkingTool.temporaryToNode = tempToNode;
  	//diagram.toolManager.linkingTool.temporaryToPort = tempToNode.port;

	diagram.addDiagramListener("LinkDrawn", (event) => {
		if (options.linkDrawn)
			options.linkDrawn(event);
	});

	diagram.addDiagramListener("LinkRelinked", (event) => {
		if (options.linkRelinked)
			options.linkRelinked(event, options.clickedObject);
	});

	diagram.addDiagramListener('ViewportBoundsChanged', (event) => {
		if (options.onViewportBoundsChanged)
			options.onViewportBoundsChanged(event);
	});

	diagram.addDiagramListener("ObjectSingleClicked", (event) => {
		options.clickedObject = {...event.subject.part.data};
	});

	diagram.addDiagramListener("SelectionDeleted", (event) => {
		let deletedObj;
		event.subject.each((obj) => { deletedObj = obj.part.data;});
		if (options.onDeleteLink && deletedObj && deletedObj.from && deletedObj.to)
			options.onDeleteLink(deletedObj);
	});

	setDiagramModel(diagram, model);

	if (options.readOnly) {
		setReadOnlyNodeTemplate(diagram, options);
	} else {
		setNodeTemplate(diagram, options);
	}

	if (options.readOnly) {
		setReadOnlyLinkTemplate(diagram);
	} else {
		setLinkTemplate(diagram);
	}

	// Set the tabindex to zero
	vnode.elm.querySelector('canvas').setAttribute('tabindex', '-1');

	return diagram;
};

function getGeometryFunc(geoName) {
	return function() {
		var geo = SVG_PATH[geoName];

		if (typeof geo === "string") {
			geo = go.Geometry.parse(geo, true);  // fill each geometry
		}
		return geo;
	}
  }

  function nodeHoverAdornment(diagram, options, node, alignment) {
		return $(go.Adornment, "Spot",
          {
		  },
		  $(go.Placeholder),
		$("Button",
			{
				alignment: alignment,
				alignmentFocus: go.Spot.Center,
				"ButtonBorder.fill": "#01778e",
				"ButtonBorder.stroke": "#01778e",
				"ButtonBorder.figure": "Circle",
				"_buttonFillOver": "#013c47",
				"_buttonStrokeOver": "#013c47",
				"_buttonFillPressed": "#013c47",
				"_buttonStrokePressed": "#013c47",
				width: 24,
				height: 24
			},
      {
				click: function(e, obj) {
					if (options.addNode) {
						 options.addNode(obj.part.data)
					}
				},
				mouseLeave: function(e, n) {
					if (!node.actualBounds.containsPoint(e.documentPoint)) {
						node.removeAdornment("mouseHover")
						var shape = node.findObject("rec");
						shape.strokeWidth = 1;
						shape.stroke = node.part.data.strokeColor;
						shape.fill = node.part.data.backgroundColor;
					};
				}
			},
      $(go.Shape, {
				stroke: "#ffffff",
				margin: new go.Margin(-1, 0, 0, -2),
				width: 16,
				height: 16
			}, new go.Binding("geometry", getGeometryFunc("add"))))
		);
	}
function setNodeTemplate(diagram, options) {
	diagram.nodeTemplate =
	$(go.Node, "Auto",
	  new go.Binding("location", "loc", go.Point.parse),
		$(go.Shape, "Rectangle", {
			minSize: new go.Size(215, 90), maxSize: new go.Size(215, 90), name:"rec",
			click: function(e, obj){ if (options.nodeClick) { options.nodeClick(obj.part.data);}}
		},
			new go.Binding("strokeWidth", "data", function(v) { return v.isFocused == true ? 2 : 1; }),
			new go.Binding("stroke", "strokeColor"), new go.Binding("fill", "backgroundColor"),
			),
			$(go.Panel, "Vertical",
				{
					margin: new go.Margin(16, 16, 16, 16),
					alignment: new go.Spot(0, 0, 0, 0) ,
				},
				$(go.TextBlock, { margin: new go.Margin(0, 0, 0, 0), alignment: new go.Spot(0, 0, 0, 0), font: "12px 'Source Sans Pro','Helvetica Neue',helvetica,arial,sans-serif"},
					new go.Binding("text", "className")
				),
				$(go.TextBlock, { margin: new go.Margin(4, 0, 0, 0), alignment: new go.Spot(0, 0, 0, 0),
					font: "16px 'Source Sans Pro','Helvetica Neue',helvetica,arial,sans-serif",
					stroke: "#01778e",//$now-color--secondary-1
					cursor: "pointer",
					overflow: go.TextBlock.OverflowEllipsis,
					width: 190,
					maxLines: 1,
					mouseEnter: function(e, obj) {
						if (obj.part.data.sys_id && obj.part.data.sys_id != "") {
							obj.isUnderline = true;
						}
					},
					mouseLeave: function(e, obj) {
						obj.isUnderline = false;
					},
					click: function(e, obj){
						if (!(obj.part.data.sys_id && obj.part.data.sys_id != "") && options.nodeClick) { options.nodeClick(obj.part.data);}
						if ((obj.part.data.sys_id && obj.part.data.sys_id != "") && options.linkClick) { options.linkClick(obj.part.data);}
					},
					toolTip:
						$("ToolTip", {"Border.fill": "#000000", "Border.stroke": "#000000"},
							$(go.TextBlock, {margin: 0, 'stroke': '#FFFFFF', 'background': '#000000'},
							new go.Binding("text", "name"))
						),
				},
					new go.Binding("text", "name"),
					new go.Binding("isUnderline", "data", function(v) { return v.focusIndex == 1 ? true : false; }),
				),
			),

			$(go.Shape, "Rectangle", { strokeWidth: 2, visible:false, margin: 10, alignment: new go.Spot(0, 0.5, 175, -25), stroke: "#01778e", alignmentFocus: go.Spot.Right, width: 25, height: 25, fill: "transparent"},
				new go.Binding("visible", "data", function(v) { return v.focusIndex == 2 ? true : false; }),
			),
			$(go.Panel, "Horizontal",
				{
					visible: true, margin: 10, alignment: new go.Spot(0, 0.5, 180, -25), maxSize: new go.Size(30, 30), alignmentFocus: go.Spot.Right,
					background: "transparent",
					width: 15,
					click: function(e, node){ if (options.toggleContextMenu) { options.toggleContextMenu(node.part.data, diagram.transformDocToView(node.part.location));} }
				},
				$(go.Shape, {strokeWidth: 0},
					new go.Binding("geometry", getGeometryFunc("ellipsis_vertical")),
				)
			),

			{ // show the Adornment when a mouseHover event occurs
				portId: "", cursor: "pointer",  // the Shape is the port, not the whole Node
				// allow all kinds of links from and to this port
				fromLinkableSelfNode: false, fromLinkableDuplicates: true,
				toLinkableSelfNode: false, toLinkableDuplicates: true,
				margin: 16,
				shadowOffset: new go.Point(2, 4),
				shadowBlur: 4,
				shadowColor: "#B2B2B2",
				selectionAdorned: false,
				deletable: false,
				mouseHover: function(e, obj) {
					var node = obj.part;
					if (node.data.canEdit == true) {
						var adornedObject;
						if (diagram.layout.angle == 90)
							adornedObject = nodeHoverAdornment(diagram, options, node, go.Spot.Bottom);
						else
							adornedObject = nodeHoverAdornment(diagram, options, node, go.Spot.Right);

						adornedObject.adornedObject = node;

						node.addAdornment("mouseHover", adornedObject);
					}
				},
				mouseEnter: function(e, node) {
					var shape = node.findObject("rec");
					shape.strokeWidth = 2;
					shape.stroke = "#01596b";//$now-color--secondary-2
					showSmallPorts(node, true);
				},
				mouseLeave: function(e, node) {
					if (!node.actualBounds.containsPoint(e.documentPoint)) {
						node.removeAdornment("mouseHover");
						var shape = node.findObject("rec");
						shape.stroke = node.part.data.strokeColor;
						shape.strokeWidth = node.part.data.strokeWidth ? Number(node.part.data.strokeWidth): 1;
					}
					showSmallPorts(node, false);
				},
			},
			new go.Binding("isShadowed", "isSelected"),
			makePort("B", go.Spot.Bottom),
			makePort("T", go.Spot.Top),
			makePort("L", go.Spot.Left),
			makePort("R", go.Spot.Right),
			new go.Binding("fromLinkable", "canEdit"),
			new go.Binding("toLinkable", "canEdit"),
	);
}

function makePort(name, spot) {
	const tootip = t("Click and drag to create a new connector line");
    const SHAPE_MAP = {};
    SHAPE_MAP[go.Spot.Bottom] = "Circle";
    SHAPE_MAP[go.Spot.Top] = "Circle";
    SHAPE_MAP[go.Spot.Left] = "Circle";
    SHAPE_MAP[go.Spot.Right] = "Circle";
    const shape = $(go.Shape, SHAPE_MAP[spot], {
			fill: "#acb4b5", // not seen, by default; set to a translucent gray by showSmallPorts, defined below
			opacity: 0.0,
			stroke: null,
			desiredSize: new go.Size(9, 9),
			alignment: spot, // align the port on the main Shape
			alignmentFocus: spot, // just inside the Shape
			portId: name, // declare this object to be a "port"
			fromSpot: spot,
			toSpot: spot, // declare where links may connect at this port
			fromLinkable: true,
			toLinkable: true, // declare whether the user may draw links to/from here
			cursor: "pointer", // show a different cursor to indicate potential link point,
			toolTip: tootip,
			mouseEnter: function(e, node) {
				node.stroke = "#01596b";
				node.strokeWidth = 5;
				node.fill = "#01596b";
			},
			mouseLeave: function(e, node) {
				node.stroke = "#acb4b5";
				node.strokeWidth = 0;
				node.fill = "#acb4b5";
			}
		},
		new go.Binding("visible", "canEdit", function(canEdit) {
			return canEdit;
		})
	);
    return shape;
}

function showSmallPorts(node, show) {
	const diag = node.diagram;
	const isHorizontal = TreeLayout.ArrangementHorizontal === diag.layout.arrangement;
    node.ports.each(function(port) {
		// Only change when nodes are editable
		if(true === node.data.canEdit) {
			if (port.portId !== "") { // don't change the default port, which is the big shape
				let process = false;
				if(isHorizontal) {
					process = go.Spot.Left == port.fromSpot || go.Spot.Right == port.fromSpot;
				} else {
					process = go.Spot.Top == port.fromSpot || go.Spot.Bottom == port.fromSpot;
				}
				if(process) {
					port.opacity = show ? 1.0 : 0.0;
				} else {
					port.visible = false;
				}
			}
		}
    });
}

function setLinkTemplate(diagram) {
	diagram.linkTemplate =
	$(go.Link, {
			routing: go.Link.AvoidsNodes,
			corner: 10,
			adjusting: go.Link.Stretch,
			relinkableFrom: true,
			relinkableTo: true
		},
		new go.Binding("fromSpot", "fromSpot", go.Spot.parse),
        new go.Binding("toSpot", "toSpot", go.Spot.parse),
		$(go.Shape, {toArrow: "Standard" }),
		$(go.Shape,
			{
				segmentIndex: -1,
				segmentOrientation: go.Link.OrientAlong,
				alignmentFocus: go.Spot.Right,
				geometry: go.Geometry.parse(SVG_PATH["arrow"])
			}
		),
		{
			cursor: "pointer",
			mouseEnter: function(e, link) {
				link.path.strokeWidth = 3;
			},
			mouseLeave: function(e, link) {
				link.path.strokeWidth = 1;
			}
		}
    );
}



