import {createCustomElement} from '@servicenow/ui-core';
import { actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-heading';
import '@servicenow/now-button';
import '@servicenow/now-loader';
import '@servicenow/now-text-link';
import '../sn-sr-ops-team-card';
import '../sn-sr-ops-create-team';
import '@servicenow/now-empty-state';
import styles from './styles.scss';
import { t } from 'sn-translate';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {createAmbSubscriptionEffect} from '@servicenow/ui-effect-amb';

const teamsQuery = 'active=true^manager=javascript:gs.getUserID()^ORsys_idINjavascript:gs.getUser().getMyGroups()^ORDERBYname';
const teamReqQuery = 'requested_item.opened_by=javascript:gs.getUserID()^status=pending^ORDERBYteam_name';

const teamsFilter = 'active=true^manager=javascript:gs.getUserID()^ORsys_id=javascript:gs.getUser().getMyGroups()^ORDERBYname';

const onCallShiftFilter = 'group=javascript:gs.getUser().getMyGroups()';

const fetchQueryGQL = `query {
	GlideRecord_Query {
		teams: sys_user_group(queryConditions: "${teamsQuery}") {
			_results {
				sys_id {
					value
				}
				name {
					displayValue
				}
			}
		}
		teamRequests: sn_sro_team_request(queryConditions: "${teamReqQuery}") {
			_results {
				sys_id {
					value
				}
				team_name {
					displayValue
				}
			}
		}
	}
}`;


//Record watcher apis
const recordWatcherForTeams = createAmbSubscriptionEffect('/rw/default/sys_user_group/:filter', {
	messageReceivedActionType: 'TEAM_UPDATED',
});

const recordWatcherForTeamsPanel = createAmbSubscriptionEffect('/rw/default/sys_user_group/:filter', {
	messageReceivedActionType: 'TEAM_UPDATED',
});


const recordWatcherForTeamRequests = createAmbSubscriptionEffect('/rw/default/sn_sro_team_request/:filter', {
	messageReceivedActionType: 'TEAM_REQUEST_UPDATED'
});

const recordWatcherForOCShifts = createAmbSubscriptionEffect('/rw/default/cmn_rota/:filter', {
	messageReceivedActionType: 'OC_SHIFT_UPDATED',
});

const recordWatcherForOCShiftsPanel = createAmbSubscriptionEffect('/rw/default/cmn_rota/:filter', {
	messageReceivedActionType: 'OC_SHIFT_UPDATED',
});


function getEncodedFilterString(filter) {
	if(typeof filter != 'string')
	return '';
	filter = filter.
	replace(/\^EQ/g, '').
	replace(/\^ORDERBY(?:DESC)?[^^]*/g, '').
	replace(/^GOTO/, '');
	return btoa(filter).replace(/=/g, '-');
}

const view = (state, {updateState, dispatch}) => {
	let label = state.properties.mode === 'page' ? t('Teams') : t('My Teams');
	let headerVariant = state.properties.mode === 'page' ? 'header-primary' : 'header-secondary';
	let cardCls = ['main-teams-container-compact'];
	if (state.properties.mode === 'page')
		cardCls = ['main-teams-container'];
	else if (state.teams.length === 0)
		cardCls.push('empty-state');

	return (
	<div>
		<div className={state.properties.mode === 'page' ? 'teams-header' : 'teams-header-compact'}>
			<div className={state.properties.mode === 'page' ? 'teams-heading' : ''}>
				<now-heading label={t(label)} level="1" variant={headerVariant}></now-heading>
			</div>
			{renderActions(state, dispatch)}
		</div>
		<div>
			{renderResultMessages(state)}
		</div>
		<div className={cardCls.join(' ')}>
			{renderTeams(state, {updateState, dispatch})}
		</div>
		{renderViewAllMessage(state)}
		{createTeamModal(state, dispatch)}
	</div>
	)
};

const renderActions = (state, dispatch) => {
	if (state.canCreateTeam) {
		if (state.properties.mode === 'page') {
			return (
				<div className='teams-actions'>
					<now-button tooltipContent={t('Create New Team')} label={t('Create New Team')} variant="primary" 
					on-click={() => dispatch('CREATE_TEAM_CLICKED')}
					size="md"></now-button>
				</div>
			);	
		}
		else {
			return (
				<div>
					<now-button icon="plus-outline" tooltipContent={t('Create New Team')} variant="secondary" 
					config-aria={{'aria-label': t('Create New Team')}}
					on-click={() => dispatch('CREATE_TEAM_CLICKED')}
					size="md"></now-button>
				</div>
			);
		}
	}
};

const renderViewAllMessage = (state) => {
	let totalRecords = state.teams.length + state.teamRequests.length;
	if (state.properties.mode !== 'page' && totalRecords > 4)
		return (
			<div className="view-all-message">
				<now-text-link label={t('Show All')} href='now/workspace/sr_ops/teams' underlined={false} variant="primary"/>
			</div>
		);
};

const renderResultMessages = (state) => {
	var result = state.actionResult;
	var alert = null;
	switch (result) {
		case 'createTeamSuccess':
			alert = <now-alert status="positive" icon="circle-check-outline" header="" content={t("Request submitted. {0} will appear on the teams page once it's approved.", state.newTeamName)} action={{"type":"dismiss"}}></now-alert>
			break;
		case 'createTeamFailure':
			alert = <now-alert status="critical" icon="circle-close-outline" header="" content={t("Team creation failed. Please check logs for more details.")} action={{"type":"dismiss"}}></now-alert>
			break
		case 'deactivateTeamSuccess':
			alert = <now-alert status="positive" icon="circle-check-outline" header="" content={t("{0} has been successfully deactivated. You can reactivate this team from the teams list.", state.teamName)} action={{ "type": "dismiss" }}></now-alert>
			break
		case 'deactivateTeamFailure':
			alert = <now-alert status="critical" icon="circle-close-outline" header="" content={t("Team deactivation failed. Please check logs for more details.")} action={{ "type": "dismiss" }}></now-alert>
			break
		case 'updateTeamSuccess':
			alert = <now-alert status="positive" icon="circle-check-outline" header="" content={t("{0} has been successfully updated.", state.teamName)} action={{ "type": "dismiss" }}></now-alert>
			break
		case 'updateTeamFailure':
			alert = <now-alert status="critical" icon="circle-close-outline" header="" content={t("Team updation failed. Please check logs for more details.")} action={{ "type": "dismiss" }}></now-alert>
			break
		case 'createTeamSetupIncomplete':
			alert = <now-alert status="info" icon="info-circle-outline" header="" content={t("The system administrator must configure the integration hub flow to create and update teams. Contact your system administrator.")} action={{ "type": "dismiss" }}></now-alert>
			break
	}
	return (
		<div className={state.properties.mode === 'page' ? 'result-msg-container': ''}>
			{alert}
		</div>
	);
}

const createTeamModal = function(state) {
	if (state.createTeam)
		return (
			<sn-sr-ops-create-team></sn-sr-ops-create-team>
		);
}
const renderTeams = function (state, {dispatch}) {
	let loaderClass = [];
	if (state.loading) {
		if (state.properties.mode === 'page')
			loaderClass.push('center-div');
		else
			loaderClass.push('center-div-compact');
		loaderClass.push('loading-icon');
		return (
			<div className={loaderClass.join(' ')}>
				<now-loader label={t("Fetching Teams")} size="lg"/>
			</div>
		);
	}
	if (state.teams.length === 0 && state.teamRequests.length === 0)
			return renderNoTeamsMessage(state, dispatch);
	let teams = [];
	for (let i = 0; i < state.teams.length; i++) {
		teams.push(
			<sn-sr-ops-team-card mode={state.properties.mode === 'page' ? 'normal' : 'compact'} team-id={state.teams[i].sys_id.value} name={state.teams[i].name.displayValue} table="sys_user_group" refresh={!state.loading}/>
		);
	}

	if (state.properties.mode === 'page') {
		for (let i = 0; i < state.teamRequests.length; i++) {
			teams.push(
				<sn-sr-ops-team-card mode='normal' team-id={state.teamRequests[i].sys_id.value} name={state.teamRequests[i].team_name.displayValue} table="sn_sro_team_request" refresh={!state.loading}/>
			);
		}
	}

	if (state.properties.mode !== 'page')
		teams = teams.slice(0,4);

	return teams;
};

function renderNoTeamsMessage(state, dispatch) {
	let createTeamMessage = <div></div>;

	if (state.canCreateTeam)
		createTeamMessage = (<div className="text-tertiary">
				<span>{t('Create a')}</span>&nbsp;
				<now-text-link label={t('new team')} href='javascript:void(0);' on-click={(e) => dispatch('CREATE_TEAM_CLICKED') } underlined={false} variant="primary"/>
				&nbsp;
				<span>{t('to get started.')}</span>
		</div>);
	  return <div className={state.properties.mode === 'page' ? 'center-div' : 'center-div-compact'}>
				<div className="no-teams-container">
					<now-empty-state type="empty" variant="fit" bodyText={false} headerText={false}/>
					<div className="text-primary">{t("Welcome! You don't have any teams yet.")}</div>
					{createTeamMessage}
				</div>
		 	</div>;
}

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchACLDataEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/getUpdateTeamAccess/''", {
	method: 'GET',
	successActionType: 'FETCH_ACL_SUCCESS_GQL'
});


const fetchACLDataSuccessEffect = ({action, updateState}) => {
	updateState({
		canCreateTeam: action.payload.canCreate,
		createTeamSetupComplete: action.payload.ihubSetupComplete
	});
};

const fetchTeamsEffect = createGraphQLEffect(
	fetchQueryGQL, {
	successActionType: 'FETCH_TEAMS_SUCCESS_GQL',
});

const fetchTeamsSuccessGQL = ({ action, updateState }) => {
	const data = action.payload.data.GlideRecord_Query.teams._results;
	const teamReqs = action.payload.data.GlideRecord_Query.teamRequests._results;
	updateState({
		teams: data,
		teamRequests: teamReqs,
		loading: false
	});
}

createCustomElement('sn-sr-ops-teams', {
	renderer: {type: snabbdom},
	view,
	styles,
	properties: {
		mode: {default: 'panel'}
	},
	initialState: {
		teams: [],
		teamRequests: [],
		loading: true,
		actionResult: 'none',
		canCreateTeam: false,
		createTeamSetupComplete: false,
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({state, dispatch}) => {
			dispatch('FETCH_ACL_DETAILS');
			dispatch('FETCH_TEAMS_GQL');
			var teamFilter = getEncodedFilterString(teamsFilter);
			var onCallFilter = getEncodedFilterString(onCallShiftFilter);
			//different actions for subscription
			if (state.properties.mode !== 'page') {
				dispatch('TEAM_RW_SUBSCRIBED_PANEL', {
					filter: teamFilter,
					subscribe: true,
				});
				dispatch('OC_SHIFT_SUBSCRIBED_PANEL', {
					filter: onCallFilter,
					subscribe: true,
				});
			}
			
			if (state.properties.mode === 'page') {
				var teamReqFilter = getEncodedFilterString(teamReqQuery);
				dispatch('TEAM_RW_SUBSCRIBED', {
					filter: teamFilter,
					subscribe: true,
				});
				dispatch('TEAM_REQUEST_RW_SUBSCRIBED', {
					filter: teamReqFilter,
					subscribe: true,
				});
				dispatch('OC_SHIFT_SUBSCRIBED', {
					filter: onCallFilter,
					subscribe: true,
				});
			}
			
		},
		'FETCH_ACL_DETAILS': fetchACLDataEffect,
		'FETCH_ACL_SUCCESS_GQL': fetchACLDataSuccessEffect,
		'FETCH_TEAMS_GQL': fetchTeamsEffect,
		'FETCH_TEAMS_SUCCESS_GQL':fetchTeamsSuccessGQL,
		'CREATE_TEAM_CLICKED': ({state, updateState}) => {
			updateState({
				createTeam: state.createTeamSetupComplete,
				actionResult: state.createTeamSetupComplete == false ? 'createTeamSetupIncomplete' : 'none'
			})
		},
		'TEAM_RW_SUBSCRIBED_PANEL': recordWatcherForTeamsPanel,
		'TEAM_RW_SUBSCRIBED': recordWatcherForTeams,
		'TEAM_REQUEST_RW_SUBSCRIBED': recordWatcherForTeamRequests,
		'OC_SHIFT_SUBSCRIBED': recordWatcherForOCShifts,
		'OC_SHIFT_SUBSCRIBED_PANEL': recordWatcherForOCShiftsPanel,
		'TEAM_UPDATED': ({updateState, dispatch}) => {
			setTimeout(() => {
				updateState({loading: true});
				dispatch('FETCH_TEAMS_GQL');
			}, 3000);
		},
		'OC_SHIFT_UPDATED': ({updateState, dispatch}) => {
			setTimeout(() => {
				updateState({loading: true});
				dispatch('FETCH_TEAMS_GQL');
			}, 3000);
		}, 
		'TEAM_REQUEST_UPDATED': ({updateState, dispatch}) => {
			updateState({loading: true});
			dispatch('FETCH_TEAMS_GQL');
		},
		'SRO_TEAM#TEAM_CREATED': ({action, dispatch, updateState}) => {
			updateState({
				createTeam: false,
				actionResult: 'createTeamSuccess',
				newTeamName: action.payload.name
			})
		},
		'SRO_TEAM#TEAM_CREATION_FAILED': ({action, dispatch, updateState}) => {
			updateState({
				createTeam: false,
				actionResult: 'createTeamFailure'
			})
		},
		'SRO_TEAM#CANCEL_CLICKED': ({updateState}) => {
			updateState({
				createTeam: false
			});
		},
		'NOW_MODAL#OPENED_SET': ({updateState}) => {
			updateState({
				createTeam: false
			});
		},
		'NOW_ALERT#ACTION_CLICKED': ({action, updateState}) => {
			updateState({
				actionResult: 'none'
			})
		},
		'TEAM_DEACTIVATED': ({action, updateState, dispatch}) => {
			updateState({
				actionResult: 'deactivateTeamSuccess',
				teamName: action.payload.name,
				loading: true
			});
			dispatch('FETCH_TEAMS_GQL');
		},
		'TEAM_DEACTIVATION_FAILED': ({updateState}) => {
			updateState({
				actionResult: 'deactivateTeamFailure'
			});
		},
		'SRO_EDIT_TEAM#TEAM_UPDATED': ({action, updateState}) => {
			updateState({
				actionResult: 'updateTeamSuccess',
				teamName: action.payload.name
			});
		},
		'SRO_EDIT_TEAM#TEAM_UPDATE_FAILED': ({updateState}) => {
			updateState({
				actionResult: 'updateTeamFailure'
			});
		}
	}
});

export default 'sn-sr-ops-teams';
