import { actionTypes } from '@servicenow/ui-core';
const {
	COMPONENT_RENDERED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

import { ACTIONS, MIN_ZOOM, MAX_ZOOM } from "./constants";
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import makeDiagram from './make-diagram';
import { setDiagramModel, setDiagramScale, setZoomToFit, setDiagramLayout } from './update-diagram';
import { onDiagramNodeClick, onDiagramNodeDeleteClick, onDiagramNodeAddClick, onDiagramNodeEditClick, onDiagramLinkClick, onDiagramViewportBoundsChanged, onDiagramLinkDrawn, onDiagramLinkRelinked, onDiagramToggleContextMenu, onDeleteLink, onObjectSingleClicked } from './utils';
import { onFocus } from './keyHandlers';

function renderDiagram(state, dispatch, rerender) {
	const {
		properties: {
			model
		},
		diagram,
		element,
		zoom
	} = state;

	if (!diagram) {
		// At the beginning (We render for the first time)
		if (!element)
			return;

		// Make the diagram
		const selfMadeDiagram = makeDiagram(element, model, {
			readOnly: !state.properties.isEdit,
			zoom: zoom,
			nodeClick: onDiagramNodeClick(dispatch), 
			linkClick: onDiagramLinkClick(dispatch),
			deleteClick: onDiagramNodeDeleteClick(dispatch), 
			editClick: onDiagramNodeEditClick(dispatch), 
			addNode: onDiagramNodeAddClick(dispatch),
			linkDrawn: onDiagramLinkDrawn(dispatch),
			linkRelinked: onDiagramLinkRelinked(dispatch),
			onViewportBoundsChanged: onDiagramViewportBoundsChanged(dispatch),
			toggleContextMenu: onDiagramToggleContextMenu(dispatch),
			onObjectSingleClicked: onObjectSingleClicked(dispatch),
			onDeleteLink: onDeleteLink(dispatch)
		});

		// Change the state and save the visible activities to the state
		dispatch(ACTIONS.INTERNAL_STATE_SET, {
			diagram: selfMadeDiagram
		});
	} else if (rerender){
		setDiagramModel(diagram, model);
	}
}

const actionHandlers = {
	[ACTIONS.INTERNAL_STATE_SET]: createUpdateStateEffect((state, action) => {
		if (!action.payload)
			return;
		return {
			...state,
			...action.payload
		};
	}),
	[ACTIONS.SAVE_ELEMENT]: ({ action, dispatch }) => {
		const { element } = action.payload;
		dispatch(ACTIONS.INTERNAL_STATE_SET, {
			element: element
		});
	},
	[COMPONENT_RENDERED]: ({ state, updateState, dispatch }) => {
		renderDiagram(state, dispatch);
	},
	[COMPONENT_PROPERTY_CHANGED]: ({ state, action, dispatch, updateState }) => {
		if (action.payload.name == 'model') {
			renderDiagram(state, dispatch, true);
		}
		if (action.payload.name === 'shouldRefocusInDiagram' && action.payload.value && action.payload.value !== action.payload.previousValue) {
			const element = state.element.elm;

			element.querySelector('.gojs').focus();
			onFocus(state, dispatch, updateState, false);
		}
	},
	[ACTIONS.NOW_BUTTON_CLICKED]: ({ action, state, dispatch }) => {
		const {
			zoom,
			step,
			diagram
		} = state;
		const { componentName } = action.meta;

		let newScale = zoom;
		switch (componentName) {
			case 'zoom-in':
				newScale = (zoom + step > MAX_ZOOM) ? MAX_ZOOM : zoom + step;
				if (newScale !== zoom)
					dispatch(ACTIONS.INTERNAL_STATE_SET, {zoom: newScale});
					setDiagramScale(diagram, newScale);
				break;
			case 'zoom-out':
				newScale = (zoom - step < MIN_ZOOM) ? MIN_ZOOM : zoom - step;
				if (newScale !== zoom)
					dispatch(ACTIONS.INTERNAL_STATE_SET, {zoom: newScale});
					setDiagramScale(diagram, newScale);
				break;
			case 'zoom-to-fit':
				setZoomToFit(diagram);
				break;
		}
		diagram.updateAllTargetBindings();
	},
	[ACTIONS.NOW_DROPDOWN_ITEM_CLICKED]: ({action, state, dispatch}) => {
		const {
			properties: {
				model
			},
			diagram
		} = state;
		setDiagramLayout(diagram, action.payload.item.id);
		setDiagramModel(diagram, model);
		diagram.updateAllTargetBindings();
		dispatch(ACTIONS.INTERNAL_STATE_SET, {layout: action.payload.item.id});
	},
	[ACTIONS.TOGGLE_CONTEXT_MENU]: ({action, state, dispatch}) => {
		let skipClick = state.skipClick + 1;
		dispatch(ACTIONS.INTERNAL_STATE_SET, {skipClick: skipClick, isMenuVisible: true, menuX: action.payload.cordinates.x, menuY: action.payload.cordinates.y, selectedMenu: action.payload.data});
	},
	[ACTIONS.DOCUMENT_CLICK]: ({action, state, dispatch}) => {
		if (state.isMenuVisible) {
			if (state.skipClick == 0) {
				dispatch(ACTIONS.INTERNAL_STATE_SET, {isMenuVisible: false});
			} else if (state.skipClick != 0){
				dispatch(ACTIONS.INTERNAL_STATE_SET, {skipClick: state.skipClick-1});
			}
		}
	}
}

export default actionHandlers;