import { actionTypes } from '@servicenow/ui-core';
const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;
import { ACTIONS, SORT_QUERY, INCIDENT_QUERY } from "./constants";
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import { fetchIncidents } from './dataSource';

const createQuery = serviceSysId =>
	serviceSysId ?
		`cmdb_ci.sys_id=${serviceSysId}^cmdb_ci.sys_class_name=cmdb_ci_service^ORcmdb_ci.sys_class_name=cmdb_ci_service_discovered^ORcmdb_ci.sys_class_name=cmdb_ci_query_based_service^active=true^state!=6^NQbusiness_service.sys_id=${serviceSysId}^business_service.sys_class_name=cmdb_ci_service^ORbusiness_service.sys_class_name=cmdb_ci_service_discovered^ORbusiness_service.sys_class_name=cmdb_ci_query_based_service`
		: INCIDENT_QUERY;

const triggerDataFetch = ({state, dispatch}) => {
	const QUERY = createQuery(state.properties.service);

	dispatch(ACTIONS.FETCH_INCIDENT, {
		queryConditions: `${SORT_QUERY[state.sortCriteria]}^${QUERY}`
	});
};

const actionHandlers = {
	[ACTIONS.INTERNAL_STATE_SET]: ({action, updateState}) => {
		if (action.payload)
			updateState({...action.payload});
	},
	[COMPONENT_BOOTSTRAPPED]: triggerDataFetch,
	[COMPONENT_PROPERTY_CHANGED]: ({action, state, dispatch}) => {
		if (action.payload.name == 'refreshIncident' && action.payload.value != action.payload.previousValue){
			triggerDataFetch({state, dispatch});
		}
	},
	[ACTIONS.FETCH_INCIDENT]: fetchIncidents,
	[ACTIONS.FETCH_INCIDENT_SUCCESS]: ({state, action, dispatch}) => {
		dispatch(ACTIONS.INTERNAL_STATE_SET, {
			incidents: action.payload.data.GlideRecord_Query.incident._results,
			totalIncidents: action.payload.data.GlideAggregateRecord_Query.aggregates[0].count, 
		});
	},
	[ACTIONS.FETCH_INCIDENT_ERROR]: ({state, action, dispatch}) => {
		//TODO: Show error message
	},
	[ACTIONS.NOW_DROPDOWN_ITEM_CLICKED]: ({state, action, dispatch, updateState}) => {
		updateState({sortCriteria: action.payload.item.id});
		triggerDataFetch({
			state: {...state, sortCriteria: action.payload.item.id},
			dispatch
		});
	},
	[ACTIONS.CARD_CLICKED]: ({state, action, dispatch}) => {
		if (action.payload.type == "incident-card") {
			dispatch(ACTIONS.ITEM_SELECTED, {
				table: 'incident',
				sys_id: action.payload.incident.sys_id.value
			});
		}
	},
	'NOW_BUTTON_ICONIC#CLICKED': ({ state, action, dispatch }) => {
		if (action.payload.action === 'refresh-incidents')
			triggerDataFetch({state, dispatch});
	}
}

export default actionHandlers;