import { createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';
import { renderPage } from './view';
import { actionHandlers } from './actionHandlers';

const view = (state, coeffects) => {
    return renderPage(state, coeffects);
};

createCustomElement('sn-sr-ops-team-page', {
    renderer: { type: snabbdom },
    view,
    initialState: {
        selectedTab: 'details',
        hideNewAction: true,
        refreshPayload: {},
        manageTeamRlOpen: true,
        selectedItem: '',
        loading: true,
        updateMembersPrecessing: false
    },
    properties: {
        table: {
            default: ""
        },
        sysId: {
            default: ""
        },
        teamName: {
            default: ''
        }
    },
    styles,
    actionHandlers: actionHandlers
});

export default 'sn-sr-ops-team-page';
