import {createHttpEffect} from '@servicenow/ui-effect-http';
import { ACTIONS } from "./constants";
import {createAmbSubscriptionEffect} from '@servicenow/ui-effect-amb';


export const updateTeamEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/updateTeam", {
	method: 'POST',
	dataParam: 'data',
	startActionType: ACTIONS.UPDATE_STARTED,
	successActionType: ACTIONS.UPDATE_FINISHED,
	errorActionType: ACTIONS.UPDATE_FAILED
});

export const getTeamDetailsEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/team_details/sys_user_group/:teamId", {
	method: 'GET',
	pathParams: ['teamId'],
	queryParams: ['getAllMemberDetails'],
	successActionType: ACTIONS.FETCH_TEAM_DETAILS_SUCCEEDED,
	errorActionType: ACTIONS.FETCH_TEAM_DETAILS_FAILED
});

export const getTeamAccessEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/getUpdateTeamAccess/:teamId", {
	method: 'GET',
	pathParams: ['teamId'],
	successActionType: ACTIONS.TEAM_ACCESS_DETAILS_FETCHED
});

export const recordWatcherForTeamUpdateRequest = createAmbSubscriptionEffect('/rw/default/sn_sro_team_request/:filter', {
	messageReceivedActionType: ACTIONS.UPDATE_TEAM_FINISHED
});
