import { t } from 'sn-translate';
import '@servicenow/now-tabs';
import '@servicenow/now-loader';
import '@servicenow/now-record-list-connected';
import '@devsnc/sn-record-input';
import '@servicenow/now-button';
import '@servicenow/now-record-field-lookup-connected';
import '@servicenow/now-record-common';
import '../sn-sr-ops-my-services-list';
import '../sn-sr-ops-manage-team-rl';
import { GROUP_MEMBER_CONSTANTS } from './constants';
import { ACTIONS } from './constants';

export function renderPage(state, { updateState, dispatch }) {
    return <div>
        {renderUpdateMembersAlert(state)}
        <div className="banner">
            <div className="page-header">
                <now-record-common-header
                            table='sys_user_group'
                            sys-id={state.properties.sysId}
							primary-value={state.nameValue}
							is-ribbon-visible={false}
							classic-form={true}
							show-ribbon-toggle={false}>
                    <sn-form-internal-tag table='sys_user_group' sys-id={state.properties.sysId}>

                    </sn-form-internal-tag>
                </now-record-common-header>
            </div>
            {renderActions(state, dispatch)}
        </div>
        <div className="body-container">
            <now-tabs items={[ { "id": "details", "label": t("Details") }, { "id": "members", "label": t("Members") }, { "id": "services", "label": t("Services") }]} selectedItem={state.selectedTab} maxWidth=""></now-tabs>
            {renderTab(state, updateState, dispatch)}
        </div>
		{renderManageTeamRl(state)}
    </div>
}

function renderUpdateMembersAlert(state) {
	if (state.updateMembersPrecessing)
		return  <div className='update-team-alert'> <now-alert status="positive" icon="circle-check-outline" header="" content={t("Changes to membership can take a few minutes. This page will refresh when updates are complete.", state.nameValue)} action={{"type":"dismiss"}}></now-alert></div>
}

function renderManageTeamRl(state) {
	if (state.manageTeamRlOpen && state.selectedItem !== '')
		return <div>
			<sn-sr-ops-manage-team-rl team-id={state.properties.sysId} related-list={state.selectedItem} />
		</div>
}

function renderActions(state, dispatch) {
	if (state.canEditTeam)
		return <div className="button-row">
			<div className="save-action">
				<now-button tooltipContent={t('Save')} label={t('Save')} variant="secondary" disabled={!(state.nameValue)}
				on-click={() => {
                    if (state.nameValue)
                        dispatch(ACTIONS.SAVE_CLICKED)
                }}
				size="md"></now-button>
			</div>
		</div>
}

function renderMembersTab(state, dispatch) {
    let query = GROUP_MEMBER_CONSTANTS.GROUP_MEMBER_TABLE_QUERY + state.properties.sysId;
    return <div className="component-container">
        <div className="card-full">
        {renderManageMemberAction(state, dispatch)}
            <now-record-list-connected table={GROUP_MEMBER_CONSTANTS.GROUP_MEMBER_TABLE_NAME} query={query} listTitle={t("Members")} hideDeclarativeActions={true} refreshRequested={state.refreshPayload}
                columns={GROUP_MEMBER_CONSTANTS.GROUP_MEMBER_TABLE_COLUMNS} limit='10' headerSize='sm' headingLevel={2} variantHeader={{ size: 'sm', headingLevel: 2 }} hideMenuButton={true} hideQuickEdit={true} hideSelectAll={true} workspaceConfigId='1ae69deec7315010d721fff1c7c260b9' />
        </div>
    </div>
}

function renderManageMemberAction(state, dispatch) {
    if (state.canEditTeam)
        return <div className="banner-button">
            <now-button label={t('Manage Members')} tooltipContent={t('Manage Members')} variant="primary" size="md" on-click={() => { dispatch(ACTIONS.MANAGE_MEMBERS_CLICKED); }}></now-button>
        </div>;
}

function renderServiceTab(state, dispatch) {
    return <div className="component-container">
        <div className="card-full">
            <sn-sr-ops-my-services-list refresh-payload={state.refreshPayload} hide-new-action={state.hideNewAction} hide-manage-services-action={!state.canEditTeam} team-id={state.properties.sysId}></sn-sr-ops-my-services-list>
        </div>
    </div>
}

function renderDetailsTab(state, updateState, dispatch) {
	let content;
	if (state.loading)
		content = <now-loader label={t("Loading")} ></now-loader>;
	else
		content = <div>
			<br/>
				<div className="row">
					  <div className="column">
					  <sn-record-input readonly={!state.canEditTeam} required={true} value={state.nameValue} type="text" name="team-name" label={t("Name")}
										onValueChange={({ value, displayValue, error }) => {
										updateState({
												nameValue: value,
                                                nameDisplayValue: displayValue
                                            });
                                        }}
				 	 />
					</div>
					<div className="column">
					  <sn-record-input readonly={!state.canEditTeam} fieldType="text" name="team-email" label={t("Email")} value={state.emailValue}
									  onValueChange={({ value, error}) => {
										updateState({
												emailValue: value
											});
										}}
					  />
					</div>
				</div>
				<div className="row">
					<div className="column">
					  <now-record-field-lookup-connected
                          readonly={!state.canEditTeam}  
						  label={t("Manager")}
						  tableName="sys_user"
						  fieldName="name"
						  value={state.managerValue}
						  displayValue={state.managerDisplayValue}
						  onValueChange={({ value, displayValue }) => {
							updateState({
									managerValue: value,
									managerDisplayValue: displayValue
								});
							}}
					  />
					</div>
                    <div className="column">	
					  <now-record-field-lookup-connected
                          readonly={!state.canEditTeam}
						  label={t("Parent Team")}	
						  tableName="sys_user_group"	
						  fieldName="name"	
						  value={state.parentValue}	
						  displayValue={state.parentDisplayValue}	
						  onValueChange={({ value, displayValue }) => {	
							updateState({	
									parentValue: value,	
									parentDisplayValue: displayValue	
								});	
							}}	
					  />	
					</div>
				</div>
				<div className="row">
					<div className="column">
						  <sn-record-input readonly={!state.canEditTeam} type="textarea" name="team-desc" label={t("Description")} value={state.descValue}
									  onValueChange={({ value }) => {
										updateState({
												descValue: value
											});
										}}/>
					</div>
				</div>
				<br/>
		</div>;
    return <div className="component-container">
            <div className="card-full">
                {content}
			</div>
        </div>
}

function renderTab(state, updateState, dispatch) {
    switch (state.selectedTab) {
        case 'members':
            return renderMembersTab(state, dispatch);
        case 'services':
            return renderServiceTab(state, dispatch);
        case 'details':
            return renderDetailsTab(state, updateState, dispatch);
    }
}
