import {actionTypes} from '@servicenow/ui-core';
import {createTeamEffect, fetchUserDetails} from './dataSource';
import {ACTIONS} from "./constants";


const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const actionHandlers = {
    [COMPONENT_BOOTSTRAPPED]: ({state, dispatch, updateState}) => {
        dispatch(ACTIONS.FETCH_USER_DETAILS);
    },
    [ACTIONS.FETCH_USER_DETAILS]: fetchUserDetails,
    [ACTIONS.FETCH_USER_DETAILS_SUCCESS]: ({action, updateState}) => {
        let user = action.payload.data.GlideDomain_Query.user;
        updateState({
            managerValue: user.sys_id,
            managerDisplayValue: user.fullName,
            membersValue: user.sys_id,
            membersDisplayValue: user.fullName
        })
    },
    [ACTIONS.CANCEL_CLICKED]: ({updateProperties}) => {
        updateProperties({
            opened: false
        })
    },
    [ACTIONS.SAVE_CLICKED]: ({state, dispatch}) => {dispatch(ACTIONS.CREATE_SUBMITTED, {
        data: {
            'name': (state.nameValue ? state.nameValue + '' : ''),
            'manager': (state.managerValue ? state.managerValue + '' : ''),
            'email': (state.emailValue ? state.emailValue + '' : ''),
            'description': (state.descValue ? state.descValue + '' : ''),
			'members': (state.membersValue ? state.membersValue : ''),
			'tags': (state.tagsValue ? state.tagsValue : '')
        }
    })},
    [ACTIONS.CREATE_SUBMITTED]: createTeamEffect,
    [ACTIONS.CREATION_STARTED]: ({updateState}) => {updateState({
        loading: true
        });
    },
    [ACTIONS.CREATION_FINISHED]: ({state, dispatch, updateState, updateProperties}) => {
        dispatch(ACTIONS.TEAM_CREATED, {
            name: state.nameValue
        });
        updateState({
            loading: false
        });
        updateProperties({
            opened: false
        });
    },
    [ACTIONS.CREATION_FAILED]: ({dispatch, updateProperties}) => {
        updateProperties({
            opened: false
        });
        dispatch(ACTIONS.TEAM_CREATION_FAILED);
    }
};

export default actionHandlers;
