import {createCustomElement} from '@servicenow/ui-core';
import {snabbdom} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-record-list-connected';
import {t} from 'sn-translate';
import { fetchService } from './dataSource';
import { ACTIONS } from './constants';
import {getProperty} from 'sn-uxpage-presource';
import styles from './sn-sr-ops-my-services-list.scss';

const CMDB_TABLE_NAME = 'sn_sro_service_metrics';
const CMDB_TABLE_COLUMNS = 'service,service.service_classification,monitored_service.severity,alert_count,incident_count';
const CMDB_TABLE_QUERY_MY_SERVICES = 'service.support_groupDYNAMICd6435e965f510100a9ad2572f2b47744^ORservice.support_group.managerDYNAMIC90d1921e5f510100a9ad2572f2b477fe^ORservice.owned_byDYNAMIC90d1921e5f510100a9ad2572f2b477fe';
const CMDB_TABLE_QUERY_UNASSIGNED_SERVICES = 'service.sys_created_by=javascript:gs.getUserName()^service.support_groupISEMPTY';
const CMDB_TABLE_QUERY_MY_SERVICES_FOR_TEAM = 'service.support_group=';


const serviceTable = CMDB_TABLE_NAME;
const serviceColumns = CMDB_TABLE_COLUMNS;
const serviceDefaultQuery = CMDB_TABLE_QUERY_MY_SERVICES;

const view = (state, dispatch) => {
	const {properties: {filter, hideNewAction}} = state;
	return renderList(state, dispatch);
};

function renderList(state, dispatch) {
	const filterAlias = state.properties.filterAlias;
	let queryToUse = null;
	let recordListClass = "container fixed-height-class";
	let recordListLimit = '10';
	if(state.properties.filter) {
		queryToUse = state.properties.filter;
	}
	// If filter alias is specified override the query
	if(filterAlias) {
		// Default to my services
		queryToUse = CMDB_TABLE_QUERY_MY_SERVICES;
		if ('unassigned' === filterAlias) {
			queryToUse = CMDB_TABLE_QUERY_UNASSIGNED_SERVICES;
			recordListClass = "container fexible-height-class";
			recordListLimit = '20';
		}
	}

	if(state.properties.teamId !== '' && state.properties.teamId !== undefined) {
		queryToUse = CMDB_TABLE_QUERY_MY_SERVICES_FOR_TEAM + state.properties.teamId;
	}

	return <div className={recordListClass}>
		{renderCreateServiceAction(state, dispatch)}
		{renderManageServiceAction(state, dispatch)}
		<now-record-list-connected table={serviceTable} query={queryToUse} listTitle={t("Services")} hideDeclarativeActions={state.properties.hideDeclarativeAction} refreshRequested={state.properties.refreshPayload}
		item-selected-action-suffix="_ST_SERVICE" columns={serviceColumns} limit={recordListLimit} headerSize='sm' headingLevel={2} variantHeader={{size: 'sm', headingLevel: 2}} hideMenuButton={true} hideQuickEdit={true} hideSelectAll={true} workspaceConfigId='1ae69deec7315010d721fff1c7c260b9'/>
	</div>
}

function renderCreateServiceAction(state, dispatch) {
	const {properties: {hideNewAction}} = state;
	if (state.properties.hideNewAction == false)
		return <div className="banner-button">
			<now-button label={t("Create New Service")} variant="secondary" size="md" on-click={ () => { dispatch(ACTIONS.CREATE_SERVICE_CLICKED); } }></now-button>
		</div>;
}

function renderManageServiceAction(state, dispatch) {
	if (state.properties.hideManageServicesAction == false)
		return <div className="banner-button">
			<now-button label={t('Manage Services')} tooltipContent={t('Manage Services')} variant="primary" size="md" on-click={() => { dispatch('SRO_TEAM_PAGE#MANAGE_SERVICES_CLICKED'); }}></now-button>
		</div>;
}

createCustomElement('sn-sr-ops-my-services-list', {
	renderer: {type: snabbdom},
	view,
	styles,
	properties: {
		filterAlias: {
		},
        filter: {
            default: serviceDefaultQuery
        },
		hideNewAction: {
			default: false
		},
		hideDeclarativeAction: {
			default: true
		},
		refreshPayload: {
			default: {}
		},
		teamId: {
			default: ''
		},
		hideManageServicesAction: {
			default: true
		}
	},
	actionHandlers: {
		[ACTIONS.ITEM_SELECTED_ST_SERVICE]: ({action, dispatch, state}) => {
			const tbl = action.payload.table;
			const id = action.payload.sys_id;
			if(false == tbl.startsWith('cmdb_ci_')) {
				dispatch(ACTIONS.PREVIEW_RECORD, {
					table: tbl,
					sys_id: id
				});
			} else {
				dispatch(ACTIONS.FETCH_SERVICE, {sys_id: action.payload.sys_id});
			}
		},
		[ACTIONS.FETCH_SERVICE]: fetchService,
		[ACTIONS.FETCH_SERVICE_SUCCESS]: ({action, dispatch, state}) => {
			const tbl = action.payload.data.GlideRecord_Query.cmdb_ci_service._results[0].sys_class_name.value;
			const id = action.payload.data.GlideRecord_Query.cmdb_ci_service._results[0].sys_id.value;
			dispatch(ACTIONS.PREVIEW_RECORD, {
				table: tbl,
				sys_id: id
			});
		},
		[ACTIONS.CREATE_SERVICE_CLICKED]: ({state, dispatch}) => {
			let newServiceParams = {
				table: 'sn_sro_service_map',
				sys_id: '98926d4c73b210109d2f6238edf6a75b',
				params: {
					componentId: '98926d4c73b210109d2f6238edf6a75b',
					serviceId: '-1',
					isEdit: true
				}
			};
			dispatch(ACTIONS.ITEM_SELECTED, newServiceParams);
		}
	}
});

export default 'sn-sr-ops-my-services-list';
