import {t} from 'sn-translate';

import { ACTIONS, KEYS, DEFAULT_STROKE_COLOR, DEFAULT_STROKE_WIDTH, SELECTED_STROKE_COLOR, REMAINING_STROKE_COLOR, DEFAULT_BACKGROUND_COLOR, SELECTED_BACKGROUND_COLOR, SELECTED_STROKE_WIDTH, REMAINING_BACKGROUND_COLOR, FOCUS_STROKE_COLOR, FOCUS_BACKGROUND_COLOR, FOCUS_STROKE_WIDTH, DIAGRAM_A11Y_INSTRUCTIONS } from "./constants";
import { updateDiagramNode } from './update-diagram';

let isFocusingIntoDiagram = false;
let announcementTimeout = null;

function getSelectedNode(nodes) {
    let selectedNodes = nodes.filter((node) => {return node.isSelected == true;})
    if (selectedNodes && selectedNodes.length > 0) {
        return selectedNodes[0];
    }
    return null;
}

function getFocusNode(nodes) {
    let focusedNodes = nodes.filter((node) => {return node.focus == true;})
    if (focusedNodes && focusedNodes.length > 0) {
        return focusedNodes[0];
    }
    return null;
}

function getRootNodes(nodes, edges) {
    nodes.forEach((node) => {
        node.isRoot = null;
    });
    for (let i = 0; i < edges.length; i++) {
		nodes.forEach((node) => {
			if (node.key == edges[i].to) {
				node.isRoot = false;
			}
		});
	}

    let rootNodes = nodes.filter((node) => {return node.isRoot != false});

    nodes.forEach((node) => {node.isRoot = false;});
	return rootNodes;
}
function focusRootNode(diagram, nodes, edges, updateState, isEdit) {
    let rootNodes = getRootNodes(nodes, edges);

    if (rootNodes && rootNodes.length > 0) {
        focusNode(diagram, nodes, rootNodes[0], 0, updateState, isEdit);
    }
}

function removeFocus(diagram, nodes) {  
    nodes.forEach((node) => {
        if (node.data && node.data.isFocused) {
            node.data.isFocused = false;
            node.data.focusIndex = null;
            if (node.isSelected) {
                node.strokeColor = SELECTED_STROKE_COLOR;
                node.backgroundColor = SELECTED_BACKGROUND_COLOR;
                node.strokeWidth = SELECTED_STROKE_WIDTH;
            } else {
                node.strokeColor = DEFAULT_STROKE_COLOR;
                node.backgroundColor = DEFAULT_BACKGROUND_COLOR;
                node.strokeWidth = DEFAULT_STROKE_WIDTH;
            }
        }
    })
}

function updateA11yMessage(node, updateState, isEdit) {
  const {
      className,
      name,
      sys_id,
      data: {focusIndex}
  } = node;

  if (announcementTimeout)
    clearTimeout(announcementTimeout);

  let additionalNodeInstructions = '';
  if (isEdit)
    additionalNodeInstructions = sys_id ? t('To view this information, press Enter.') : t('To edit this information, press Enter.');

  let a11yMessage;
  switch (focusIndex) {
      case 0:
          a11yMessage =
            `${t('Service type: {0}. Service name: {1}.', className, name)} ${additionalNodeInstructions}`;
          break;
      case 1:
          a11yMessage = t('Service name: {0}, link.', name);
          break;
      case 2:
          a11yMessage = t('{0} popup menu, collapsed. To expand popup menu, press Enter.', name);
          break;
  }

  if (a11yMessage) {
      if (isFocusingIntoDiagram) {
        a11yMessage = `${DIAGRAM_A11Y_INSTRUCTIONS} ${a11yMessage}`;
        isFocusingIntoDiagram = false;
      }
      updateState({a11yMessage});

      // This is needed to separately announce services with the same name
      announcementTimeout = setTimeout(() => {
        updateState({a11yMessage: ''});
      }, 10000);
  }
}

function focusNode(diagram, nodes, node, focusIndex, updateState, isEdit) {
    removeFocus(diagram, nodes);
    node.data = {isFocused: true, focusIndex: focusIndex ? focusIndex: 0};
    node.strokeColor = FOCUS_STROKE_COLOR;
    node.backgroundColor = FOCUS_BACKGROUND_COLOR;
    node.strokeWidth = FOCUS_STROKE_WIDTH;
    let coords = diagram.findNodeForKey(node.key).part.getDocumentBounds();
    let isNodeRendered = isNaN(coords.x) && isNaN(coords.x) && diagram.viewportBounds.containsRect(diagram.findNodeForKey(node.key).part.actualBounds) == false;
    if (!isNodeRendered) {
        diagram.scrollToRect(coords);
    } else {
        setTimeout(() => {
            let coords = diagram.findNodeForKey(node.key).part.getDocumentBounds();
            diagram.scrollToRect(coords);
        });
    }
    diagram.updateAllTargetBindings();
    updateA11yMessage(node, updateState, isEdit);
}

function getChildNodes(nodes, edges, node) {
    let connectedChildEdges = edges.filter((edge) => {return edge.from == node.key;});
    let childNodes = [];
    if (connectedChildEdges && connectedChildEdges.length > 0) {
        for (let i = 0; i < connectedChildEdges.length; i++) {
            let cns = nodes.filter((node) => {return node.key === connectedChildEdges[i].to});
            childNodes = childNodes.concat(cns);
        }
    }
    return childNodes;
}

function getChildNode(state, nodes, edges, node) {
    const {
        diagram
    } = state;
    let connectedChildEdges = edges.filter((edge) => {return edge.from == node.key;});
    if (connectedChildEdges && connectedChildEdges.length > 0) {
        if (connectedChildEdges.length == 1) {
            let connectedNodes = nodes.filter((node) => {return node.key === connectedChildEdges[0].to});
            return connectedNodes[0];
        } else {
            let connectedNodes = nodes.filter((node) => {return node.key === connectedChildEdges[0].to});

            let returnChildNode = connectedNodes[0];
            let returnNodePosition = getNodePosition(diagram, connectedNodes[0].key);

            for (let i = 1; i < connectedChildEdges.length; i++) {
                let connectedNodes = nodes.filter((node) => {return node.key === connectedChildEdges[i].to});
                let position = getNodePosition(diagram, connectedNodes[0].key);

                if (state.layout != "horizontal" && position.y < returnNodePosition.y) {
                    returnChildNode = connectedNodes[0];
                    returnNodePosition = position;
                } else if (state.layout == "horizontal" && position.x < returnNodePosition.x) {
                    returnChildNode = connectedNodes[0];
                    returnNodePosition = position;
                }
            }
            return returnChildNode;
        }
    }
    return null;
}

function getParentNode(state, nodes, edges, node) {
    const {
        diagram
    } = state;
    let connectedParentEdges = edges.filter((edge) => {return edge.to == node.key;});
    if (connectedParentEdges && connectedParentEdges.length > 0) {
        if (connectedParentEdges.length == 1) {
            let connectedNodes = nodes.filter((node) => {return node.key === connectedParentEdges[0].from});
            return connectedNodes[0];
        } else {
            let connectedNodes = nodes.filter((node) => {return node.key === connectedParentEdges[0].from});

            let returnParentNode = connectedNodes[0];
            let returnNodePosition = getNodePosition(diagram, connectedNodes[0].key);

            for (let i = 1; i < connectedParentEdges.length; i++) {
                let connectedNodes = nodes.filter((node) => {return node.key === connectedParentEdges[i].from});
                let position = getNodePosition(diagram, connectedNodes[0].key);

                if (state.layout != "horizontal" && position.y > returnNodePosition.y) {
                    returnParentNode = connectedNodes[0];
                    returnNodePosition = position;
                } else if (state.layout == "horizontal" && position.x > returnNodePosition.x) {
                    returnParentNode = connectedNodes[0];
                    returnNodePosition = position;
                }
            }
            return returnParentNode;
        }
    }
    return null;
}

function getSiblingNodes(nodes, edges, node) {
    let connectedParentEdges = edges.filter((edge) => {return edge.to == node.key;});
    let siblingNodes = [];
    if (connectedParentEdges && connectedParentEdges.length > 0) {
        for (let i = 0; i < connectedParentEdges.length; i++) {
            let connectedParentNodes = nodes.filter((node) => {return node.key === connectedParentEdges[i].from});
            let childNodes = getChildNodes(nodes, edges, connectedParentNodes[0]);
            siblingNodes = siblingNodes.concat(childNodes);
        }
    }

    siblingNodes = siblingNodes.filter((n) => {return n.key != node.key});
    return siblingNodes;
}

function focusOnGraph(state, dispatch, updateState) {
    const {
        properties: {
            model,
            isEdit
        },
        diagram
    } = state;
    let fNode = getFocusNode(model.nodes, model.edges);
    let selectedNode = getSelectedNode(model.nodes, model.edges);
    if (fNode) {
        fNode.focus = false;
        focusNode(diagram, model.nodes, fNode, fNode.focusIndex, updateState, isEdit);
    } else if (!selectedNode) {
        focusRootNode(diagram, model.nodes, model.edges, updateState, isEdit);
    } else {
        focusNode(diagram, model.nodes, selectedNode, 0, updateState, isEdit);
    }
}

function focusChildNode(state, dispatch, updateState) {
    const {
        properties: {
            model,
            isEdit
        },
        diagram
    } = state;
    let focusedNode = model.nodes.filter((node) => {return node.data && node.data.isFocused == true;});
    if (focusedNode && focusedNode.length > 0) {
        focusedNode = focusedNode[0];
        let childNode = getChildNode(state, model.nodes, model.edges, focusedNode);
        if (childNode) {
            focusNode(diagram, model.nodes, childNode, 0, updateState, isEdit);
        }
    }
}

function focusParentNode(state, dispatch, updateState) {
    const {
        properties: {
            model,
            isEdit
        },
        diagram
    } = state;
    let focusedNode = model.nodes.filter((node) => {return node.data && node.data.isFocused == true;});
    if (focusedNode && focusedNode.length > 0) {
        focusedNode = focusedNode[0];
        let parentNode = getParentNode(state, model.nodes, model.edges, focusedNode);
        if (parentNode) {
            focusNode(diagram, model.nodes, parentNode, 0, updateState, isEdit);
        }
    }
}

function focusRightSibling(state, dispatch, updateState) {
    const {
        properties: {
            model,
            isEdit
        },
        diagram
    } = state;
    let focusedNode = model.nodes.filter((node) => {return node.data && node.data.isFocused == true;});
    if (focusedNode && focusedNode.length > 0) {
        focusedNode = focusedNode[0];
        let focusedNodePosition = getNodePosition(diagram, focusedNode.key);
        let siblingNodes = getSiblingNodes(model.nodes, model.edges, focusedNode);

        if (siblingNodes && siblingNodes.length > 0) {
            let nearestRightSibling = null;
            let nearestRightSiblingPosition = null;
            for (let i = 0; i < siblingNodes.length; i++) {
                let position = getNodePosition(diagram, siblingNodes[i].key);
                if (state.layout != "horizontal" && position.y === focusedNodePosition.y && position.x > focusedNodePosition.x) {
                    if (!nearestRightSibling) {
                        nearestRightSibling = siblingNodes[i];
                        nearestRightSiblingPosition = position;
                    } else if(position.x < nearestRightSiblingPosition.x) {
                        nearestRightSibling = siblingNodes[i];
                        nearestRightSiblingPosition = position;
                    }
                } else if (state.layout == "horizontal" && position.x === focusedNodePosition.x && position.y < focusedNodePosition.y) {
                    if (!nearestRightSibling) {
                        nearestRightSibling = siblingNodes[i];
                        nearestRightSiblingPosition = position;
                    } else if(position.y > nearestRightSiblingPosition.y) {
                        nearestRightSibling = siblingNodes[i];
                        nearestRightSiblingPosition = position;
                    }
                }
            }
            if (nearestRightSibling) {
                focusNode(diagram, model.nodes, nearestRightSibling, 0, updateState, isEdit);
            }
        } else {
            let isRootNode = model.edges.filter((edge) => { return edge.to == focusedNode.key;});
            if (isRootNode && isRootNode.length == 0) {
                let rootNodes = getRootNodes(model.nodes, model.edges);
                let nearestRightRootNode = null;
                let nearestRightRootNodePosition = null;
                for (let i = 0; i < rootNodes.length; i++) {
                    let position = getNodePosition(diagram, rootNodes[i].key);
                    if (state.layout != "horizontal" && position.y === focusedNodePosition.y && position.x > focusedNodePosition.x) {
                        if (!nearestRightRootNode) {
                            nearestRightRootNode = rootNodes[i];
                            nearestRightRootNodePosition = position;
                        } else if(position.x < nearestRightRootNodePosition.x) {
                            nearestRightRootNode = rootNodes[i];
                            nearestRightRootNodePosition = position;
                        }
                    } else if (state.layout == "horizontal" && position.x === focusedNodePosition.x && position.y < focusedNodePosition.y) {
                        if (!nearestRightRootNode) {
                            nearestRightRootNode = rootNodes[i];
                            nearestRightRootNodePosition = position;
                        } else if(position.y > nearestRightRootNodePosition.y) {
                            nearestRightRootNode = rootNodes[i];
                            nearestRightRootNodePosition = position;
                        }
                    }
                }
                if (nearestRightRootNode) {
                    focusNode(diagram, model.nodes, nearestRightRootNode, 0, updateState, isEdit);
                }
            }
        }
    }
}

function focusLeftSibling(state, dispatch, updateState) {
    const {
        properties: {
            model,
            isEdit
        },
        diagram
    } = state;
    let focusedNode = model.nodes.filter((node) => {return node.data && node.data.isFocused == true;});
    if (focusedNode && focusedNode.length > 0) {
        focusedNode = focusedNode[0];
        let focusedNodePosition = getNodePosition(diagram, focusedNode.key);
        let siblingNodes = getSiblingNodes(model.nodes, model.edges, focusedNode);

        if (siblingNodes && siblingNodes.length > 0) {
            let nearestLeftSibling = null;
            let nearestLeftSiblingPosition = null;
            for (let i = 0; i < siblingNodes.length; i++) {
                let position = getNodePosition(diagram, siblingNodes[i].key);
                if (state.layout != 'horizontal' && position.y === focusedNodePosition.y && position.x < focusedNodePosition.x) {
                    if (!nearestLeftSibling) {
                        nearestLeftSibling = siblingNodes[i];
                        nearestLeftSiblingPosition = position;
                    } else if(position.x > nearestLeftSiblingPosition.x) {
                        nearestLeftSibling = siblingNodes[i];
                        nearestLeftSiblingPosition = position;
                    }
                } else if (state.layout == 'horizontal' && position.x === focusedNodePosition.x && position.y > focusedNodePosition.y) {
                    if (!nearestLeftSibling) {
                        nearestLeftSibling = siblingNodes[i];
                        nearestLeftSiblingPosition = position;
                    } else if(position.y < nearestLeftSiblingPosition.y) {
                        nearestLeftSibling = siblingNodes[i];
                        nearestLeftSiblingPosition = position;
                    }
                }
            }
            if (nearestLeftSibling) {
                focusNode(diagram, model.nodes, nearestLeftSibling, 0, updateState, isEdit);
            }
        }  else {
            let isRootNode = model.edges.filter((edge) => { return edge.to == focusedNode.key;});
            if (isRootNode && isRootNode.length == 0) {
                let rootNodes = getRootNodes(model.nodes, model.edges);
                let nearestLeftRootNode = null;
                let nearestLeftRootNodePosition = null;
                for (let i = 0; i < rootNodes.length; i++) {
                    let position = getNodePosition(diagram, rootNodes[i].key);
                    if (state.layout != "horizontal" && position.y === focusedNodePosition.y && position.x < focusedNodePosition.x) {
                        if (!nearestLeftRootNode) {
                            nearestLeftRootNode = rootNodes[i];
                            nearestLeftRootNodePosition = position;
                        } else if(position.x > nearestLeftRootNodePosition.x) {
                            nearestLeftRootNode = rootNodes[i];
                            nearestLeftRootNodePosition = position;
                        }
                    } else if (state.layout == "horizontal" && position.x === focusedNodePosition.x && position.y > focusedNodePosition.y) {
                        if (!nearestLeftRootNode) {
                            nearestLeftRootNode = rootNodes[i];
                            nearestLeftRootNodePosition = position;
                        } else if(position.y < nearestLeftRootNodePosition.y) {
                            nearestLeftRootNode = rootNodes[i];
                            nearestLeftRootNodePosition = position;
                        }
                    }
                }
                if (nearestLeftRootNode) {
                    focusNode(diagram, model.nodes, nearestLeftRootNode, 0, updateState, isEdit);
                }
            }
        }
    }
}

function getNodePosition(diagram, key) {
    return {
        x: diagram.transformDocToView(diagram.findNodeForKey(key).position).x,
        y: diagram.transformDocToView(diagram.findNodeForKey(key).position).y
    }
}

function onTabPress(state, updateState) {
    const {
        properties: {
            model,
            isEdit
        },
        diagram
    } = state;
    let isHandled = false;
    let focusedNode = model.nodes.filter((node) => {return node.data && node.data.isFocused == true;});
    if (focusedNode && focusedNode.length > 0) {
        focusedNode = focusedNode[0];

        if (focusedNode.data.focusIndex < 2) {
            focusedNode.data.focusIndex++;
            isHandled = true;
            if (focusedNode.data.focusIndex == 1 && !focusedNode.sys_id) {
                focusedNode.data.focusIndex++;
            }
            updateA11yMessage(focusedNode, updateState, isEdit);
        }
    }
    diagram.updateAllTargetBindings();
    return isHandled;
}

function onShiftTabPress(state, updateState) {
    const {
        properties: {
            model,
            isEdit
        },
        diagram
    } = state;
    let isHandled = false;
    let focusedNode = model.nodes.filter((node) => {return node.data && node.data.isFocused == true;});
    if (focusedNode && focusedNode.length > 0) {
        focusedNode = focusedNode[0];

        if (focusedNode.data.focusIndex != 0) {
            focusedNode.data.focusIndex--;
            isHandled = true;
            if (focusedNode.data.focusIndex == 1 && !focusedNode.sys_id) {
                focusedNode.data.focusIndex--;
            }
            updateA11yMessage(focusedNode, updateState, isEdit);
        }
    }
    diagram.updateAllTargetBindings();
    return isHandled;
}

function enterPress(state, dispatch) {
    const {
        properties: {
            model
        },
        diagram
    } = state;
    let focusedNode = model.nodes.filter((node) => {return node.data && node.data.isFocused == true;});
    if (focusedNode && focusedNode.length > 0) {
        focusedNode = focusedNode[0];
        if (focusedNode.data.focusIndex == 0) {
            removeFocus(diagram, model.nodes);
            diagram.updateAllTargetBindings();
            dispatch(ACTIONS.NODE_SELECTED, {data: focusedNode, actionType: 'enter-key'});
        } else if (focusedNode.data.focusIndex == 1) {
            dispatch(ACTIONS.LINK_CLICKED, {data: focusedNode});
        } else if (focusedNode.data.focusIndex == 2) {
            dispatch(ACTIONS.TOGGLE_CONTEXT_MENU, {cordinates: getNodePosition(diagram, focusedNode.key), data: focusedNode})
            setTimeout(() => {
                let element = state.element.elm;
                if (element.querySelector('.context-menu .menu-item')) {
                    element.querySelector('.context-menu .menu-item').focus();
                }
            }, 100);
        }
    }
}

export function onKeyDown(state, dispatch, updateState, event) {
    const {
        properties: {
            model
        },
        diagram
    } = state;
    if ([KEYS.ARROW_DOWN, KEYS.ARROW_UP, KEYS.ARROW_LEFT, KEYS.ARROW_RIGHT].indexOf(event.which) !== -1) {
        event.preventDefault();
        event.stopPropagation();
    }
    let isHandled = false;
    if (event.which == KEYS.TAB && event.shiftKey == true) {
        if (event.target.classList.contains('gojs')) {
            isHandled = onShiftTabPress(state, updateState);
        }
        if (isHandled)
            event.preventDefault();
        else {
            removeFocus(diagram, model.nodes);
            diagram.updateAllTargetBindings();
        }
    } else if (event.which == KEYS.TAB) {
        if (event.target.classList.contains('gojs')) {
            isHandled = onTabPress(state, updateState);
        }
        if (isHandled)
            event.preventDefault();
        else {
            removeFocus(diagram, model.nodes);
            diagram.updateAllTargetBindings();
        }
    } else if (event.which == KEYS.ENTER) {
        enterPress(state, dispatch);
    }
}

export function onFocus(state, dispatch, updateState, focusIntoDiagram) {
    isFocusingIntoDiagram = focusIntoDiagram;
    focusOnGraph(state, dispatch, updateState);
}

const keyHandlers = {
	[ACTIONS.KEYUP]: ({action, dispatch, updateState, state}) => {
        switch(action.payload.event.which) {
            case KEYS.ARROW_DOWN:
                if (state.layout != "horizontal") {
                    focusChildNode(state, dispatch, updateState);
                } else {
                    focusLeftSibling(state, dispatch, updateState);
                }
                break;
            case KEYS.ARROW_UP:
                if (state.layout != "horizontal") {
                    focusParentNode(state, dispatch, updateState);
                } else {
                    focusRightSibling(state, dispatch, updateState);
                }
                break;
            case KEYS.ARROW_LEFT:
                if (state.layout != "horizontal") {
                    focusLeftSibling(state, dispatch, updateState);
                } else {
                    focusParentNode(state, dispatch, updateState);
                }
                break;
            case KEYS.ARROW_RIGHT:
                if (state.layout != "horizontal") {
                    focusRightSibling(state, dispatch, updateState);
                } else {
                    focusChildNode(state, dispatch, updateState);
                }
                break;
        }
    },
    [ACTIONS.MENU_KEYDOWN]: ({action, dispatch, updateState, state}) => {
        switch(action.payload.event.which) {
            case KEYS.ARROW_DOWN:
                let nextSibling = action.payload.target.nextSibling;
                if (nextSibling) {
                    nextSibling.focus();
                }
                break;
            case KEYS.ARROW_UP:
                let previousSibling = action.payload.target.previousSibling;
                if (previousSibling) {
                    previousSibling.focus();
                }
                break;
            case KEYS.ESCAPE:
                dispatch(ACTIONS.INTERNAL_STATE_SET, {isMenuVisible: false});
                action.payload.data.focus = true;
                action.payload.data.focusIndex = 2;
                setTimeout(() => {
                    let element = state.element.elm;
                    element.querySelector('.gojs').focus();
                    onFocus(state, dispatch, updateState, false);
                });
                break;
            case KEYS.TAB:
                dispatch(ACTIONS.INTERNAL_STATE_SET, {isMenuVisible: false});
                action.payload.data.focus = true;
                action.payload.data.focusIndex = 2;
                setTimeout(() => {
                    let element = state.element.elm;
                    element.querySelector('.gojs').focus();
                    onFocus(state, dispatch, updateState, false);
                });
                break;
        }
    },
}

export default keyHandlers;