
export const servicesQuery = 'service.support_groupDYNAMICd6435e965f510100a9ad2572f2b47744^ORservice.support_group.managerDYNAMIC90d1921e5f510100a9ad2572f2b477fe^ORservice.owned_byDYNAMIC90d1921e5f510100a9ad2572f2b477fe'; //All services supported by one of my groups
export const cmdbCiServicesQuery = 'sys_class_name=cmdb_ci_service^ORsys_class_name=cmdb_ci_service_discovered^support_groupDYNAMICd6435e965f510100a9ad2572f2b47744^ORsupport_group.managerDYNAMIC90d1921e5f510100a9ad2572f2b477fe^ORowned_byDYNAMIC90d1921e5f510100a9ad2572f2b477fe'; //All services supported by one of my groups
export const unassignedServiceQuery = 'service.sys_created_by=javascript:gs.getUserName()^service.support_groupISEMPTY';

export const fetchMyServicesQueryGQL = `query {
	GlideAggregateRecord_Query(tableName: "sn_sro_service_metrics", queryConditions: "${servicesQuery}") {
		aggregates{
			count
		}
	}
}`;

export const fetchUnassignedServicesQueryGQL = `query {
	GlideAggregateRecord_Query(tableName: "sn_sro_service_metrics", queryConditions: "${unassignedServiceQuery}") {
		aggregates{
			count
		}
	}
}`;

export const fetchMetricsRecordsQueryGQL = `query {
	GlideRecord_Query {
	  sn_sro_service_metrics(queryConditions: "serviceISNOTEMPTY", pagination: {limit: 1000}) {
		_results {
		  sys_id {
			value
		  }
		  alert_count {
			value
		  }
		  incident_count {
			value
		  }
		}
	  }
	}
  }`;

export const fetchRelatedRecordsQueryGQL = `query($table: String!, $filter: String!) {
	GlideRecord_Query {
	cmdb_ci_service(queryConditions: "${cmdbCiServicesQuery}") {
	_results {
		sys_id {
			value
		} 
		name {
			displayValue
		}
			_query {						
				GlideAggregateRecord_Query(tableName: $table, queryConditions: $filter) {
					aggregates{
						count
					}
				}
			}
		}
		}
	}
}`;


export const fetchServiceChangeRequestCount = `query {
	GlideAggregateRecord_Query(tableName: "change_request", queryConditions: "active=true^cmdb_ci.sys_class_name=cmdb_ci_service_discovered^ORcmdb_ci.sys_class_name=cmdb_ci_service^ORcmdb_ci.sys_class_name=cmdb_ci_query_based_service^cmdb_ci.support_groupDYNAMICd6435e965f510100a9ad2572f2b47744^ORcmdb_ci.support_group.managerDYNAMIC90d1921e5f510100a9ad2572f2b477fe^ORcmdb_ci.owned_byDYNAMIC90d1921e5f510100a9ad2572f2b477fe^NQactive=true^business_service.sys_class_name=cmdb_ci_service_discovered^ORbusiness_service.sys_class_name=cmdb_ci_service^ORbusiness_service.sys_class_name=cmdb_ci_query_based_service^business_service.support_groupDYNAMICd6435e965f510100a9ad2572f2b47744^ORbusiness_service.support_group.managerDYNAMIC90d1921e5f510100a9ad2572f2b477fe^ORbusiness_service.owned_byDYNAMIC90d1921e5f510100a9ad2572f2b477fe") {
		aggregates{
			count
		}
	}
}`;