import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import { ACTIONS } from "./constants";

const actionsQuery = "ORDERBYname";

const fetchQueryGQL = `query {
	GlideRecord_Query {
		actions: sn_sro_act(queryConditions: "${actionsQuery}") {
			_results {
				sys_id {
                    value
                }
				name {
                    value
                    displayValue
                }
                icon {
                    value
                }
                cfg_req {
                    value
                }
			}
		}
	}
}`;

export const getAllActionsEffect = createGraphQLEffect(
	fetchQueryGQL, {
	successActionType: ACTIONS.FETCH_ALL_ACTIONS_REQUEST_SUCCEEDED,
});