import {createCustomElement} from '@servicenow/ui-core';
import {createGraphQLEffect} from "@servicenow/ui-effect-graphql";
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {actionTypes} from '@servicenow/ui-core';
import { t } from 'sn-translate';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-sr-ops-get-started-info.scss';
import {fetchServicesQuery, fetchTeamsQuery} from './dataSource';
import '@servicenow/now-icon';
import '@servicenow/now-button';
import '../sn-sr-ops-create-team';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;
const view = (state, {updateState, dispatch}) => {
	const {servicesLoaded, teamsLoaded, showAlert, canCreateService} = state;
	if (showAlert && servicesLoaded && teamsLoaded && canCreateService && state.servicesCount == 0 && state.teamsCount == 0) {
		return renderAlert(state, {updateState, dispatch});
	}
	else if (state.teamCreated)
		return <now-alert status="positive" icon="circle-check-outline" header="" content={t("Request submitted. {0} will appear on the teams page once it's approved.", state.newTeamName)} action={{"type":"dismiss"}}></now-alert>
	else
		return null;
};

function renderTeamsLink(state, {updateState, dispatch}) {
	const {canCreateTeam} = state;
	if (state.canCreateTeam && state.createTeamSetupComplete)
		return <div>
			<a className="link-text" href="javascript:void(0);" on-click={() => { updateState({...state, isAddTeamModalVisible: true}); return false;} }>{'teams'}</a>
			&nbsp;
			<div className="alert-content">and</div>
			&nbsp;	
		</div>;
}

function renderServicesLink(state, {updateState, dispatch}) {
	const {canCreateService} = state;
	if (state.canCreateService)
		return <a className="link-text" href="javascript:void(0);" on-click={() => { dispatch("NOW_GET_STARTED_CREATE_SERVICE");} }>{'services'}</a>;
}

function renderTeamsSetupText(state, {updateState, dispatch}) {
	if (state.canCreateTeam && state.createTeamSetupComplete == false)
		return <div>
			&nbsp;
			<div className="alert-content">{t('To set up a team, the system administrator must configure the integration hub flow. Contact your system administrator.')}</div>
		</div>;
}

function renderAlert(state, {updateState, dispatch}) {
	return <div>
		<div className="alert">
			<div className="icon">
				<now-icon icon="bell-outline" size="md"></now-icon>
			</div>
			<div className="alert-container">
				<div className="alert-content">{t('Getting Started? Get started by setting up')}</div>
				&nbsp;
				{renderTeamsLink(state, {updateState, dispatch})}
				{renderServicesLink(state, {updateState, dispatch})}
				<div className="link-text">.</div>
				{renderTeamsSetupText(state, {updateState, dispatch})}
			</div>
			<div>
				<now-button-stateful icon="close-outline" tooltipContent="close" configRole="button" size="md" variant="tertiary" configAria={{"aria-label":"Close"}}></now-button-stateful>
			</div>		
		</div>
		{renderTeamModal(state)}
	</div>
};

function renderTeamModal(state) {
	if (state.isAddTeamModalVisible) {
		return (
			<sn-sr-ops-create-team/>
		);
	}
}
const fetchServicesEffect = createGraphQLEffect(
	fetchServicesQuery, {
	successActionType: "FETCH_SERVICES_SUCCESS",
	errorActionType: "FETCH_SERVICES_FAILURE"
});

const fetchServicesSuccess = ({ action, updateState }) => {
	let data = action.payload.data.GlideAggregateRecord_Query.aggregates[0].count;
	updateState({servicesCount: data, servicesLoaded: true});
};

const fetchServicesFailure = ({ action, updateState, dispatch }) => {
	console.log("Failure fetching services");
};

const fetchTeamsEffect = createGraphQLEffect(
	fetchTeamsQuery, {
	successActionType: "FETCH_TEAMS_SUCCESS",
	errorActionType: "FETCH_TEAMS_FAILURE"
});

const fetchTeamsSuccess = ({ action, updateState }) => {
	let data = action.payload.data.GlideRecord_Query.sys_user_group._results.length;
	updateState({teamsCount: data, teamsLoaded: true});
};

const fetchTeamsFailue = ({ action, updateState, dispatch }) => {
	console.log("Failure fetching teams");
};

//Create team access
const fetchTeamACLDataEffect = createHttpEffect("api/sn_sro/sr_ops_workflow/getUpdateTeamAccess/''", {
	method: 'GET',
	successActionType: 'FETCH_TEAM_ACL_SUCCESS',
	errorActionType: 'FETCH_TEAM_ACL_FAILURE'
});

const fetchTeamACLDataSuccessEffect = ({action, updateState}) => {
	updateState({canCreateTeam: action.payload.canCreate, createTeamSetupComplete: action.payload.ihubSetupComplete});
};

const fetchTeamACLDataFailureEffect = ({action, updateState}) => {
	console.log("Failure fetching create team access for logged in user");
};

//Create service access
const fetchCreateServiceAccess = createHttpEffect('/api/sn_sro/sr_ops_workflow/getCreateAccess', {
	method: 'GET',
	successActionType: "FETCH_SERVICE_ACL_SUCCESS",
	errorActionType: "FETCH_SERVICE_ACL_FAILURE",
});

const fetchCreateServiceAccessSuccess = ({ action, updateState, dispatch }) => {
	updateState({canCreateService: action.payload});
}

const fetchCreateServiceAccessFailure = ({action, updateState}) => {
	console.log("Failure fetching create service access for logged in user");
};

createCustomElement('sn-sr-ops-get-started-info', {
	renderer: {type: snabbdom},
	initialState: {
		isAddTeamModalVisible: false,
		servicesCount: 0,
		showAlert: true,
		teamsCount: 0,
		canCreateTeam: false,
		createTeamSetupComplete: false,
		teamCreated: false
	},
	view,
	styles,
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({state, dispatch, updateState}) => {
			dispatch("FETCH_SERVICES");
			dispatch("FETCH_TEAMS");
			dispatch("FETCH_TEAM_ACL");
			dispatch("FETCH_SERVICE_ACL");
		},
		"FETCH_SERVICES": fetchServicesEffect,
		"FETCH_SERVICES_SUCCESS": fetchServicesSuccess,
		"FETCH_SERVICES_FAILURE": fetchServicesFailure,
		"FETCH_TEAMS": fetchTeamsEffect,
		"FETCH_TEAMS_SUCCESS": fetchTeamsSuccess,
		"FETCH_TEAMS_FAILURE": fetchTeamsFailue,
		"FETCH_TEAM_ACL": fetchTeamACLDataEffect,
		"FETCH_TEAM_ACL_SUCCESS": fetchTeamACLDataSuccessEffect,
		"FETCH_TEAM_ACL_FAILURE": fetchTeamACLDataFailureEffect,
		"FETCH_SERVICE_ACL": fetchCreateServiceAccess,
		"FETCH_SERVICE_ACL_SUCCESS": fetchCreateServiceAccessSuccess,
		"FETCH_SERVICE_ACL_FAILURE": fetchCreateServiceAccessFailure,
		"NOW_BUTTON_STATEFUL#SELECTED_SET": ({action, dispatch, updateState}) => {
			updateState({showAlert: false});
		},
		"SRO_TEAM#CANCEL_CLICKED": ({updateState}) => {
			updateState({isAddTeamModalVisible: false});
		},
		'NOW_MODAL#OPENED_SET': ({updateState}) => {
			updateState({isAddTeamModalVisible: false});
		},
		"SRO_TEAM#TEAM_CREATED": ({action, updateState}) => {
			updateState({
				teamsCount: 1,
				newTeamName: action.payload.name,
				teamCreated: true
			});
		},
		"NOW_GET_STARTED_CREATE_SERVICE": ({state, dispatch}) => {
			let newServiceParams = {
				table: 'sn_sro_service_map',
				sys_id: '98926d4c73b210109d2f6238edf6a75b',
				params: {
					componentId: '98926d4c73b210109d2f6238edf6a75b',
					serviceId: '-1',
					isEdit: true
				}
			};
			dispatch("ITEM_SELECTED", newServiceParams);
		},
		'NOW_ALERT#ACTION_CLICKED': ({action, updateState}) => {
			if (action.payload.action.type === 'dismiss') {
				updateState({
					teamCreated: false
				})
			}
		}
	}
});

export default 'sn-sr-ops-get-started-info';
