import {createHttpEffect} from '@servicenow/ui-effect-http';
import { ACTIONS } from "./constants";

export const fetchConditionRecord = createHttpEffect('api/now/table/:tableName/:sysId', {
	method: 'GET',
	dataParam: 'data',
	pathParams: ['tableName', 'sysId'],
	successActionType: ACTIONS.FETCH_CONDITION_SUCCESS,
	errorActionType: ACTIONS.FETCH_CONDITION_ERROR
});