import '@servicenow/now-record-list-connected';
import '@servicenow/now-button';
import '@servicenow/now-card';
import '@servicenow/now-record-common';
import '@servicenow/now-empty-state';
import { t } from 'sn-translate';
import '../sn-sr-ops-my-services-list';
import '../sn-sr-ops-alerts';
import '../sn-sr-ops-incidents';
import '../sn-sr-ops-changes';
import '../sn-sr-ops-service-map';
import '@servicenow/now-tabs';
import { ACTIONS } from './constants';

export function renderPage(state, {updateState, dispatch}) {
    const {unassignedServicesCount, refreshRequested, showList, table, query, title, column} = state;
    let unassignedLabel = t("Unassigned Services ({0})",state.unassignedServicesCount);
    let actions = [{
		actionComponent: 'sn-sr-ops-alerts',
		actionAttributes: {
            refreshAlert: state.refreshAlertRequested,
            className: 'sn-sr-ops-alerts--services-page'
		},
		order: 0,
		assignmentId: `sn-sr-ops-alerts`,
		name: 'sn-sr-ops-alerts',
		tooltip: t('Alert List'),
		icon: 'lightning-outline'
    }, {
		actionComponent: 'sn-sr-ops-incidents',
		actionAttributes: {
            refreshIncident: state.refreshIncidentRequested,
            className: 'sn-sr-ops-incidents--services-page'
		},
		order: 1,
		assignmentId: `sn-sr-ops-incidents`,
		name: 'sn-sr-ops-incidents',
		tooltip: t('Incident List'),
		icon: 'wrench-outline'
    }, {
			actionComponent: 'sn-sr-ops-changes',
			actionAttributes: {
				refreshChange: state.refreshChangeRequested,
				className: 'sn-sr-ops-changes--services-page'
			},
			order: 1,
			assignmentId: 'sn-sr-ops-changes',
			name: 'sn-sr-ops-changes',
			tooltip: t('Changes List'),
			icon: 'change-outline'
        }];
        
    if(state.showList == true)
        return <div>
                <div className="list-container">
                    <div className="back-button">
                        <now-button-bare label={t('Back')} icon-start="arrow-left-outline" variant="tertiary" size="md" on-click={ () => { dispatch(ACTIONS.RETURN); } }/>
                    </div>
                    <div className="record-list">
                        <now-record-list-connected table={state.table} listTitle={title} hideDeclarativeActions={true} query={state.query}
                        columns={column} limit='10' variantHeader={{size: 'sm', headingLevel: 2}} hideMenuButton={true} hideQuickEdit={true} hideSelectAll={true}/>
                    </div>
                </div>
            </div>;
    else {
        let tabClass = ['tab-container'];
        let bodyClass = ['body-wrapper'];
        if (state.selectedTab === "unassigned") 
            bodyClass.push('body-wrapper--hide-overflow');
        return <div className={tabClass.join(' ')}>
        <div className="banner">
            <div className="banner-text">
                <span className="page-header">{t('Services Overview')}</span>
            </div>
            {renderCreateServiceButton(state, dispatch)}
        </div>
        <div className={bodyClass.join(' ')}>
                <div className="left-container">
                    <now-tabs items={[{"id":"myServices","label":t("My Services")},{"id":"unassigned","label":unassignedLabel}]} selectedItem={state.selectedTab} maxWidth=""></now-tabs>
                    {renderTab(state, actions, dispatch)}
                </div>
                <div className="right-container">
                    <now-record-common-sidebar
                        actions={actions}
                    />
                </div>
            </div>
        </div>;
    }
}

function renderCreateServiceButton(state, dispatch) {
    const {canCreateService} = state;
    if (state.canCreateService == true) {
        return <div className="banner-button">
            <now-button label={t("Create New Service")} variant="primary" size="md" on-click={ () => { dispatch(ACTIONS.CREATE_SERVICE_ACTION_CLICKED); } }></now-button>
        </div>;
    }
}

function renderMyServicesTab(state, actions, dispatch) {
	const chartStlyConfiguration = {
		width: 'auto',
    	height: 'auto',
		dataLabelConfig: {
			align: 'middle'
		}
	};
        return <div id="myServices" className="my-services">
            <div className="component-container">
                <div className="card-gap">
                        <now-card append-to-payload={{...state.alertDetails, type: 'overview-card'}} size="lg" interaction="click" config-aria={{'aria-label': t("Active Alerts")}}>
                            <div className="report-header">{t("Active Alerts")}</div>
                            <span className="card-body">{state.alerts}</span>
                        </now-card>
                </div>
                <div className="card-gap">
                        <now-card append-to-payload={{...state.incidentDetails, type: 'overview-card'}} size="lg" interaction="click" config-aria={{'aria-label': t("Open Incidents")}}>
                            <div className="report-header">{t("Open Incidents")}</div>
                            <span className="card-body">{state.incidents}</span>
                        </now-card>
                </div>
                <div className="card-gap">
                        <now-card append-to-payload={{...state.changeDetails, type: 'overview-card'}} size="lg" interaction="click" config-aria={{'aria-label': t("Open Changes")}}>
                            <div className="report-header">{t("Open Changes")}</div>
                            <span className="card-body">{state.changes}</span>
                        </now-card>
                </div>
            </div>
			<sn-sr-ops-my-services-list refresh-payload={state.refreshPayload} hide-new-action={state.hideNewAction}></sn-sr-ops-my-services-list>
        </div>;
}

function renderTab(state, actions, dispatch) {
    if (state.selectedTab == "myServices")
        return renderMyServicesTab(state, actions, dispatch);
   else if (state.selectedTab == "unassigned")
    return <div className="unassigned-list" id="unassigned">
    <sn-sr-ops-my-services-list filterAlias="unassigned" refresh-payload={state.refreshPayload} hide-new-action={state.hideNewAction}></sn-sr-ops-my-services-list>
    </div>;
}

export function renderNoServiceMessage(state, {updateState, dispatch}) {
return <div className="no-service">
            <div className="banner">
                <div className="banner-text">
                    <span className="page-header">{t('Services Overview')}</span>
                </div>
                {renderCreateServiceButton(state, dispatch)}
            </div>
            <div className="divider"/>
            <div className="center-div-wrapper">
                <div className='center-div'>
                    <now-empty-state type="empty" variant="fit" bodyText={false} headerText={false}/>
                    <div className="text1">{t("Welcome! You don't have any services yet.")}</div>
                    <div className="text2">{ /* BUG: Wrong way to translate */ }
                        <span>{t('Create a')}</span>&nbsp;
                            <now-text-link label={t('new service')} href='javascript:void(0);' on-click={(e) => dispatch(ACTIONS.OPEN_NODE_MAP) }underlined={false} variant="primary"/>
                        &nbsp;
                        <span>{t('to get started.')}</span>
                    </div>
                </div>
            </div>
        </div>;
}

export function renderServiceMap(state, {updateState}) {
    return (
        <div className="service-map-wrapper">
            <sn-sr-ops-service-map></sn-sr-ops-service-map>
        </div>
    );
}

export const returnToOverview = ({state, updateState}) => {
    const {showList} = state;
    updateState({showList: false});
};

export const openCreateServiceModal = () => {
};
