import {createCustomElement} from '@servicenow/ui-core';
import {snabbdom} from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-sr-ops-ar-conditions.scss';
import {arConditionHandlers} from './actionHandlers';
import {renderConditions} from './view';

const view = (state, coeffects) => {
	return renderConditions(state, coeffects);
}

createCustomElement('sn-sr-ops-ar-conditions', {
	renderer: {type: snabbdom},
	initialState: {
		count : 0,
		tempCondDisplayValue: [],
		openConditionsReferenceListModal: false,
		showEmptyCondition: true,
	},
	properties: {
		conditions: {
			default : ''
		},
		isLegacyRule: {
			default: ''
		},
		refresh: {
			default : false
		},
		isNewAlertRule: {
			default: ''
		}
	},
	styles,
	view,
	actionHandlers: arConditionHandlers
});

export default 'sn-sr-ops-ar-conditions';
