const fetchServicesSuccessGQL = ({ action, updateState, dispatch }) => {
	const data = action.payload.data.GlideAggregateRecord_Query.aggregates[0].count;
	updateState({servicesCount: data, hasLoaded: true});
	if (data > 0){
		dispatch(ACTIONS.FETCH_METRICS_GQL);
		dispatch(ACTIONS.FETCH_CHANGES_GQL, {});
	}
}

