const renderList = (state, action, updateState) => {
	const tableName = `${action.payload[0].table}`;
	const queryOverride = action.payload[0].query;

	let stateUpdate = {};

	switch(tableName) {
		case "em_alert":
			stateUpdate = {
				query: queryOverride ? `${queryOverride}` : alertsQuery,
				title: t("Active Alerts"),
				column: "number, description, cmdb_ci, severity, sys_created_on"
			};
			break;
		case "incident":
			stateUpdate = {
				query: queryOverride ? `${queryOverride}` : incidentsQuery,
				title: t("Open Incidents"),
				column: "number, short_description, caller_id, priority, state, category"
			};
			break;
		case "change_request":
			stateUpdate = {
				query: queryOverride ? `${queryOverride}` : changesQuery,
				title: t("Open Changes"),
				column: "number, short_description, requested_by, priority, state, category"
			};
			break;
	}

	if (Object.keys(stateUpdate).length > 0)
		updateState({
			...stateUpdate,
			showList: true,
			table: tableName
		});
};

