const getMetricsCount = result => {
	var ret = {};
	var alertCount = 0, incidentCount = 0, i;
	console.log('2: ' + JSON.stringify(result));
	for (i=0; i<result.length; i++){
		console.log(i + ': alertCount: ' + alertCount + ' + ' + parseInt(result[i].alert_count.value));
		alertCount += parseInt(result[i].alert_count.value);
		console.log('new alertCount: ' + alertCount);
		incidentCount += parseInt(result[i].incident_count.value);
	}
	ret['alerts'] = alertCount;
	ret['incidents'] = incidentCount;
	return ret;
}

const fetchMetricsSuccessGQL = coeffects => {
	const {action: {payload}, updateState} = coeffects;
	const {result: {alert_count:alerts,incident_count:incidents}} = payload;
	updateState({alerts, incidents});
}

