//Record watcher apis
const recordWatcher = createAmbSubscriptionEffect('/rw/default/sn_sro_service_metrics/:filter', {
	messageReceivedActionType: RW_ACTIONS.MESSAGE_RECEIVED,
});

const refreshServices = ({ action, updateState, dispatch }) => {
	const CHANGE_IGNORE_WINDOW = 10000;
	if(Date.now()-CHANGE_IGNORE_WINDOW < refreshTime) {
		// If we have not started a timer start one
		if(-1 == pendingUpdates) {
			// Update the counter so we don't start another timer
			pendingUpdates++;
			// Kick off timer trying again outside window time
			console.log("Starting queued updates timer");
			setTimeout(function () {
				console.log("Executing queued updates timer");
			    dispatch(RW_ACTIONS.MESSAGE_RECEIVED);
			}, CHANGE_IGNORE_WINDOW);
			// Do nothing else
			return;
		}
		// It didn't trap in the above so a timer was kicked off just cpature count
		pendingUpdates++
		console.log(pendingUpdates + " updates queued");
		// Do nothing else
		return;
	}
	// We are within the window so make it OK to start another timer to buffer further events
	pendingUpdates = -1;
	dispatch(ACTIONS.FETCH_SERVICES_GQL);
	dispatch(ACTIONS.FETCH_UNASSIGNED_SERVICES_GQL);
	refreshAlertCount++;
	updateState({refreshAlertRequested: refreshAlertCount, refreshAlertReport: Date.now()});	
	refreshChanges({ action, updateState, dispatch })
	updateState({refreshPayload: {timestamp:Date.now(), background: false, loaderDuration: 0 }});
	refreshTime = Date.now();
}

const changesRecordWatcher = createAmbSubscriptionEffect('/rw/default/change_request/:filter', {
	messageReceivedActionType: RW_ACTIONS.MESSAGE_RECEIVED_CHANGE,
});

const refreshChanges = ({ action, updateState, dispatch }) => {
	dispatch(ACTIONS.FETCH_CHANGES_GQL, {});
	refreshChangeCount++;
	updateState({
		refreshChangeRequested: refreshChangeCount,
		refreshChangeReport: Date.now()
	});
}

function getEncodedFilterString(filter) {
if(typeof filter != 'string')
return '';
filter = filter.
replace(/\^EQ/g, '').
replace(/\^ORDERBY(?:DESC)?[^^]*/g, '').
replace(/^GOTO/, '');
return btoa(filter).replace(/=/g, '-');
}


