const ACTION_PREFIX = 'NOW_SERVICE_MAP#';
const LINK_VALIDATION_CFG = {
	'cmdb_ci_service': {
		allowedFromClasses: {
			'cmdb_ci_service': {}
		},
		allowedToClasses: {
			'cmdb_ci_service_discovered': {},
			'cmdb_ci_service': {},
			'service_offering': {}
		}
	},
	'service_offering': {
		allowedFromClasses: {
			'cmdb_ci_service': {}
		},
		allowedToClasses: {
			'cmdb_ci_service_discovered': {}
		}
	},
	'cmdb_ci_service_discovered': {
		allowedFromClasses: {
			'cmdb_ci_service': {},
			'cmdb_ci_service_discovered': {},
			'service_offering': {}
		},
		allowedToClasses: {
			'cmdb_ci_service_discovered': {}
		}
	}
};
export const VALIDATION_API = {
	canLinkFromClass: function(currentClass, fromClass) {
		const classConfig = LINK_VALIDATION_CFG[currentClass];
		const allowed = classConfig ? classConfig.allowedFromClasses.hasOwnProperty(fromClass) : false;
		return allowed;
	},
	canLinkToClass: function(currentClass, toClass) {
		const classConfig = LINK_VALIDATION_CFG[currentClass];
		const allowed = classConfig ? classConfig.allowedToClasses.hasOwnProperty(toClass) : false;
		return allowed;
	}
}

